import React,{useState, useEffect} from 'react'
import Payment from './Components/Payment/Payment'
import Summary from '../CheckOut/Components/Summary/Summary'
import './PaymentSkeleton.scss'
import Navigation from '../../components/Common/Navigation/Navigation'
import { urls } from '../../routes/routes';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import totalMob from './Components/Payment/Total/Total';
import Total from './Components/Payment/Total/Total'
// import Button from '../../Input-controls/Button/Button';
import {actions as cartActions} from './../../redux/ducks/cart'
import {actions as paymentOptActions} from './../../redux/ducks/payment_options';
import {actions as orderAction} from './../../redux/ducks/order';
import { CheckoutItems } from '../../utils/Env';
import Loader from '../../utils/Loader/Loader';
import { notify } from '../../Input-controls/Toast/Toast'
import { storeData } from '../../utils/Api'
import { Clevertap } from '../../utils/Utils'

function PaymentSkeleton(props) {

  const[cartDta, setCartDta]=useState('');
  const [apiCallFlg,setApiCallFlg] = useState(false);

  const filterCart=(type, data)=>{
      return  data.find(e=>e.type==type)
   }

   useEffect(() => {
      if(props.cart.data){
         //  
         createCheckout(props.cart.data.cart)
      }
      // eslint-disable-next-line
   }, [props.cart.data])

   const createCheckout=(cartArr)=>{


       const keys=CheckoutItems

       let objCart={ }

       for(let i=0; i<keys.length; i++){
           objCart[keys[i]]=filterCart(keys[i], cartArr.items)
       }

       objCart={...objCart, ...cartArr}
       delete objCart.items
      //  console.log("++++++====",objCart)
       setCartDta({...objCart})

     }

    const [currSelPytOpt, setCurrSelPytOpt] = useState();
    const[selectedMethod, setSelectedMethod]=useState()

    const[subscribeClicked, setSubscribeClicked]=useState(false)

    useEffect(() => {
      if(props.payLink.data && subscribeClicked){
        // console.log(props.payLink.data.redirect_url)

        if(props.payLink.data.success==1){
          window.location.href=props.payLink.data.data.redirect_url;
        }
        else if(!!props.payLink.data.message){
          setPaymentLoading(false)
          notify.danger(props.payLink.data.message);
        }
        else{
          setPaymentLoading(false)
          notify.danger(props.payLink.data.msg)
        }
      }
    }, [props.payLink.data])

    const[PaymentLoading, setPaymentLoading]=useState(false)

    const handleClick=()=>{
      let lobj = {};
      lobj.click = true;
      lobj.data = {pay_option:currSelPytOpt, pay_mode:selectedMethod};
      Clevertap("Buy_on_payments_page",lobj);
      setSubscribeClicked(true);
      setPaymentLoading(true)
        storeData("orderRequest",JSON.stringify({pay_option:currSelPytOpt, pay_mode:selectedMethod}));
        props.putOrder({pay_option:currSelPytOpt, pay_mode:selectedMethod})
    }

    const handlePaymtSelect = (pval) => {
      setCurrSelPytOpt(pval);
    }

    useEffect(() => {
        props.setCart("","",'get');
        setApiCallFlg(true);
        props.fetchPaymentOptiions();
        // eslint-disable-next-line
      }, [])

      const [paymentOptionArr, setPaymentOptionArr] = useState([]);
      const [paymentModes, setPaymentModes] = useState([]);
      const[banners, setBanners]=useState([])
      const[billingText, setBillingText] = useState([]);

    useEffect(()=>{
      if(apiCallFlg && !!props.paymentOption.data && props.paymentOption.data.success)
      {
        setApiCallFlg(true);
        setPaymentOptionArr(props.paymentOption.data.data.payment_options);
        let itm=props.paymentOption.data.data.payment_modes.find(e=>e.value='ot_order')
        if(itm){
          let res=itm.purchase_options.find(e=>e.default===true && e.visible===true)
            if(res){
              props.setCart("ES", {
                "purchase_options": {
                    "duration": res.duration,
                    "pay_mode": 'ot_order'
                }
              })
            }
        }
        console.log(props.paymentOption.data.data.payment_modes)
        setPaymentModes(props.paymentOption.data.data.payment_modes)
        setBanners(props.paymentOption.data.data.banners)
        let val=""
        if(props.paymentOption.data.data.payment_modes.length===1){
         val= props.paymentOption.data.data.payment_modes[0].value
        }else{
          val=props.paymentOption.data.data.payment_modes.find(e=>e.default===true).value
        }
        if(!selectedMethod){
          setSelectedMethod(val)
        }
        localStorage.setItem("order",
                JSON.stringify({
                  pay_mode: val
                })
              )
        setBillingText(props.paymentOption.data.data.billing_text);
      }
    },[props.paymentOption])


    const[SelectedManualPayment, setSelectedManualPayment]=useState()
    const[showPay, setShowPay]=useState(false)

    useEffect(() => {

      if(cartDta.ES && "details" in cartDta.ES){
        // console.log(cartDta.ES.details)
          const{purchase_options}= cartDta.ES.details;
          if(purchase_options){
            // console.log("++++",purchase_options)
              setSelectedMethod(purchase_options.pay_mode)
              setSelectedManualPayment(purchase_options.duration)
              setShowPay(true)
          }else{
            setShowPay(true)
          }
      }

  }, [cartDta.ES])

    return (
        <div>
          {/* {console.log(cartDta.ES)} */}
          {props.paymentOption.isLoading || props.payLink.isLoading || PaymentLoading ? <Loader FullScreen={true}/>:null}
          <>
                <div style={{backgroundColor: '#F5F9FF',width: '100%'}}>
                    <Navigation  backPath={urls.CHECKOUT}/>

                    <div className="main-flx">

                        <div className="payment-skle">
                        <div>
                            <Total price={cartDta.total}/>
                            {showPay &&
                            <Payment banners={banners}
                              SelectedManualPayment={SelectedManualPayment}
                              selectedMethod={selectedMethod} cartData={cartDta.ES}
                              setSelectedMethod={(e)=>setSelectedMethod(e)}
                              currentSelectedOpt={handlePaymtSelect}
                              paymentOption={paymentOptionArr}
                              paymentModes={paymentModes} 
                              billingText={billingText} />}
                        </div>
                        </div>
                        {cartDta.ES ?
                          <div  className="summary-skele" style={{width: '35%'}}>
                              <Summary Pricing={cartDta} hideCoupon={localStorage.getItem("LG")==1}  subscribeClick={handleClick}/>
                          </div>
                        : null}
                    </div>
                    {/* <div className="payment-btn">
                        <Button value="Pay" className="pay-btn-cls" />
                    </div> */}
                    <div className="mobview-float-cls">
                       <button onClick={handleClick} className="subscribe-btnn">Pay</button>
                    </div>
                </div>
            </>
        </div>
    )
}

const mapStateToProps = (state) => {
  return {
    cart:state.cart,
    paymentOption: state.payment_options,
    payLink:state.set_order
}}

const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...cartActions,
        ...paymentOptActions,
        ...orderAction
        //ADD IF ITS FROM DIFFRENT ACTION FILE
    }, dispatch)
  });

export default connect(mapStateToProps, mapDispatchToProps)(PaymentSkeleton)
