import React, { useState, useEffect } from 'react'
import './../../Skeleton.scss'
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import {actions as navigationActions} from './../../../../redux/ducks/navigation_stack'
import {actions as stateAction} from './../../../../redux/ducks/state_change'
import { g_countryCode } from '../../../../utils/Constants';
import Button from '../../../../Input-controls/Button/Button';
import CountryInput from '../../../../Input-controls/Country-Input/CountryInput';
import ApiLoader from '../../../../utils/ApiLoader/ApiLoader';
import { CheckIndianNumber } from '../../../../utils/Validator';
import { urls } from '../../../../routes/routes';
import { Clevertap, ClevertapUserProfile } from '../../../../utils/Utils';

function PhoneNumber(props) {

    const[apiCalled, setApiCalled]=useState(false)

    const chnageMobileFn = (e) => {
      const {name,value}=e.target;
      if(value.length < 11)
      {
        props.setState('verify',name, value);
      }
    }

    const sendOtpBtnClk = () => {

        window.clevertap.onUserLogin.push({
          "Site": {
            "Phone": "+91"+props.state.Phone, 
            "Identity": props.state.Phone, 
          }
        });

        setLoading(true)
        setApiCalled(true)
        props.loginRequest({num:props.state.Phone, cc:g_countryCode});
    }

    useEffect(() => {

      if(props.data.authObj.authStatus===true && apiCalled){
        setLoading(false)
        props.history.push(urls.HOME)
      }
    }, [])



    useEffect(() => {

      if('authObj' in props.data && props.data.authObj.sendOtp === true && apiCalled){

        props.history.push(urls.VERIFY_OTP)
      }
    }, [props.data])


    const phnNumEntrPdFn = (e) => {
      if(e.keyCode === 13)
      {
        if(CheckIndianNumber(props.state.Phone))
        {
          sendOtpBtnClk();
        }
      }
    }

    const[loading, setLoading]=useState(false)

    return (
        <>
        {loading?<ApiLoader/>:
              <>
              <div className="PurchaseMainSection">
                <div className="PurchaseMainContent phoneNumContent">
                  <div>
                    <img src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1596285859/New-home-screen/signUpImg_fb9xrk.svg"
                      className="sgnUpImgCls" />
                  </div>
                  <div className="signUpNewTxtCls">
                    {localStorage.getItem("LG") == "1" ?
                      "Sign up with your phone number to start with your company's lifecare journey"
                      : "Sign up with your phone number to start with your company's healthcare journey"
                    }
                  </div>
                    <div className="my-3">
                        <span className="HeadingPurchase">{'Sign up'}</span>
                    </div>
                    <CountryInput
                        value={props.state.Phone}
                        name="Phone"
                        type='number'
                        onChange={(e)=>chnageMobileFn(e)}
                        maxLength={10}
                        keyDownFunction="true"
                        onKeyDown={(e) => {phnNumEntrPdFn(e)}}
                        placeholder="Mobile Number"
                        style={{width:'240px'}}/>
                </div>
            </div>
            <div className="PurchaseButtonSection">
                <Button
                    onClick={()=>sendOtpBtnClk()}
                    disabled={props.state.validPhone===false && "disabled"}
                    className="mt-1 btnDisbChk"
                    value="Send OTP"/>
            </div>
            </>}
        </>
    )
}


const mapStateToProps = (state) => ({
  state:state.userstate.verify,
  data:state.navigationStack,
})

const mapDispatchToProps = (dispatch) => ({
  ...bindActionCreators({
      ...navigationActions,
      ...stateAction,
      //ADD IF ITS FROM DIFFRENT ACTION FILE
  }, dispatch)
});


export default connect(mapStateToProps, mapDispatchToProps)(PhoneNumber)
