import React, { useState, useEffect } from 'react'
import './PlanCard.scss';
// import Button from '../../../../Input-controls/Button/Button';
import { withRouter } from 'react-router-dom';
import { urls } from '../../../../routes/routes';
import InfoBox from '../../../../components/Common/InfoBox/InfoBox';
import { numDifferentiation, numberformat } from '../../util';
import PlanInfo from '../../../Informations/planInfo';
import GroupHealth from '../../../Informations/groupHealth'
import { CapitalizeString } from './../../util'

function PlanCard(props) {


    const[showInfo, setShowInfo]=useState(false)
    const[selectedHelp, setSelectedHelp]=useState("")

    const HandleBuy=()=>{
        console.log(props.data)
        props.HandleBuy(props.data.name, props.data._id, props.data.group_id, props.data.ui.features_short, props.data.ui.features, props.data.ui.insurance_summary)
    }

    useEffect(() => {
        localStorage.setItem("provider", JSON.stringify(props.provider))
    }, [])

    const Capitalize=(string)=>{
        return string[0].toUpperCase()+string.substring(1) 
    }

    return (
        <>
        <InfoBox showInfo={showInfo} setShowInfo={(bool)=>setShowInfo(bool)}>
            {selectedHelp==="PLAN_INFO"?
                    <PlanInfo 
                    data={props.data}
                    HideProvider={props.data.core_offerings && props.data.core_offerings[0].insured===0?true:false}
                    provider={props.data.core_offerings?props.data.core_offerings:null}/>
                :
             selectedHelp==="GROUP_HEALTH" && 
                    <GroupHealth provider={props.data.core_Offerings} summary={props.data.ui.insurance_summary} />
            }
        </InfoBox>



        <div className={`card-pack-cls ${props.pricingSelected===props.data.name && 'card-pack-cls-selected'}`} onClick={()=>props.cardClicked(props.data, props.index)}>

            {props.pricingSelected===props.data.name &&
                <div class="SideCheck_plan">
                    <span class="checkmark_bss_plan">
                        <div class="checkmark_stem_plan"></div>
                        <div class="checkmark_kick_plan"></div>
                    </span>
                </div>
            }

            {/* <div style={{textAlign: 'center',marginTop: '10px'}}>
                <div style={{width: '100px',margin: 'auto'}}>
                    <img src={props.data.ui.icon} alt="icons" className={props.provider && props.provider.provider_name == "max_life" ? "plan-img lazyload eliteImgCls" : "plan-img lazyload"} />
                </div>
            </div> */}

            {console.log(props.data, "2345t6433")}

            <div className="plan-name-cls" style={{marginTop:'35px', color:props.data.ui.name_color_code}}>
                <span>
                 {CapitalizeString(props.data.name)} Plan
                </span>
            </div>
            {/* <div className="amount-div">
                {props.data.insured_amount===0?"Not Available":numDifferentiation(props.data.insured_amount)}
            </div> */}

            {/* <div className="d-flex" style={{justifyContent: 'center'}}>
                <div className="insurance-name" onClick={()=>{
                    setShowInfo(true);
                    setSelectedHelp("GROUP_HEALTH")
                    }}>
                  {props.plan.feature_title}
                </div>
            </div> */}


        <div style={{height: '255px',margin:'auto'}}>
            {props.data.ui.features_short.map((e, index)=>(
 
                <div className="main-sub-clss d-flex">

                <div className="checkedd">
                       <label class="container">
                           <input type="checkbox" checked="checked" />
                           <span class="planpage-checkmarkk"></span>
                        </label>
                 </div>

                     <div className="main-clses">
                            {index===0 && props.data.category!=="topaz"?
                                <div style={{textDecoration:'underline'}} dangerouslySetInnerHTML={{ __html: e}} onClick={()=>{
                                    setShowInfo(true);
                                    setSelectedHelp("GROUP_HEALTH")
                                    }}/>
                                :
                                <div dangerouslySetInnerHTML={{ __html: e}}/>
                            }
                         </div>
                     </div>
            ))}
            </div>

            <div className="more-cls" onClick={()=>{
                setShowInfo(true);
                setSelectedHelp("PLAN_INFO")
                }}>More</div>
             <>
             {/* {console.log(props.data)} */}
             {/* {console.log("00000",props.data.pricing.find(e=>e.range==props.rangeSelected.range))} */}
                <div className="discount-amount my-4">
                    ₹{numberformat.format((props.data.pricing.find(e=>e.range===props.rangeSelected.range).price) * parseInt(props.members))}
                </div>
             </>
             {/* <>
             <div className="actual-amount">
                 <del><span style={{fontWeight:'bold'}}>₹</span>{numberformat.format((props.data.offerings[0].demographic_range.find(e=>e.range===props.rangeSelected.range).pre_discount_price_per_user) * parseInt(props.members))}</del>
             </div>
             <div className="discount-amount">
                ₹{numberformat.format((props.data.offerings[0].demographic_range.find(e=>e.range===props.rangeSelected.range).total_price_per_user) * parseInt(props.members))}
             </div>
             </> */}
             <div className="subscrpt-type">
                {Capitalize(props.rangeSelected.pricing_frequency)} Subscription
             </div>
             <div className="buttn-div">
                 <button className={`buy-btn-div ${props.pricingSelected==props.data.name && 'buy-btn-div-selected'}`} onClick={()=>HandleBuy()}>Buy</button>
             </div>
        </div>
        </>
    )
}
export default withRouter(PlanCard)
