import React, { useState, useEffect } from 'react'
import './Summary.scss';
import PromoInput from '../../../../Input-controls/Promo-Input/PromoInput';
import { numberformat } from '../../../PlanPurchase/util';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import {actions as cartActions} from './../../../../redux/ducks/cart'
import { notify } from '../../../../Input-controls/Toast/Toast';
import { Clevertap } from '../../../../utils/Utils';

function Summary(props) {

  const onTabOutSumFn = (e) => {
    if (e.key === 'Tab' && e.shiftKey) {
      e.preventDefault();
    }
  }

    const[coupon, setCoupon]=useState('')
    const [couponApplied, setCouponApplied] = useState(false)

    const[couponclicked, setCouponClicked]=useState(false)

    useEffect(() => {
      
      if(props.cart.data){
        let couponArray=props.cart.data.cart.items.find(e=>e.type=="CN")
        if(couponArray){
          // console.log("+++",couponArray)
          // notify.success(props.cart.data.msg)
          setCouponApplied(true)

          if(couponclicked){
            notify.success(props.cart.data.msg)
            setCouponClicked(false)
          }
        }else{
          // notify.danger(props.cart.data.msg)
          setCoupon("")
          setCouponApplied(false)

          if(couponclicked){
            notify.danger(props.cart.data.msg)
            setCouponClicked(false)
          }
        }
        // setCouponClicked(false)
      }

    }, [props.cart])



    const HandleCoupon=(bool)=>{
      setCouponClicked(true)
      if(bool=="add"){
        let lobj = {};
        lobj.applyClick = coupon;
        Clevertap("Apply_Coupon",lobj);
        props.setCart("CN", {code:coupon});
        // setCouponApplied(true)
      }else{
        // setCouponApplied(false)
        let lobj = {};
        lobj.removeClick = true;
        Clevertap("Remove_Coupon",lobj);
        props.setCart("CN", {code:""});
      }
    }

    useEffect(() => {
      // 
        // console.log("object", props.Pricing.CN)
      if(props.Pricing.CN){
        const{details, price, active}=props.Pricing.CN;
        if(active==true){
          setCoupon(details.code)
          setCouponApplied(true)
        }
      }else{
          setCoupon("")
          setCouponApplied(false)
      }
    }, [])


    const ShowFrequency=()=>{
      if('frequency_prices' in props.Pricing.ES.details && props.Pricing.ES.price!==props.Pricing.ES.details.frequency_prices.price){
        return true
      }else{
        return false
      }
    }

    return (
        <div className="summary-cls">
          {/* {console.log(props.Pricing)} */}
             <div className="summary-div-cls">Payment Summary</div>
             <div className="hr-sec-div"><hr/></div>

             <div className="summ-flx">
                 <div className="first-flx">
                  {ShowFrequency() &&
                      <div className="monthly-pln-price">{props.Pricing.ES.details.frequency_prices.label}</div>
                     }

                     <div className={ShowFrequency() ?"cmn-sum-cls" :"monthly-pln-price"}>{props.Pricing.ES.label}</div>

                    {!props.hideSubContent &&
                        <>
                          {props.Pricing.DS.price!==0 &&
                          <>
                          <div className="cmn-sum-cls">Refundable Deposit (15 days)</div>
                          <div className="cmn-sum-cls">Subtotal</div>
                          </>}

                          {couponApplied===true && props.Pricing.CN &&
                          <>
                              <div className="cmn-sum-cls">Discount</div>
                              <div className="cmn-sum-cls">Plan price after discount</div>
                            </>
                          }

                          <div className="cmn-sum-cls">GST (18%)</div>
                          <div style={{width: '90%',marginLeft: '30px'}}><hr/></div>
                          <div className="cmn-sum-div">Total Amount</div>
                      </>}
                 </div>

                 <div className="second-flx">

                  {ShowFrequency() &&
                        <div className="monthly-pln-price">₹{numberformat.format(props.Pricing.ES.details.frequency_prices.price)}</div>
                     }

                     <div className={ShowFrequency() ?"cmn-sum-cls" :"monthly-pln-price"}>₹{numberformat.format(props.Pricing.ES.price)}</div>

                     {!props.hideSubContent &&
                        <>
                            {props.Pricing.DS.price!==0 &&
                            <>
                            <div className="cmn-sum-cls">₹{numberformat.format(props.Pricing.DS.price)}</div>
                            <div className="cmn-sum-cls">₹{numberformat.format(props.Pricing.ES.price+props.Pricing.DS.price)}</div>
                            </>}

                            {couponApplied===true && props.Pricing.CN &&
                            <>
                                <div className="cmn-sum-cls">-₹{numberformat.format(Math.abs(props.Pricing.CN.price))}</div>
                                <div className="cmn-sum-cls">₹{numberformat.format((props.Pricing.ES.price+props.Pricing.DS.price)-Math.abs(props.Pricing.CN.price))}</div>
                            </>
                            }

                            <div className="cmn-sum-cls">₹{numberformat.format(props.Pricing.TX.price)}</div>
                            <div style={{width: '90%'}}><hr className="sumLineChk"/></div>
                              <div className="cmn-sum-div">₹{numberformat.format(props.Pricing.total)}</div>
                        </>}
                 </div>
             </div>
            {!props.hideCoupon &&
             <PromoInput
                couponApplied={couponApplied}
                HandleCoupon={(bool)=>HandleCoupon(bool)}
                value={coupon}
                onChange={(e)=>setCoupon(e.target.value)}/>}

              <div className="summary-btn" style={{marginTop:props.hideSubContent && "40px"}}>
              <div style={{textAlign: 'center',marginTop: '20px'}}>
                     <button className="subscribe-btn" onClick={()=>{Clevertap("Buy_button_checkout",{click:true});props.subscribeClick()}}>
                       Buy {!props.hideSubContent  && ' for ₹ ' +(numberformat.format(props.Pricing.total)) + '/-'}</button>
                 </div>
              </div>
        </div>
    )
}

const mapStateToProps = (state) => ({
  cart:state.cart
})

const mapDispatchToProps = (dispatch) => ({
  ...bindActionCreators({
      ...cartActions,
  }, dispatch)
});


export default connect(mapStateToProps, mapDispatchToProps)(Summary)
