import React, { useState } from 'react'
import './PromoInput.scss'
import { notify } from '../Toast/Toast'

export default function PromoInput(props) {


    const handleApplyCoupon=()=>{
        if(props.value){
            props.HandleCoupon("add")
        }else{
            notify.warning("Promo code is empty!")
        }
    }

    return (
        <div>
           
         {props.couponApplied===false ?
            <div className="promocode-apply">
                <div className="apply-code-div" onClick={()=>handleApplyCoupon()}>Apply</div>
                <input
                    onChange={(e)=>props.onChange(e)}
                    value={props.value} 
                    type="text" 
                    placeholder="Promo Code"
                    className="code-cls" />
            </div>
             :
             <div className="promocode-apply">
               
                <div className="apply-code-div" onClick={()=>props.HandleCoupon("remove")}>Remove</div>
                <input
                    value={props.value} 
                    style={{cursor:'not-allowed'}}
                    type="text" 
                    disabled 
                    className="code-cls" />
                <img src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1593431325/New-home-screen/Group_449_wjn79y.png" className="lazyload" height="15px" />
            </div>
            }
        </div>
    )
}
