import React from 'react'
import Header from '../../components/Home/HeaderSection/Header'
import Card_Title from './Components/Card_Title'
import Banner_Benefits from './Components/Banner_Benefits'
import Benefits_of_ordereing from './Components/Benefits_of_ordereing'
import Org_Benefits from './Components/Org_Benefits'
import FooterComponent from '../../components/Common/Footer/footer';
import { GroupTemLife_Data } from './Data/GroupTerm_life_Data'
import Terms_conditions from './Components/Terms_conditions'

export default function GroupTerm_life_Details() {
    return (
        <div>
            <Header />
            <Card_Title data={GroupTemLife_Data} />
            <Banner_Benefits data={GroupTemLife_Data}/>
            <Benefits_of_ordereing data={GroupTemLife_Data} />
            <Terms_conditions data={GroupTemLife_Data} />

            <Org_Benefits />
            <div className="footer-comp">
            <FooterComponent />
            </div>
        </div>
    )
}
