import React, { useState, useEffect } from 'react'
import Button from '../../../../Input-controls/Button/Button'
import Input from '../../../../Input-controls/Input/Input'
import AutoComplete from '../../../../Input-controls/AutoComplete/AutoComplete'
import './Gstin.scss';
// import { updateState } from '../../../../utils/StateHandler/updateState';
import GSTIN from '../../../../components/Home/GetStarted/GSTIN';
import { actions as gstActions } from '../../../../redux/ducks/gst';
import { actions as companyActions } from '../../../../redux/ducks/search_company';
import {actions as stateAction} from './../../../../redux/ducks/state_change'
import {actions as cartActions} from './../../../../redux/ducks/cart'
import { connect } from 'react-redux';
import { outTxtByTime, heightWidthCalc } from '../../../../utils/Api';
import { CheckGstin } from '../../../../utils/Validator';
import ApiLoader from '../../../../utils/ApiLoader/ApiLoader';
import { bindActionCreators } from 'redux';
import { urls } from '../../../../routes/routes';
import InfoBox from '../../../../components/Common/InfoBox/InfoBox';
import WhyCompnay from '../../../Informations/whyCompnay';
import WhyGstin from '../../../Informations/whyGstin'
import { Clevertap } from '../../../../utils/Utils';
import Popup from '../../../../components/Common/ConfirmationPopUp/Popup';

function Gstin(props) {

    const [state, setstate] = useState({})

    const[apiCalled, setApiCalled]=useState(false)

    const[showPopAuth,setShowPopAuth]=useState(false);

    const[localGst,setLocalGst]=useState("");

    const[apiError, setApiError]=useState({
      companyError:"",
      gstError:""
    })

    useEffect(()=>{
      setLocalGst(props.state.GstNumber);
    },[])

    const resetErrorState=()=>{
      setApiError({
        companyError:"",
        gstError:""
      })
    }

    const handleChange=(e)=>{
      
        resetErrorState();
        setErrorRespCmpSrch("");
        const{name, value}=e.target;
        props.setState('gstDetails',name, value);
        props.setState('gstDetails','CompanyName', "");
        props.selectedCompany(null);
    }

    const handleCompanyName =(e)=>{
      
        resetErrorState();
        setErrorRespCmpSrch("");
        const{name, value}=e.target;
        props.setState('gstDetails',name, value);
        props.selectedCompany(null);
        if(value.length > 2)
        {
          /*let {SuggestObj} = autoSugest;
          SuggestObj.showSuggestions = true;
          SuggestObj.isLoading = true;
          SuggestObj.filteredSuggestions = [];
          SuggestObj.filteredSuggestions = SuggestObj.suggestions.filter(
          (suggestion) =>
            suggestion.business_name.toLowerCase().indexOf(value.toLowerCase()) > -1
          );
          setAutoSuggest({SuggestObj});*/
          setAutoSugInit(true);
          props.fetchCompany(value);
        }
        else{
          let {SuggestObj} = autoSugest;
          SuggestObj.showSuggestions = false;
          SuggestObj.isLoading = false;
          SuggestObj.filteredSuggestions = [];
          setAutoSuggest({SuggestObj});
          props.setInitValSearch();
        }
    }

    const [autoSugest, setAutoSuggest] = useState({"SuggestObj": {
      activeSuggestion: 0,
      filteredSuggestions: [],
      showSuggestions: false,
      isLoading:false,
      suggestions: []
    }})

    useEffect(()=>{
      //
      
      setErrorRespCmpSrch("");
      if(autoSugInit == true && props.companyData.isLoading == true)
      {
        let {SuggestObj} = autoSugest;
        SuggestObj.isLoading = props.companyData.isLoading;
        SuggestObj.showSuggestions = true;
        SuggestObj.filteredSuggestions = props.companyData.data ? props.companyData.data.data : [];
        setAutoSuggest({SuggestObj});
      }
      else if(autoSugInit == true && props.companyData.isLoading == false && props.companyData.data == null){
        let {SuggestObj} = autoSugest;
        SuggestObj.isLoading = props.companyData.isLoading;
        SuggestObj.showSuggestions = false;
        SuggestObj.filteredSuggestions = (props.companyData.data && props.companyData.data.data) ? props.companyData.data.data.data.hits : [];
        setAutoSuggest({SuggestObj});
      }
      else if(autoSugInit == true && props.companyData.isLoading == false && props.companyData.data.status == 1){
        let {SuggestObj} = autoSugest;
        SuggestObj.isLoading = props.companyData.isLoading;
        SuggestObj.showSuggestions = true;
        SuggestObj.filteredSuggestions = (props.companyData.data && props.companyData.data.data) ? props.companyData.data.data.data.hits : [];
        setAutoSuggest({SuggestObj});
      }
      else if(autoSugInit == true && props.companyData.isLoading == false && props.companyData.data.status == 0){
        //alert(props.companyData.data.data.msg);
        setAutoSugInit(false);
        let {SuggestObj} = autoSugest;
        SuggestObj.isLoading = props.companyData.isLoading;
        SuggestObj.showSuggestions = false;
        SuggestObj.filteredSuggestions = [];
        setAutoSuggest({SuggestObj});
        setErrorRespCmpSrch(props.companyData.data.data.msg);
      }
      //
    },[props.companyData])

    const [errorRespCmpSrch, setErrorRespCmpSrch] = useState("");
    const onSuggestionClick = (e) => {

      let lobj;
      let currentId = e.currentTarget.dataset.id;
      for(var i=0;i<autoSugest.SuggestObj.filteredSuggestions.length;i++)
      {
        lobj = autoSugest.SuggestObj.filteredSuggestions[i];
        if(currentId == lobj.identifier){
          break;
        }
      }
      let {SuggestObj} = autoSugest;
      SuggestObj.activeSuggestion = 0;
      SuggestObj.filteredSuggestions = [];
      SuggestObj.showSuggestions = false;
      SuggestObj.isLoading = false;
      setAutoSuggest({SuggestObj});
      setAutoSugInit(false);
      props.setState('gstDetails','GstNumber', "");
      props.setState('gstDetails','CompanyName', e.currentTarget.innerText);
      Clevertap('CompanyNameEntered',{company:e.currentTarget.innerText});
      props.selectedCompany(lobj);
    }

    const HandleProceed=()=>{
        if(!!props.state.GstNumber)
        {
          //clevertap
          Clevertap('GSTNumberEntered',{gstin:props.state.GstNumber})
          props.fetchGstin(props.state.GstNumber);
          setApiCalled(true);
        }
        else{
          //clevertap
          Clevertap('ProceedWithCompanyName',{clicked:true})
          props.history.push(urls.COMPANY_DETAILS);
        }
        setLoading(true)
    }



    useEffect(() => {

        if(!!props.gstData.data && apiCalled){
          
          setApiCalled(false);
          setLoading(false);
          if(props.gstData.data.success==1 && localStorage.getItem('LG') == "1" && props.gstData.data.data.LGE == false){
            setShowPopAuth(true);
          }
          else if(props.gstData.data.success==1){
            Clevertap('GST_Reponse',{data:JSON.stringify(props.gstData.data)}); 
            props.history.push(urls.COMPANY_DETAILS)
          }
          else if(props.gstData.data.success == 0 && localStorage.getItem('LG') == "1"){
            setShowPopAuth(true);
          }
          else{
            setApiError({...apiError, gstError:props.gstData.data.msg})
          }
        }
        // eslint-disable-next-line
    }, [props.gstData])

    useEffect(()=>{
      let ltimeTxt = outTxtByTime();
      setstate({...state, timeBasedText: ltimeTxt});
      document.body.addEventListener('click', function(event){
        setTimeout(function(){
          let {SuggestObj} = autoSugest;
          SuggestObj.showSuggestions = false;
          SuggestObj.isLoading = false;
          setAutoSuggest({SuggestObj});
        },50);
      });
      return () => {
        document.body.removeEventListener('click', function(event){});
      };
      // eslint-disable-next-line
    },[])

     const[showInfo, setShowInfo]=useState(false)
     const[selectedHelp, setSelectedHelp]=useState("")
     const[autoSugInit, setAutoSugInit] = useState(false);

     const[loading, setLoading]=useState(false);

     const closeLGPopup = ()=> {
       setShowPopAuth(false);
       props.setState('gstDetails',"GstNumber", localGst);
       props.history.push("/");
     }

    return (
        <>
        {loading? <ApiLoader/>
        :
        <>
        <Popup show={showPopAuth} showImg={false}
          data={{title:"Error", content:"The given GSTIN was not found in our records. Please contact your Branch service manager."}}
          Close={closeLGPopup}/>

         <InfoBox showInfo={showInfo} setShowInfo={(bool)=>setShowInfo(bool)}>
            {selectedHelp=="COMPANY"?<WhyCompnay/>
                :
             selectedHelp=="GSTIN" && <WhyGstin/>}
        </InfoBox>
        <div className="PurchaseMainSection">
               <div className="GSTINContent">
                   <div className="mt-3 mb-4">
                       <span className="HeadingPurchase">{"Hi, "+state.timeBasedText+" :)"}</span>
                   </div>
                   <div>

                   <div>
                    <Input
                        type="text"
                        name="CompanyName"
                        value={props.state.CompanyName}
                        onChange={handleCompanyName}
                        err={errorRespCmpSrch}
                        className="searchCmpNamGst"
                        help="Only For Companies & LLP"
                        label="Company name"
                        width={heightWidthCalc().width<360?"240px":"280px"}
                        autoComplete="off"
                        placeholder="Find your Company Name"
                        disabled={localStorage.getItem("LG") == 1 ? "disabled" : false}
                        HelpClicked={()=>{
                            setShowInfo(true)
                            setSelectedHelp('COMPANY')
                            }}/>
                    </div>

                    <div className="relativedropdown_company">

                      <AutoComplete showSuggestions={autoSugest.SuggestObj.showSuggestions}
                        filteredSuggestions={autoSugest.SuggestObj.filteredSuggestions}
                        onSuggestionClick={onSuggestionClick}
                        isLoading={autoSugest.SuggestObj.isLoading}
                        inputTxt={props.state.CompanyName}
                        width={heightWidthCalc().width<360?"240px":"280px"}
                      />
                    </div>
                    </div>

                    <div className="ordive">Or</div>
                    <Input
                       type="text"
                       name="GstNumber"
                       value={props.state.GstNumber}
                       onChange={handleChange}
                       label="GSTIN number"
                       help="For Others"
                       err={apiError.gstError}
                       width={heightWidthCalc().width<360?"240px":"280px"}
                       placeholder="Enter your GSTIN number"
                       HelpClicked={()=>{
                        setShowInfo(true)
                        setSelectedHelp('GSTIN')
                        }}/>
               </div>
           </div>
           <div className="PurchaseButtonSection">
               <Button
                   onClick={HandleProceed}
                   disabled={(props.state.validGstNumber==false && props.companyData.selectedCompany == null) && 'disabled'}
                   className="mt-1"
                   value="Proceed"/>
           </div>
           </>
        }
   </>
    )
}

const mapStateToProps = (state) => {
  return {
    gstData:state.gst,
    state:state.userstate.gstDetails,
    companyData: state.search_company
}}

const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...stateAction,
        ...gstActions,
        ...companyActions,
        ...cartActions
        //ADD IF ITS FROM DIFFRENT ACTION FILE
    }, dispatch)
  });


export default connect(mapStateToProps, mapDispatchToProps)(Gstin)
