import React from 'react'

export default function Waiting_period(props) {
    return (
        <>
        <div>
            <div className="waiting-period-flx waitmargn">
                <div className="waiting-frstflx">
                    <div className="waitheader">{props.data.Waitingperiod.heading}</div>
                    <div className="waitdesc">{props.data.Waitingperiod.desc}</div>
                    <div className="waitdesc">
                        {props.data.Waitingperiod.Lists.map(e=>(
                            <ul>

                                <li className="colorlists">{e.list}</li>
                            </ul>
                        ))}
                    </div>
                </div>

              <div className="covid-flx">
                  <div className="waitheader">{props.data.Covid.heading}</div>
                <div className="waitdesc">{props.data.Covid.desc}</div>
                    
                </div> 
            </div>
        </div>
        </>
    )
}
