import React, { useState, useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import Button from '../../../../Input-controls/Button/Button'
import Input from '../../../../Input-controls/Input/Input'
import './CompanyDetails.scss';
import { connect } from 'react-redux';
import ApiLoader from '../../../../utils/ApiLoader/ApiLoader';
import {actions as stateAction} from './../../../../redux/ducks/state_change'
import { bindActionCreators } from 'redux';
import { urls } from '../../../../routes/routes';
import InfoBox from '../../../../components/Common/InfoBox/InfoBox';
import WhyTotalEmployees from '../../../Informations/whyTotalEmployees';
import {actions as cartActions} from './../../../../redux/ducks/cart'
import {actions as verifyGstAction} from './../../../../redux/ducks/verifyGst'
import { g_invalidEmpCount } from '../../../../utils/Constants';
import { heightWidthCalc } from '../../../../utils/Api';
import { notify } from '../../../../Input-controls/Toast/Toast';
import { Clevertap, ClevertapUserProfile, ShowCoverage } from '../../../../utils/Utils';
import { urlPrefixEmpSure} from '../../../../utils/MicroServiceURL'
import Axios from 'axios';

 function CompanyDetail (props) {

  const[loading, setLoading]=useState(false)

   const [buttonClick,setButtonClick] = useState(false);

   const [state, setstate] = useState({
     business_name:"",
     address:{
       state:"",
       city:"",
       pincode:""
     },
     business_nature:[],
     gstin:""
   })

    useEffect(() => {
      
      if(props.gstData.data && props.searchCmpnyData.selectedCompany === null){
        setstate(props.gstData.data.data);
      }
      else if(!!props.searchCmpnyData && !!props.searchCmpnyData.selectedCompany){
        let lrespObj = props.searchCmpnyData.selectedCompany;
        let lobj = {};
        lobj.address={};
        lobj.address.state = lrespObj.State;
        lobj.address.city = lrespObj.address;
        lobj.business_nature = lrespObj.business_nature;
        lobj.gstin="";
        lobj.business_name = lrespObj.business_name;
        setstate(lobj);
      }
    }, []);

    useEffect(()=>{

      if(buttonClick && props.cart.isLoading == false){
        setButtonClick(false);
        setLoading(false)
        try{
          if(props.cart.data.success === 1)
          {
            props.setVerifyGstin()
            props.history.push(urls.SELECT_PLAN);
            // if(ShowCoverage()){
            //   props.history.push(urls.COVERAGES);
            // }else{
            //   props.history.push(urls.SELECT_PLAN);
            // }
             
          }
          else if(props.cart.data.success === 0 && !!props.cart.data.msg)
          {
            notify.danger(props.cart.data.msg);
          }
          else{
            notify.danger("Error while processing the request. We are looking into it.");
          }
        }
        catch(e){
          notify.danger("Error while processing the request. We are looking into it.");
        }
      }
    },[props.cart])

    const handleChange=(e)=>{
      
        let {name, value}=e.target;
        if(value.length == 1)
        {
          value = value.trim();
        }
        props.setState('companyDetails',name, value);
    }

    const[showInfo, setShowInfo]=useState(false)



    const NetwordCallCart=()=>{
      let data={
        employer: {
          "business_contact_number": props.data.Phone,
          "business_email": props.state.Email,
          "admin_name": props.state.Name,
          "gstin":"",
          "business_name":"",
          'identifier':"",
          'address':''
        },
        employeeCount: {
          "total_employees":props.state.TotalEmployees,
        },
      }
      if(props.company_search_name){
        data.employer.business_name = props.company_search_name.business_name;
        data.employer.identifier = props.company_search_name.identifier;
        data.employer.address = props.company_search_name.address && props.company_search_name.address
      }else{
        data.employer.gstin=props.gstnUser.GstNumber
      }
      props.setCart("ES", {...data});
    }


    const ClevertapCall=()=>{
      Clevertap('EmployerDetails',{
        "Phone": props.data.Phone,
        "Email": props.state.Email,
        "Name": props.state.Name,
        "total_employees":props.state.TotalEmployees,
      })
      ClevertapUserProfile("UserProfile",{
          "Name": props.state.Name,
          "Email": props.state.Email.toLowerCase(),
      })
    }

    const HandleSubmit=()=>{

      
      setButtonClick(true);
      setLoading(true)

      Axios.post(urlPrefixEmpSure('/employer/clevertap-check'),
          {
            "phone_number":props.data.Phone,
            "email_id":props.state.Email
          },  
          { 
            headers:{
                access_key:'token dpHau891H5uKt5ACrGKHENIuQLGL2JkD',
                Authorization:`bearer ${localStorage.getItem('token')}`
            },
        })
        .then(res=>{
          if(res.data.tap==true){
            ClevertapCall()
          }
          NetwordCallCart()
        })
        .catch(err=>{
          NetwordCallCart()
        })

    }
    return (
        <>
        {props.cart.isLoading?<ApiLoader/>:
        <>
          <InfoBox showInfo={showInfo} setShowInfo={(bool)=>setShowInfo(bool)}>
            <WhyTotalEmployees/>
          </InfoBox>
        <div className="PurchaseMainSection">
               <div className="CompanyContent">
                   <div className="mt-2" style={{fontSize:'14px'}}>Welcome</div>
                   <div className="mt-2">
                        <span className="HeadingPurchaseee">{state.business_name}</span>
                   </div>
                   {state.address && <div className="AddressDetails">{state.address.state} {state.address.city} {state.address.pincode}</div> }
                   <div className="LargeMonSection">
                     <div className="marginfor_company_name">
                      <Input
                        name="Name"
                        value={props.state.Name}
                        onChange={handleChange}
                        type="text"
                        label="Your Name"
                        width={heightWidthCalc().width<360?"240px":"280px"}
                        placeholder="Your Name"/>
                     </div>

                     <div className="marginfor_company">
                        <Input
                          name="Email"
                          value={props.state.Email}
                          onChange={handleChange}
                          type="text"
                          label="Your Email ID"
                          width={heightWidthCalc().width<360?"240px":"280px"}
                          placeholder="Your Email ID"/>
                     </div>
                     <div className="marginfor_company">
                        <Input
                          name="TotalEmployees"
                          value={props.state.TotalEmployees}
                          err={(props.state.validTotalEmployees===false && props.state.TotalEmployees!=="") && g_invalidEmpCount}
                          onChange={handleChange}
                          type="number"
                          label="Total Employees"
                          width={heightWidthCalc().width<360?"240px":"280px"}
                          help="Why"
                          placeholder="Number of Employees"
                          HelpClicked={()=>{
                            setShowInfo(true)
                            }}/>
                     </div>
                   </div>
               </div>
           </div>
           <div className="PurchaseButtonSection divPaddingBelow">
               <Button
                   onClick={()=>HandleSubmit()}
                   disabled={props.state.validTotalEmployees=== true && props.state.TotalEmployees!=="" && props.state.validName===true && props.state.validEmail===true ? "" :  "disabled"}
                   className="mt-1"
                   value="Proceed"/>
           </div>
           </>}
   </>
    )
}

const mapStateToProps = (state) => {

  return {
    gstData:state.gst,
    state:state.userstate.companyDetails,
    data:state.userstate.verify,
    searchCmpnyData: state.search_company,
    gstnUser:state.userstate.gstDetails,
    cart: state.cart,
    company_search_name: state.search_company.selectedCompany,
}}

const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
      ...verifyGstAction,
        ...stateAction,
        ...cartActions
        //ADD IF ITS FROM DIFFRENT ACTION FILE
    }, dispatch)
  });


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CompanyDetail))
