import React from 'react'
import './ItemsSelected.scss'
import BasePlan from './BasePlanCard/BasePlan'
import SelectedPlan from './SelectedPlanCard/SelectedPlan'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import {actions as coveragesActions} from './../../../../redux/ducks/state_change';
import CoverageProceed from './CoverageProceed/CoverageProceed'

function ItemsSelected(props) {

    const HandleRemove=(id)=>{
            let data=[...props.coverages]
            data.splice(props.coverages.indexOf(id), 1)
            props.setCoverages(data)
   }

    return (
        <div className="SideDivNavC">
            <div className="Custmsplan">Customised Plan({props.coverages && props.coverages.length+1})</div>
            <hr/>
            <div className="CustMarClls">
                <BasePlan data={props.data}/>
            </div>
            {props.coverages.length!==0 ?
            <div className="CustMarClls">
                {props.coverages && props.coverages.map(e=>(
                    <SelectedPlan selected={e} data={props.data} HandleRemove={HandleRemove}/>
                ))}
            </div>
            :
            <div className="notfound-div">
                <img src="https://res.cloudinary.com/onsurity/image/upload/v1598255363/New-home-screen/Group_3424_cwfm9d.svg" className="ntselect" />
                <div className="selct-contnt">Select a plan from the list<br/> to customise your plan</div>
            </div>}
            <div className="COvedProcced">
                <CoverageProceed/>
            </div>
        </div>
    )
}

const mapStateToProps = (state) => ({
    coverages:state.userstate.coverages
})

const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...coveragesActions,
    }, dispatch)
})

export default connect(mapStateToProps, mapDispatchToProps)(ItemsSelected)