import React, { useRef, useState, useEffect } from 'react'
import './Header.scss';
// import { notify } from '../../../Input-controls/Toast/Toast';
import { urls } from '../../../routes/routes';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import {actions as navigationActions} from '../../../redux/ducks/navigation_stack'
import { bindActionCreators } from 'redux';
import { CapitalizeString } from '../../../layouts/PlanPurchase/util';
import {actions as user_state} from '../../../redux/ducks/state_change'
import {actions as nav_logout} from '../../../redux/ducks/navigation_stack';
import {actions as resetCompany} from '../../../redux/ducks/search_company';
import { navigation } from '../../../utils/Env';
import { ResetData, Clevertap, IsRedBus } from '../../../utils/Utils';
import { DashboardUrl } from '../../../utils/MicroServiceURL';
import Loader from '../../../utils/Loader/Loader';
import CheckBox from '../../../Input-controls/CheckBox/CheckBox';
import Toggle from '../../../Input-controls/Toggle/Toggle';

function Header(props) {

    const [showText, setShowText] = useState(false);


    const ref = useRef("")

    const[toggle, setToggle]=useState(false)

    const HandleToggle=()=>{
        setToggle(!toggle)
        ref.current.classList.toggle('active');
    }

    const getAppFn = () => {
        let lobj = {};
        lobj.clicked = true;
        Clevertap("GetTheApp",lobj);
        props.setNavSelected(navigation.HOME)
        props.history.push({
            pathname:urls.HOME,
            from:'getapp'
        })
    }

    const SetMyaccToggle=()=>{
        let lobj = {};
		    lobj.clicked = true;
        Clevertap("My_Account",lobj);
        setShowText(!showText)
    }

    const HandleDanshBoard=()=>{
        window.location.href=`${DashboardUrl}?token=${localStorage.getItem('token')}`
    }

    const [logoutClicked, setLogoutClicked]=useState(false)

    // useEffect(() => {

    //     if(props.logoutSession && logoutClicked){
    //         ResetData()
    //     }

    // }, [props.logoutSession])
    const [loading, setloading]=useState(false)

    const HandleLogout=()=>{
      
        SetMyaccToggle();
        // setLogoutClicked(true)
        props.resetState();
        props.resetCompany();
        props.logout();
        props.resetAuthObj();
        localStorage.setItem('surity:log','0');

        setTimeout(function(){
          props.resetAuthObj();
        },500)

        let lgdata=localStorage.getItem("LG");

        if(lgdata!==undefined || lgdata!==null){
            localStorage.setItem("LG",0);
            setloading(true)
            setTimeout(()=>{
                window.location.reload();
              },1000)
        }
    }
    //whatsappCheck: current whatsapp status of the user
    // const[whatsappCheck, setWhatsappCheck]=useState(false)

    // useEffect(() => {
    //    if(props.whatsapp===true){
    //     setWhatsappCheck(true)
    //    }else{
    //     setWhatsappCheck(false)
    //    }
    // }, [])

    const HandleWhatsapp=()=>{
        props.whatsappStatus(!props.whatsapp)
        props.updateWhatsapp(!props.whatsapp===true?1:0)
    }


//     const refNav = useRef(null)

//     useEffect(() => {
//         console.log(window.pageYOffset)
//         if(window.pageYOffset>100){
// alert()
//         }
//     }, [window])

    return (
        <>
        {loading && <Loader FullScreen={true}/> }

        {/* for web view */}
        <div className="HomeheaderCls">
        <div className="headWeb">
            <nav className="navbar mscrollShadow" >
              <div className="homeWrapperComponents d-flex">
                <a href="/" className="logo">
                    <img src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1595591334/New-home-screen/Group_1328_bbme78.svg"  alt="onsurity_logo" className="onsuimg" />
                </a>
                <ul className="main-nav" id="js-menu">
                <li className="liLink">
                   <span onClick={()=>props.history.push(urls.HOME)} style={{cursor:'pointer'}} className={`nav-links ${props.nav_selected==navigation.HOME && "selectedNav"}`}  id="navv">Home</span>
                        <a title="Affordable Employee Healthcare with group medical insurance" href="https://www.onsurity.com/" alt="Home" style={{fontSize: '0px'}}>Home</a>
                    </li>
                    <li className="liLink">
                      <span onClick={()=>props.history.push(urls.ABOUT_US)} style={{cursor:'pointer'}} className={`nav-links ${props.nav_selected==navigation.ABOUT && "selectedNav"}`} id="navv" >About Us</span>
                       <a title="Onsurity - Best Employee Healthcare Platform " href="https://www.onsurity.com/teamsure/about-us" alt="About Us" style={{fontSize: '0px'}}>About Us</a>
                    </li>
                    <li className="liLink">
                      <span onClick={()=>props.history.push(urls.CONTACT_US)} style={{cursor:'pointer'}}  className={`nav-links ${props.nav_selected==navigation.CONTACT && "selectedNav"}`} id="navv" >Contact Us</span>
                      <a title="Onsurity – Contact Us for Customer Support and Partnerships" href="https://www.onsurity.com/teamsure/contact-us" alt="Contact Us" style={{fontSize: '0px'}}>Contact Us</a>
                    </li>
                    <li className="liLink">
                        <a href={urls.BLOG_DESC} className={`nav-links ${props.nav_selected==navigation.BLOG && "selectedNav"}`} target="_blank" id="navv">Blog</a>
                    </li>
                    {/* <li className="liLink">
                        <a href={urls.FAQ} className={`nav-links ${props.nav_selected==navigation.FAQ && "selectedNav"}`} id="navv">FAQs</a>
                    </li> */}
                     {localStorage.getItem("LG")!=1 &&
                    <li className="getappflex liLink">
                        <button id="getapp" onClick={()=>getAppFn()} className="nav-links">Get the App</button>
                    </li>}

                      <li className="myaccountflex liLink">
                        {props.auth.authStatus==true?
                            <button id="login-link"
                            onClick={() =>SetMyaccToggle()}
                            className="ml-0 nav-links">My Account</button>
                        :
                            <button id="myaccount" className="nav-links ml-0"
                                 onClick={()=>{
                                     props.history.push(urls.VERIFY_PHONE)
                                     Clevertap('Login',{clicked:true})
                                    }}>{IsRedBus()} </button>
                        }
                      </li>
                      {
                        showText &&
                      <div className="acount-details-div">
                          <div className="wrp-account-sec">
                           <div className="acchol-name">Hello{props.name && `, ${CapitalizeString(props.name).split(' ')[0]}`}</div>
                           <div className="achol-numnber">+91 {props.state.Phone}</div>
                           {props.dashboard.dashBoardAccess &&
                           <>
                           <div className="account-sec-hr"></div>
                           <div className="dashboard-emp" onClick={()=>HandleDanshBoard()}>Employer Dashboard</div>
                           </>}
                           <div className="account-sec-hr"></div>
                           <div className="logout-sec mb-3" style={{display:'flex'}}>
                               <div style={{margin:'auto 0px', marginRight:'10px'}}>Whats App Notification</div>
                               <div>
                                    <Toggle onClick={()=>HandleWhatsapp()} checked={props.whatsapp}/>
                                   {/* <CheckBox onClick={()=>HandleWhatsapp()} checked={props.whatsapp}/> */}
                               </div>
                           </div>
                           <div className="account-sec-hr"></div>
                           <div className="logout-sec mb-3" onClick={()=>HandleLogout()}>Logout</div>
                          </div>
                     </div>
                    }



                </ul>
                </div>
            </nav>
        </div>

{/* for mobile view */}
         <div className="headMob">
         <nav className="navbar">
     <span className="navbar-toggle" id="js-navbar-toggle" onClick={()=>HandleToggle()}>
         {toggle?
         <img src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594304772/New-home-screen/Group_582_ypzywa.svg" alt="toggle" />
         :
         <i className="fa fa-bars"></i>
         }
     </span>
     <a href={urls.HOME} className="logo">
          <img alt="logo" src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1595591334/New-home-screen/Group_1328_bbme78.svg" onClick={()=>props.setNavSelected(navigation.HOME)} className="onsuimg" />
     </a>
     <ul ref={ref} className="main-navigation" id="js-menu">
      <li className="liLink">
        <span onClick={()=>{props.history.push(urls.HOME); HandleToggle()}}  id="navv" className={`nav-links ${props.nav_selected==navigation.HOME && "selectedNav"}`}>Home</span>
        <a title="Affordable Employee Healthcare with group medical insurance" href="https://www.onsurity.com/" alt="Home" style={{fontSize: '0px'}}>Home</a>
      </li>
       <li className="liLink">
         <span onClick={()=>{props.history.push(urls.ABOUT_US); HandleToggle()}}  id="navv" className={`nav-links ${props.nav_selected==navigation.ABOUT && "selectedNav"}`} >About Us</span>
         <a title="Onsurity - Best Employee Healthcare Platform " href="https://www.onsurity.com/teamsure/about-us" alt="About Us" style={{fontSize: '0px'}}>About Us</a>
       </li>
       <li className="liLink">
         <span onClick={()=>{props.history.push(urls.CONTACT_US); HandleToggle()}}  id="navv" className={`nav-links ${props.nav_selected==navigation.CONTACT && "selectedNav"}`} >Contact Us</span>
         <a title="Onsurity – Contact Us for Customer Support and Partnerships" href="https://www.onsurity.com/teamsure/contact-us" alt="Contact Us" style={{fontSize: '0px'}}>Contact Us</a>
       </li>
       
       
         <li className="liLink">
            <a href={urls.BLOG_DESC} target="_blank" className="nav-links" id="navv" className={`nav-links ${props.nav_selected==navigation.BLOG && "selectedNav"}`} >Blog</a>
        </li>
        {/* <li className="liLink">
            <a href={urls.FAQ} className="nav-links" id="navv" className={`nav-links ${props.nav_selected==navigation.FAQ && "selectedNav"}`}>FAQs</a>
        </li> */}
        {localStorage.getItem("LG")!=1 &&
        <li className="getappflex liLink">
            <button id="getapp" onClick={()=>{getAppFn(); HandleToggle()}} className="nav-links">Get the App</button>
        </li>}
        <li className="myaccountflex liLink">
                        {props.auth.authStatus==true?
                            <button id="login-link"
                            onClick={() =>{
                                HandleLogout()
                                HandleToggle()
                            }}
                            className="ml-0 nav-links">Logout</button>
                        :
                            <button id="myaccount" className="nav-links ml-0"
                                 onClick={()=>props.history.push(urls.VERIFY_PHONE)}>{IsRedBus()}</button>
                        }

                      </li>

            <li className="liLink">
                <div className="logout-sec mb-3" style={{display:'flex', width:'fit-content', margin:'auto'}}>
                    <div style={{margin:'auto 0px', marginRight:'10px'}}>Whats App Notification</div>
                    <div>
                        <Toggle onClick={()=>HandleWhatsapp()} checked={props.whatsapp}/>
                        {/* <CheckBox onClick={()=>HandleWhatsapp()} checked={props.whatsapp}/> */}
                    </div>
                </div>
            </li>
      </ul>
      
   </nav>
     </div>
     </div>
       </>

    )
}

const mapStateToProps = (state) => ({
    dashboard:state.userstate,
    state:state.userstate.verify,
    name:state.userstate.companyDetails.Name,
    auth:state.navigationStack.authObj,
    nav_selected:state.userstate.navigationSelected,
    logoutSession:state.navigationStack.logOutData,
    whatsapp:state.userstate.whatsapp,
  })

const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...navigationActions,
        ...user_state,
        ...nav_logout,
        ...resetCompany
        //ADD IF ITS FROM DIFFRENT ACTION FILE
    }, dispatch)
  });


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Header))
