import React from 'react'
import './GstinInput.scss';

export default function GstinInput(props) {
    return (
        <div className="gstn-box-cls">
            {props.verified?
            <div className="add-txt-btn" onClick={props.removeClick}>Remove</div>
            :
            <div className="add-txt-btn" onClick={props.addClick}>Add</div>}
            <input
                value={props.value} 
                style={{cursor:props.verified?'not-allowed':''}}
                disabled={props.verified}
                onChange={props.onChange}
                type="text"
                placeholder="Enter your GSTIN number" 
                className="checkout-inputtype" />
        </div>
    )
}
