import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import Header from '../../components/Home/HeaderSection/Header'
import './Faq.scss'
import { useRef } from 'react'
import SearchInput from '../../Input-controls/SearchInput/SearchInput'
import {faqApi} from './data'
import { bindActionCreators } from 'redux'
import {actions as FaqActions} from '../../redux/ducks/faq' 

const Faq = (props) => {
    
    useEffect(() => {
        props.fetchFaq();
    }, [])
    
    const[currentFaq, setCurrentFaq]=useState([])
    const[faqSections, setFaqSections]=useState([])
    const[selected, setSelected]=useState("All Queries")
    const[questionSelected, setQuestionSelected]=useState(null)


    useEffect(() => {
        if(props.faq && props.faq.success==1){
            const faqData=props.faq.data.faq;
            setCurrentFaq(faqData)
            setFaqSections(['All Queries',...new Set(faqData.map(e=>e.name))])
            // console.log(['All Queries',...new Set(faqData.map(e=>e.name))])
        }
        
    }, [props.faq])

    const handleCardClicked=(data)=>{
        // console.log(data)
        setSelected(data)
        if(data=="All Queries"){
            setCurrentFaq(props.faq.data.faq)
        }else{
            setCurrentFaq(props.faq.data.faq.filter(e=>e.name==data))
            // console.log(props.faq.data.faq.filter(e=>e.name==data))
        }
        setQuestionSelected(null)
      }

    const HandleQstnClick=(e, index)=>{
        setQuestionSelected(index)
    }
      
    const[query, setQuery]=useState("")
    const[searchable, setSearchable]=useState(false)
    
    const HandleSearch=(e)=>{
        setQuery(e.target.value)
        if(e.target.value.length>2){
            setSearchable(true)
            setCurrentFaq(props.faq.data.faq.filter(i=>(i.question.toLowerCase().search(e.target.value.toLowerCase())!==-1)))
        }else{
            setSearchable(false)
            setCurrentFaq(props.faq.data.faq)
            setSelected("All Queries")
        }
    }
    return (
        <div>
            <Header/>
            <div>
                <div className="faqHeading">Frequently Asked Questions</div>
                <div className="faqSubHeading">Here are some of our most frequently asked questions to help you with your queries.</div>
            </div>

            <div className="SearchSecFaq">
                <SearchInput
                    value={query}
                    onChange={(e)=>HandleSearch(e)}
                    placeholder="Search for Query"/>
            </div>

            <div>
                <div className="main_nav_divf">
                    {searchable==false &&
                    <>
                    <div className="mt-3 px-3 blockmain_divf">
                    {faqSections.map((e, index)=>(    
                        <>
                        <div onClick={()=>handleCardClicked(e)}>
                        <div className={`price_block_mobilef ${selected==e && "header_color_faq"}`}>{e}</div>
                        {selected==e && <div className="selevctedfaq"/>}
                        </div>
                        </>
                    ))}
                    </div>
                    <div className="hrLineFaq"/>
                    </>}

                    <div className="faqSecQstn mt-4 mb-5">
                        {currentFaq.length>0? currentFaq.map((e, index)=>(
                            <>
                            <div className="divquestions pl-3">
                                <div className="d-flex" style={{cursor:"pointer"}} onClick={()=>HandleQstnClick(e, index)}>
                                    <div className="qstfaq">{e.question}</div>
                                    <div className="expandicofaq">
                                      {questionSelected==index?
                                        <img src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594098448/New-home-screen/Group_524_dihlpj.svg"alt="index" />
                                        :
                                        <img src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594098550/New-home-screen/Group_524_1_qixswe.svg" alt="index" />
                                      }
                                    </div>
                                </div>
                                <div className="ansfaq">
                                   <div style={{height:questionSelected==index?"fit-content":"0px"}} className={`infaqi`}>
                                      {e.answer}
                                   </div>
                                </div>
                            </div>
                            <hr/>
                            </>
                        ))
                        :
                        <div className="text-center">No records found!</div>
                    }
                    </div>
                </div>
            </div>
        </div>
    ) 
}

const mapStateToProps = (state) => ({
  faq:state.faq.data
})


const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...FaqActions,
    }, dispatch)
  });
  
  
  export default connect(mapStateToProps, mapDispatchToProps)(Faq)
