import React from 'react'
import Header from '../../components/Home/HeaderSection/Header'
import Card_Title from './Components/Card_Title'
import Banner_Benefits from './Components/Banner_Benefits'
import Benefits_of_ordereing from './Components/Benefits_of_ordereing'
import How_It_Works from './Components/How_It_Works'
import Org_Benefits from './Components/Org_Benefits'
import FooterComponent from '../../components/Common/Footer/footer';
import { GroupHealthins_Data } from './Data/GroupHealthIns_Data'
import Redeem_Fitness from './Components/Redeem_Fitness'
import Covered from './Components/Covered'
import Waiting_period from './Components/Waiting_period'
import { Helmet } from 'react-helmet'
import Seo from '../../utils/Seo'

export default function GroupHealthins_Details() {
    return (
        <div>

               <Header />
            <Card_Title data={GroupHealthins_Data} />
            <Banner_Benefits data={GroupHealthins_Data} hdfc={true}/>
            <Benefits_of_ordereing data={GroupHealthins_Data} grouphealth={true} />
            <Covered data={GroupHealthins_Data}/>
            <Waiting_period data={GroupHealthins_Data}/>
            <Redeem_Fitness data={GroupHealthins_Data} />
            

            <Org_Benefits />
            <div className="footer-comp">
            <FooterComponent />
            </div>
        </div>
    )
}
