import React, { useState, useEffect } from 'react'
import  './Wellnessmem.scss';
import { connect } from 'react-redux';
import { numDifferentiation, CapitalizeString } from '../../../PlanPurchase/util';
import GroupHealth from '../../../Informations/groupHealth';
import InfoBox from '../../../../components/Common/InfoBox/InfoBox';


function Wellnessmem(props) {

    const[showInfo, setShowInfo]=useState(false)
    const[wellnessState, setWellnessState]=useState()

    useEffect(() => {
        if(props.state && !!props.state.plan_id){
            setWellnessState(props.state)
            localStorage.setItem('wellness', JSON.stringify(props.state))
        }else{
            let data=JSON.parse(localStorage.getItem('wellness'))
            setWellnessState(data)
        }
    }, [props.state])

    const getProvider=()=>{
        let obj=""
        let item=localStorage.getItem("provider")
        if(item){
            obj=JSON.parse(localStorage.getItem("provider"))
        }else{
            obj={
                provider_name:''
            }
        }
        return obj
    }

    const getSummary=()=>{
        let lc=localStorage.getItem("insurance_summary")
        if(lc!=='undefined'){
           return JSON.parse(localStorage.getItem("insurance_summary"))
        }else{
            return undefined
        }
    }

    return (
        <>
        {wellnessState &&
        <>
        <InfoBox showInfo={showInfo} setShowInfo={(bool)=>setShowInfo(bool)}>
            <GroupHealth provider={getProvider()} summary={getSummary()}/>
        </InfoBox>
        <div className="well-mem-cls">
            {props.paymentSuccess?
             <div className="welns-mem-cls">{CapitalizeString(wellnessState.pricing_name)} Plan under Onsurity Plus Membership</div>
            :
            <div className="welns-mem-cls welns-mem-margin">{CapitalizeString(wellnessState.pricing_name)} Plan under Onsurity Plus Membership</div>
             }
             <div className="mem-flx">
                {/* <div className="plan-colr-div">
                        <div className="plan-amount-cls">{wellnessState.insured_amount===0?"0":numDifferentiation(wellnessState.insured_amount)}</div>
                        <div className="plan-info">Sum Insured</div>

                        <div className="wellness-insu-flx">
                        <div className="plan-infoo">{localStorage.getItem("LG") == 1 || localStorage.getItem("LG") == 0 ? "Group Term Life Insurance" : "Group Health Insurance"}</div>
                         <div className="qutnm-img-src"><img onClick={()=>setShowInfo(true)} src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594708752/New-home-screen/Group_739_ryueox.svg" alt="icon" /></div>
                        </div>
                </div> */}
                <div className="checkk-welltxt">
                  {wellnessState.features.feature_short.map((e,index)=> (
                      <>
                        {e &&
                        <div className="labl-cls">
                            <div className="checkeddiv">
                                {index==0 && wellnessState.features.feature_short.length!==3  && wellnessState.pricing_name!="topaz"?
                                <label class="containerdiv" onClick={()=> setShowInfo(true)}>
                                    <input type="checkbox" checked="checked" />
                                    <span class="checkout-checkmarkk"></span>
                                    <div className="label-divv" style={{textDecoration:'underline'}} dangerouslySetInnerHTML={{ __html: e}}/>
                                </label>
                                :
                                <label class="containerdiv">
                                    <input type="checkbox" checked="checked" />
                                    <span class="checkout-checkmarkk"></span>
                                    <div className="label-divv" dangerouslySetInnerHTML={{ __html: e}}/>
                                </label>
                                 } 
                             
                            </div>
                        </div>}
                      </>
                 ))}
              </div>
           </div>
         </div>
</>
}
</>
    )
}


const mapStateToProps = (state) => {
    return {
        state:state.userstate.planSelected
  }}


  export default connect(mapStateToProps, null)(Wellnessmem)
