import React from 'react'
import OrgBenefits from '../../../components/Home/OrganizationBenefits/OrgBenefits'
// import { memberBenefits } from '../../Home/data/memBenefits_data'
// import { memberBenefits_Data } from '../Data/MemBenefits_Data'
import { urls } from '../../../routes/routes'
import { Benefits_detail_Data } from '../Data/Benefits_detail_Data';
import { memberBenefits } from '../../HomeLG/data/memBenefits_data';

export default function Org_Benefits() {

    let avilableurls=()=>{
        for(let key in urls){
            if(urls[key]==window.location.pathname){
                return key
            }
        }
    }


    let filterData=()=>{
      
        let orglist={...Benefits_detail_Data};
        if(localStorage.getItem("LG") == "1"){
          orglist = {...memberBenefits};
          orglist.backColor = '#F5F9FF';
          orglist.Heading="Other benefits of the Onsurity Plus Membership"
        }
        let cards=orglist.CardData.filter(e=>e.url!=avilableurls());
        orglist.CardData=cards;
        return orglist
    }

    let data =filterData()

    return (
        <div className="orgbene">
               <div className="orgbenmrgn">
                   <OrgBenefits showDesc={false} data={data}/>
                </div>

        </div>
    )
}
