import React,{useState, useEffect, useMemo, useRef} from 'react'
import './PlanSkeleton.scss';
import TeamMember from './Components/Team-Member/TeamMember'
import EmpCount from './Components/Employee-Count/EmpCount';
import AgePrice from './Components/Age-Price/AgePrice';
import PlanCard from './Components/Plan-Cards/PlanCard';
// import Coverage from './Components/Coverage/Coverage';
import Navigation from '../../components/Common/Navigation/Navigation';
import { urls } from '../../routes/routes';
import { bindActionCreators } from 'redux';
import {actions as planAction} from './../../redux/ducks/plan'
import {actions as stateAction} from './../../redux/ducks/state_change'
import { connect } from 'react-redux';
import { findDefaultOffering, findDefaultPricing, findPricingByName, SideScroll } from './util';
import {actions as cartActions} from './../../redux/ducks/cart';
import { notify } from '../../Input-controls/Toast/Toast';
import ApiLoader from '../../utils/ApiLoader/ApiLoader';
import Loader from '../../utils/Loader/Loader';
import { Clevertap, ShowCoverage } from '../../utils/Utils';

import {actions as nav_logout} from './../../redux/ducks/navigation_stack';
import {actions as resetCompany} from './../../redux/ducks/search_company';
import {actions as user_state} from './../../redux/ducks/state_change';
import { ResetData, getQueryStringParams } from './../../utils/Utils';

function PlanSkeleton(props) {

      const [buttonClick,setButtonClick] = useState(false);

      useEffect(() => {
        if(ShowCoverage()){
          props.fetchPlans(props.gstData.GstNumber, [...props.coverages, ...props.defaultCoverages])
        }else{
          props.fetchPlans(props.gstData.GstNumber, ['c1', "c2", "c3", "c4", "c5"])
        }
       
        // eslint-disable-next-line
      }, [])

      useEffect(()=>{
        // 
        if(buttonClick && props.cart.isLoading == false){
          setButtonClick(false);
          try{
            if(props.cart.data.success === 1)
            {
              props.history.push(urls.ADD_PEOPLE);
            }
            else if(props.cart.data.success === 0 && !!props.cart.data.msg)
            {
              notify.danger(props.cart.data.msg);
            }
            else{
              notify.danger("Error while processing the request. We are looking into it.");
            }
          }
          catch(e){
            notify.danger("Error while processing the request. We are looking into it.");
          }
        }
      },[props.cart])


      const[currSelect,setCurrSelect]=useState(-1);

      const[plans, setPlans]=useState()
      const[plan, setPlan]=useState()
      const[pricingSelected, setPricingSelected]=useState()
      const[demographicRange, setDemographicRange]=useState()
      const[rangeSelected, setRangeSelected]=useState()
      const[members, setMembers]=useState()
      const[provider, setProvider]=useState() 
      const ref=useRef(null);
      const navRef = useRef(null);

      const InitialPlan=()=>{
        setPlan(props.plans.data.plans[0])
        let defaultPricing=findDefaultPricing(props.plans.data.plans[0]);
        // if(!defaultPricing){

        // }
        
        setProvider(defaultPricing.core_offerings[0])

        handleSlide(defaultPricing.index);
        setPricingSelected(defaultPricing.name);
        setDemographicRange(defaultPricing.pricing);
        setRangeSelected(defaultPricing.pricing[0]);
        setCurrSelect(defaultPricing.index);
      }

      const PreviousSelectedPlan=()=>{
        console.log(props.prevPlan, props.plans.data.plans)
        const{plan_id, pricing_name, range, selected_plan_id, policy_type}=props.prevPlan;
        let plan=props.plans.data.plans.find(e=>e.policy_type==policy_type)
        setPlan(plan)
        let Pricing=findPricingByName(plan,pricing_name)

        setProvider(Pricing.core_offerings[0])

        handleSlide(Pricing.index);
        setPricingSelected(Pricing.name)
        setDemographicRange(Pricing.pricing)
        setRangeSelected(Pricing.pricing.find(e=>e.range==range))
        setCurrSelect(Pricing.index);
      }

      useEffect(()=>{
        window.addEventListener('storage', function(event){
          // do what you want on logout-event
          if (event.key == 'surity:log') {
            let loginFlag=localStorage.getItem('surity:log')
            if((loginFlag==0 || loginFlag == null) && window.location.pathname == urls.SELECT_PLAN){
              props.resetState()
              props.resetCompany();
              props.logout();
              ResetData()
              setTimeout(function(){
                props.history.push("/");
              },500)
            }
          }
        })
      },[])

      useEffect(()=>{
        if(currSelect != -1){
          handleSlide(currSelect);
          MoveSlide(currSelect);
        }
      },[currSelect])

      const[navPosition, setNavPositon]=useState(0)

      const handleSlide=(index)=>{
          switch (index) {
              case 0:
                  setNavPositon(0)
                  // SideScroll(ref.current,'multi', 0, 200, 200)
                  break;
              case 1:
                  setNavPositon(110)
                  break;
              case 2:
                  setNavPositon(220)
                  break;
              case 3:
                  setNavPositon(330)
                  break;
              case 4:
                  setNavPositon(440)
                  break;
              default:
                  setNavPositon(0)
                  break;
          }
      }

      const MoveSlide=(index)=>{
        let lwidthNavHead=0
        let CardFlexSize=0
        try{
         lwidthNavHead = document.getElementById("NavPlanHeadId").offsetWidth;
         CardFlexSize = document.getElementById("ItemsCardIdPlan").children[0].offsetWidth;
        }catch{
          lwidthNavHead=0
          CardFlexSize=0
        }
        switch (index) {
            case 0:
                SideScroll(ref.current,'multi', 0, 0, 0);
                SideScroll(navRef.current,'multi',0,0,0);
                break;
            case 1:
              SideScroll(ref.current,'multi', 0, 200, 200)
              SideScroll(navRef.current,'multi',0,0,0);
                break;
            case 2:
              SideScroll(ref.current,'multi', 0, 440, 440)
                break;
            case 3:
              SideScroll(ref.current,'multi', 0, 680, 680);
              SideScroll(navRef.current,'multi',0,lwidthNavHead,lwidthNavHead);
                break;
            case 4:
              SideScroll(ref.current,'multi', 0, CardFlexSize, CardFlexSize)
              SideScroll(navRef.current,'multi',0,lwidthNavHead,lwidthNavHead);
                break;
            default:
                SideScroll(ref.current,'multi', 0, 0, 0);
                SideScroll(navRef.current,'multi',0,0,0);
                setNavPositon(0)
                break;
        }
    }

      useMemo(() => {
        if(props.plans){
          const {plans}=props.plans.data;
          if(plans.length>0){
            try{
              if(props.prevPlan.pricing_name){
                PreviousSelectedPlan()
              }else{
                InitialPlan()
              }
            }catch{
              InitialPlan()
            }
            setPlans(props.plans.data.plans)
            setMembers(props.empData.TotalEmployees)

          }else{
            alert("No plans found")
          }


        }
      }, [props.plans])


      const currentRadioSelectFn = (data) => {
        setPlan(data)
        let defaultPricing=findDefaultPricing(data)
        handleSlide(defaultPricing.index)
        setPricingSelected(defaultPricing.name)
        setDemographicRange(defaultPricing.pricing)
        setRangeSelected(defaultPricing.pricing[0]);
        setCurrSelect(defaultPricing.index);
      }

      const demoClicked =(data)=>{
        setRangeSelected(data)
      }

      const handleCardClicked=(data, index)=>{
        setCurrSelect(index);
        handleSlide(index)
        MoveSlide(index)
        setPricingSelected(data.name)
        setDemographicRange(data.pricing)
        setRangeSelected(data.pricing.find(e=>e.range===rangeSelected.range))
      }

    const HandleBuy=(price_name, plan_id, group_id,  feature_short, feature, insurance_summary)=>{

      localStorage.setItem("insurance_summary",JSON.stringify(insurance_summary))
      
      setButtonClick(true);
      //ADDING DATYA TO STORE
      const{coverage_ids, policy_type}=plan;
      const{range}=rangeSelected;

      console.log(price_name, plan_id, group_id,  feature_short, feature)

      demographicRange.map((obj,ind)=>{
        obj.TMCount = 0;
        obj.TMFCount = 0;
        obj.index = ind;
        if(obj.range == range){
          obj.selectedAgeRange = ind;
        }
      })

        props.resetObject("addPeople");
        //SENDING DATA TO CART

        Clevertap('EmployerDetails',{
          "PolicyType": policy_type,
          "AgeRange":range,
          "ClickedPlan": `${price_name} Plan`,
          "total_employees":props.empData.TotalEmployees,
        })

        let data={
          plan: {
              "plan_id": plan_id,
              "offerings": "default",
              "gender": "all",
              policy_type,
              // "insured_amount": insured_amount,
              "show_monthly": true,
              "pricing_frequency":'monthly',
              "pricing_frequency_in_days":plan.pricing_frequency_in_days,
              "target_product":plan.target_product
          }
        }
        let type="ES"
        // if(plan.target_product.toLowerCase()=="healthsure"){
        //   type="ES"
        // }else if(plan.target_product.toLowerCase()=="lifecare"){
        //   type="LC"
        // }
        props.setCart(type, {...data})


        let planSelected={
          coverage_ids:coverage_ids || [],
          plan_id:[],
          selected_plan_id:plan_id,
          pricing_name:price_name,
          group_id:group_id,
          // insured_amount:insured_amount,
          policy_type:policy_type,
          range:range,
          demographicRange:demographicRange,
          features:{
            feature_short,
            feature
          }
        }

        // let data;
        if(policy_type=="2A+2C"){
          let plan_ids=[]
          let plansArray={
            plans:[]
          }
          plans.map(e=>{
            plan_ids.push(e.plan_id)
          })
          planSelected.plan_id=[...plan_ids]

          // data=plansArray;
        }else if(policy_type=="1A"){
          planSelected.plan_id=[plan_id]
        }

        // console.log(planSelected)
        // props.setCart("ES", {...data})

        props.setPlan({...planSelected})

        //PUSHED TO NEXT PAGE

    }


    return (
      <>
      {props.cart.isLoading?<Loader FullScreen={true}/>:<>
      {/* <Navigation backPath={ShowCoverage()?urls.COVERAGES:urls.COMPANY_DETAILS}/> */}
      <Navigation backPath={urls.COMPANY_DETAILS}/>
      {plans &&
      <div style={{backgroundColor: '#F5F9FF',width: '100%'}}>
           <div className="plan-purchase-div">

             {/* COMMON CLASS FOR WEB AND MOBILE */}
               <div className="team_and_member">
                <div className="well-txt-div">Buy Onsurity Plus Membership for</div>
                  <div className="team-flex-div">
                      {plans.map((p)=>(
                          <TeamMember
                            data={p}
                            selected={plan}
                            currentSelection={(data)=>{currentRadioSelectFn(data)}}
                          />
                        ))}
                  </div>
               </div>

              {/* ONLY FOR MOBILE VIEW */}
              <div className="mobileShow sticky_paln_nav">
                  <div className="total_member_mobile">Price for {members} Members</div>
                  <div className="main_nav_div" ref={navRef} id="NavPlanHeadId">
                      <div className="mt-3 px-3 blockmain_div">
                        {plan.variants.map((e, index)=>(
                          <div className={currSelect == index ?"currPlanSelCls price_block_mobile": "price_block_mobile"}
                            onClick={()=>handleCardClicked(e, index)}>
                            {e.name}&nbsp; Plan
                          </div>
                        ))}
                      </div>
                      <div>
                        <div style={{left:`${navPosition}px`}} className="mx-3 price_block_mobile_scroller"/>
                      </div>
                  </div>
              </div>

              <div className="mobilePlan"  ref={ref} id="ItemsCardIdPlan">
                <div className="class_plan_flex_mob">
                  {plan.variants.map((e, index)=>(
                      <PlanCard
                        plan={plan}
                        HandleBuy={HandleBuy}
                        data={e}
                        index={index}
                        provider={provider}
                        members={members}
                        cardClicked={(data, index)=>handleCardClicked(data, index)}
                        pricingSelected={pricingSelected}
                        rangeSelected={rangeSelected}
                        />
                  ))}
                </div>
              </div>

            {/* COMMON VIEW FOR WEB AND MOBILE */}
            <div style={{backgroundColor: '#fff'}}>
              <div className="team_and_member">
                <div className="emp-age-flex">
                    <div className="emp-count-skele">
                        <EmpCount
                          members={members}
                          />
                    </div>
                    <div className="age-price-skele">
                        <AgePrice
                          plan={plan}
                          pricingSelected={pricingSelected}
                          rangeSelected={rangeSelected}
                          data={demographicRange}
                          DemoClicked={demoClicked} />
                    </div>
                </div>
              </div>
            </div>


            {/* HIDDEN IN MOBILE VIEW */}
            {/* <div className={plan.variants.length == 1 ? "plan-card-flxx lgCardStylePlnPg": "plan-card-flxx"}> */}
            <div className={plan.variants.length < 5 ? "plan-card-flxx lgCardStylePlnPg": "plan-card-flxx"}>

                {plan.variants.map(e=>(
                    <PlanCard
                      plan={plan}
                      HandleBuy={HandleBuy}
                      data={e}
                      provider={provider}
                      members={members}
                      cardClicked={(data, index)=>handleCardClicked(data, index)}
                      pricingSelected={pricingSelected}
                      rangeSelected={rangeSelected}
                      />
                ))}
            </div>
          </div>
      </div>}
      </>}
       </>
    )
}

const mapStateToProps = (state) => ({
  gstData:state.userstate.gstDetails,
  empData:state.userstate.companyDetails,
  prevPlan:state.userstate.planSelected,
  plans:state.plans.data,
  cart: state.cart,

  defaultCoverages:state.userstate.defaultCoverages,
  coverages:state.userstate.coverages
})

const mapDispatchToProps = (dispatch) => ({
  ...bindActionCreators({
      ...stateAction,
      ...planAction,
      ...cartActions,
      ...nav_logout,
      ...user_state,
      ...resetCompany
  }, dispatch)
});


export default connect(mapStateToProps, mapDispatchToProps)(PlanSkeleton)
