                              <li className="inner-lists" style={{marginTop: '10px'}}>Communication from Onsurity</li>
                              {/* <div className="desc-cls">We use the information collected (including recordings of customer support calls after notifying You and with Your consent) to assist You when You contact Our customer support services, including to:</div> */}     

