import React  from 'react';
import {numberWithCommas} from '../../../utils/Api.js';

export default function AddPplSummry(props) {

  return(
    <>
      <div className="AddPplSummry_main_cls">
        <div className="frstColAdplSum">
          <span className="adjstSzSumCls tmPriceLbl">
          Team Member Pricing
          </span>
          <span className="adjstSzSumCls tmPriceVal">
            {"₹"+ numberWithCommas(props.totalValues.totalAmnt)}
          </span>
        </div>
        <div className="sendColAdplSum">
          <span className="adjstSzSumCls tmCountSumryLbl">
            Team Member Count
          </span>
          <span className="adjstSzSumCls tmCountSumryVal">
            {numberWithCommas(props.totalValues.totalCount)}
          </span>
        </div>
      </div>
    </>
  )
}
