import React, { useState, useEffect } from 'react'
import './Skeleton.scss'
import SliderPurchase from './Static_Slider/Slide_Purchase'
import { slidePurchaseData } from './Static_Slider/data/sliderPurchase_data'
import { urls } from '../../routes/routes'
import Navigation from '../../components/Common/Navigation/Navigation'
import NotFound from "../../Input-controls/404/NotFound";
import Oops from "../Oops-NotFound/Oops";
import Loader from '../../utils/Loader/Loader';

export default function PurchaseSkeleton(props) {

    const [state, setstate] = useState("0%")
    const[backPath, setBackPath]=useState("")
    const[step, setStep]=useState(1);

    const[pageCheck,setPageCheck] = useState(true);
    const[blogUrl,setBlogUrl] = useState("null");

    useEffect(() => {
      const {pathname}=window.location;

      if(pathname==urls.VERIFY_OTP){
        setstate("25%")
        setBackPath(urls.VERIFY_PHONE)
        setStep(1)
        setPageCheck(true);
      }else if(pathname==urls.GSTIN_DETAILS){
        setstate("50%")
        setBackPath(urls.HOME)
        setStep(2)
        setPageCheck(true);
      }else if(pathname==urls.COMPANY_DETAILS){
        setstate("75%")
        setBackPath(urls.GSTIN_DETAILS)
        setStep(3)
        setPageCheck(true);
      }else if(pathname==urls.VERIFY_PHONE){
        setstate("0%")
        setBackPath(urls.HOME)
        setStep(1)
        setPageCheck(true);
      }
      else if(pathname == urls.BLOG){
        setBlogUrl("load");
        setPageCheck(false);
        //setTimeout(function() {
          window.open("https://blog.onsurity.com/","_self");
        //},100);
      }
      else{
        setPageCheck(false);
      }
    }, [window.location.pathname])


    return (
        <>
        { pageCheck == true ?
         (<><Navigation loaded={state} backPath={backPath}/>
        <div className="purchaseBackground">
            <div className="relSkelpjone">
              <div className="purchaseFlex">
                  <div className="flex-div-pu mx-auto purchaseSectionGlobal">
                    <div className="FlexValuePurchase1">
                        {props.children}
                    </div>
                    <div className="FlexValuePurchase2">
                    <SliderPurchase  customClass="purchaseSlide" blur={false} slideDirection="right" timeDurationSlide={5000} data={slidePurchaseData} carosalDots={true} fixedContent={false} content="" keyData="fitsr"/>
                    </div>
                  </div>
                  <div className="steps-prchase-d">Step {step} of 3</div>
              </div>
            </div>
        </div></>)
        : blogUrl == "load" ? <Loader FullScreen={true}/>
        :
        (
          <Oops />
        )
      }
        </>
    )
}
