import React from 'react'
import './Oops.scss';
import Navigation from '../../components/Common/Navigation/Navigation'
import { Link } from 'react-router-dom';

export default function Oops() {
    return (
        <div>
            <Navigation hideBack={true}/>
            <div className="notfound-flx">
                <div className="contents-flx">
                    <div className="oops-txt">Oops!</div>
                    <div className="notpage-txtxs">We can't seem to find the page you're looking for.</div>
                    <div className="notpage-txtxs">Error code: 404</div>
                    <div className="notpage-txtxs"  style={{paddingTop: '0px'}}>Here are some helpful links instead:</div>
                    <Link to="/">
                    <div className="links-div" style={{color:'blue'}}>Home</div>
                    </Link>
                    <Link to="/teamsure/about-us">
                    <div className="links-div" style={{color:'blue'}}>About us</div>
                    </Link>
                    {/*<Link to="">
                    <div className="links-div">Help</div>
                    </Link>*/}
                    <Link to="/teamsure/contact-us">
                    <div className="links-div" style={{color:'blue'}}>Contact us</div>
                    </Link>
                </div>

                <div className="oopsimg-flx">
                        <img src="https://res.cloudinary.com/onsurity/image/upload/v1598958407/New-home-screen/Group_3451_kbyyuy.svg" width="380px" className="imagee" />
                </div>
            </div>
        </div>
    )
}
