import React from 'react'
import './Styles.scss';

export default function Banner_Benefits(props) {
    return (
        <div className="deatils-banner">
            <div className="bannerflx">
                <div className="banner-image"><img src={props.data.Banner.bannerImage} width="330px" /></div>
                <div className="banner-headerflx">
                <div className="banner-heading">{props.data.Banner.Heading}</div>
                <div className="banner-desc">{props.data.Banner.Description}</div>

                {props.showList &&
                <div className="ban-lists">
                    <ul>
                        <li className="colorlists bannerlist">{props.data.Banner.lists}</li>
                        <li className="colorlists bannerlist">{props.data.Banner.listss}</li>
                    </ul>
                </div>}

                {props.hdfc &&
                 <div>
                 <div className="banner-desc">{props.data.Banner.desc}</div>
                 <div style={{marginTop: '3px',fontWeight: 'bold',color: '#091C3F'}}>{props.data.Banner.hdfc}</div>
             </div>}
               
                </div>
            </div>
            
        </div>
    )
}
