import React, { useState, Suspense, useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import './Home.scss'
/*import AOS from 'aos';
import 'aos/dist/aos.css';*/

//Required Data
import {slider1Data} from './data/slider1_data';
import {slider2Data,contentData} from './data/slider2_data';
import {memberBenefits} from './data/memBenefits_data';
import {organizationBenefits} from './data/orgBenefits_data';
import {customerSays} from './data/custSays_data';
import {trusted} from './data/trustedBy';
import {requestSection, requestDemo} from './data/requestSection_data';
import { howItWorks } from './data/howItWorks';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {actions as navigationActions} from '../../redux/ducks/navigation_stack'
import {actions as navSeclectedAction} from '../../redux/ducks/state_change'
import {actions as stateAction} from '../../redux/ducks/state_change'
import { CurrentVersion, navigation } from '../../utils/Env';
import Loader from '../../utils/Loader/Loader';
import { getQueryStringParams } from '../../utils/Utils';
import { urls } from '../../routes/routes';

//import {storeData, retrieveData, encryptStr, decryptStr} from '../../utils/Api';

//Home components
const Header = React.lazy(() => import('../../components/Home/HeaderSection/Header'));
const OrgBenefits = React.lazy(() => import('../../components/Home/OrganizationBenefits/OrgBenefits'));
const GSTIN = React.lazy(() => import('../../components/Home/GetStarted/GSTIN'));
const Stores = React.lazy(() => import('../../components/Home/AppStores/Stores'));
const Slides = React.lazy(() => import('../../components/Common/Sliders/Slide4'));
const Wellness = React.lazy(() => import('./WellnessPrice/Wellness'));
const Request = React.lazy(() => import('../../components/Home/RequestDemo/Request'));
const FooterComponent = React.lazy(() => import('../../components/Common/Footer/footer'));
const OurCustomers = React.lazy(() => import('../../components/Home/OurCustomers/OurCustomers'));



function Home(props) {
  // AOS.init({
  //   duration: 1000,
  // })

	  const [slide1,setSlide1] = useState([]);
    const [slide2,setSlide2] = useState([]);

    const [getAppLoader, setGetAppLoader] = useState(false);


    useEffect(() => {
      localStorage.removeItem("order")
      let data=localStorage.getItem("LG")
      if(data && data==0 && props.location.pathname==urls.HOME){
        setGetAppLoader(true)
        localStorage.removeItem("LG")
        window.location.reload();
      }
    }, [])
  

    useEffect(() => {
      // storeData("Arun","Test");
      // console.log(retrieveData("Arun"));
      // console.log(encryptStr("Arun"));
      // console.log(decryptStr("QXJ1bg=="));
      // props.menuplateStack()
      setSlide1(slider1Data);
      setSlide2(slider2Data);
    },[]);

    useEffect(() => {
      if(props.location.from && props.location.from=='getapp' && document.readyState === "complete"){
        setGetAppLoader(true);
        setTimeout(()=>{
          setGetAppLoader(false);
          let ltopPos = document.getElementById("strOutrPrntDivId_new").offsetTop;
          window.scrollTo({top:(ltopPos), left:0, behavior: 'smooth'});
        }, 950)
      }
    }, [props.location])

    useEffect(() => {
      props.setNavSelected(navigation.HOME);
      window.addEventListener('scroll', handleScroll);

      return () => {
        document.getElementsByClassName("navbar")[0].classList.add("more-BoxShadow");
        window.removeEventListener('scroll', handleScroll);
      }
    }, [])

    const handleScroll = () => {
      let ltop = document.getElementById("slideFirstSecHmId").getBoundingClientRect().top;
      if(ltop < 0){
        document.getElementsByClassName("navbar")[0].classList.add("more-BoxShadow");
      }
      else{
        document.getElementsByClassName("navbar")[0].classList.remove("more-BoxShadow");
      }
    }
    return (
        <div className="HomePagePrntCls">
        {getAppLoader ? <Loader/> : null}
          <div className="HeaderParentCls1">
            <Suspense fallback={<div></div>}>
              <Header hideGetApp={true}/>
            </Suspense>
          </div>
          { slide1.length > 0 ? (
           <div data-aos="fade-up fade-down" className="homeWrapperComponents" id="slideFirstSecHmId">
            <Suspense fallback={<div className="trans">
              {/* <div className="backLoadGray">
                <img className="loaderGlobalCenter" height="120px" src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594991299/New-home-screen/animation_200_kcq7uf1t_1_k1guxr.gif"/>
              </div> */}
            </div>}>
              <Slides customClass="w3-content paddingHeaderSider" blur={false} slideDirection="right"
                timeDurationSlide={1000} data={slide1} carosalDots={false} fixedContent={false}
                content={contentData} keyData="fitsr" fixedData={true}/>
            </Suspense>
          </div> ) : null}
           <div data-aos="">
            <Suspense fallback={<div></div>}>
              <Request info={requestSection} />
            </Suspense>
          </div>
          <div data-aos="fade-up fade-down" className="homeScrollOrgComponents">
          <Suspense fallback={<div></div>}>
            <OrgBenefits data={organizationBenefits}/>
          </Suspense>
          </div>
          <div data-aos="fade-up fade-down" className="homeScrollOrgComponents">
            <div className="lineBtwnHmPgCls">
            </div>
          </div>
          <div data-aos="fade-up fade-down" className="homeScrollOrgComponents">
            <Suspense fallback={<div></div>}>
            <OrgBenefits data={memberBenefits}/>
          </Suspense>
          </div>
          <div data-aos="fade-up fade-down">
            <Suspense fallback={<div></div>}>
              <Wellness/>
            </Suspense>
          </div>
          <div data-aos="fade-up fade-down" className="homeScrollOrgComponents">
            <Suspense fallback={<div></div>}>
              {/* <OurCustomers /> */}
              <OrgBenefits data={trusted}/>
            </Suspense>
          </div>
          <div data-aos="fade-up fade-down" className="">
            <Suspense fallback={<div></div>}>
              <OrgBenefits data={customerSays}/>
            </Suspense>
          </div>
          {/* { slide2.length > 0 ? (
          <div data-aos="fade-up fade-down">
            <Suspense fallback={<div></div>}>
			        <Slides customClass="w3-content lead_org_gap_Cls" blur={false} slideDirection="right" timeDurationSlide={5000} data={slide2} carosalDots={true} fixedContent={true} content={contentData} keyData="Second"/>
            </Suspense>
           </div> ) : null} */}
          <div data-aos="fade-up fade-down">
            <Suspense fallback={<div></div>}>
              <OrgBenefits data={howItWorks}/>
            </Suspense>
          </div>
           <div data-aos="">
            <Suspense fallback={<div></div>}>
              <Request info={requestSection} />
            </Suspense>
          </div>
          {/*<div id="strOutrPrntDivId_new"></div>
          <div id="strOutrPrntDivId" data-aos="fade-up fade-down" className="homeWrapperComponents">
            <Suspense fallback={<div></div>}>
              <Stores />
            </Suspense>
          </div>
          <div data-aos="" className="moveUpHmeCls">
            <Suspense fallback={<div></div>}>
              <Request info={requestDemo} isWraped={true}/>
            </Suspense>
          </div>*/}
          <div data-aos="fade-up fade-down" className=" homeWrapperComponents ">
            <Suspense fallback={<div></div>}>
			        <FooterComponent />
            </Suspense>
          </div>
        </div>
    )
}

const mapDispatchToProps = (dispatch) => ({
  ...bindActionCreators({
      ...stateAction,
      ...navigationActions,
      ...navSeclectedAction
      //ADD IF ITS FROM DIFFRENT ACTION FILE
  }, dispatch)
});


export default withRouter(connect(null, mapDispatchToProps)(Home))
