import React from 'react'
import './AutoComplete.scss';

export default function AutoComplete(props) {

  const getHiglightTxt = (pStr) => {
    // eslint-disable-next-line
    let lout = pStr;
    let liptTxt = props.inputTxt;
    let lipTxtArr = liptTxt.split("");
    let lrespTxtArr = pStr.split("");
    for(let i=0;i<lipTxtArr.length;i++){
      if(lrespTxtArr[i] == undefined){
        break;
      }
      if(lipTxtArr[i].toLowerCase() == lrespTxtArr[i].toLowerCase())
      {
        lrespTxtArr[i] = "<span class='fntBld'>"+lrespTxtArr[i]+"</span>";
      }

    }
    return {__html: lrespTxtArr.join("")};
    //return lrespTxtArr.join("");
  }
    return (
        <div>
          {props.showSuggestions ?
            props.filteredSuggestions.length > 0 ?
              (<div className="autoSugestParent mx-auto prntAutoSutWid" style={{width:props.width}}>
                {props.filteredSuggestions.map((suggestion, index) => {
                  return (<>
                    <div data-id={suggestion.identifier} key={"autoSugest"+index}
                      onClick={(e)=>props.onSuggestionClick(e)} className="auSgstDivGst autoCmpFntSty"
                      dangerouslySetInnerHTML={getHiglightTxt(suggestion.business_name)} />

                  </>);
              })}
              </div>)
            : props.isLoading === true ? (
              <div className="mx-auto prntAutoSutWid noSgstDivGst" style={{width:props.width}}>
                <div className="spinner-border ml-2" role="status">
                {/* <img src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594380062/Rolling-1s-200px_gnf1gy.gif"/> */}
                {/* <span class="sr-only">Loading...</span> */}
                </div><span className="loadingcomp">Loading...</span>
              </div>
            )
            :<div className="noSgstDivGst no-suggestions prntAutoSutWid mx-auto" style={{width:props.width}}>
              No suggestions
            </div>
          :null}
        </div>
    )
}
