import React, { useState, useEffect } from 'react'
import './CompanyDetails.scss';
// import Input from '../../../../Input-controls/Input/Input';
import GstinInput from '../../../../Input-controls/Gstin-Input/GstinInput';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import {actions as gstVerifyActions} from '../../../../redux/ducks/verifyGst'
import {actions as cartActions} from '../../../../redux/ducks/cart'
import { notify } from '../../../../Input-controls/Toast/Toast';
import { Validator } from './../../../../utils/Validator'
import Loader from './../../../../utils/Loader/Loader';
import { Clevertap } from '../../../../utils/Utils';

function CompanyDetails(props) {

    const[selected]=useState(1)

    const{companyDetails, gstDetails, verify}=props.user;
    const[gstState]=useState(props.gst.data && props.gst.data.data)
    const[gstInput, setGstInput]=useState()
    const[gstVerified, setGstVerified]=useState(false)

    const[gstClicked, setGstclicked]=useState(false)

    const [buttonClick,setButtonClick] = useState(false);

    const CartCall=(key, data)=>{
        // console.log(data);

        let Objdata={
            employer: {
              "business_contact_number":verify.Phone,
              "business_email": companyDetails.Email,
              "admin_name": companyDetails.Name,
              "gstin":"",
              "business_name":"",
              'identifier':"",
              'address':''
            },
          }
          if(key=="Company"){
            Objdata.employer.business_name = data.business_name;
            Objdata.employer.identifier = data.identifier;
            Objdata.employer.address = data.address
          }else{
            Objdata.employer.gstin=data.gstin
          }
          props.setCart("ES", {...Objdata});
    }

    const handleClick=()=>{
        if(!gstInput){
            notify.warning("No GSTIN entered")
        }
        else if(Validator('GstNumber', gstInput)){
            setButtonClick(true);
            setGstclicked(true);
            Clevertap("addGstCheckout",{click:true,data:gstInput});
            props.verifyGstin(gstInput)
        }else{
            notify.danger("GSTIN is invalid")
        }
    }

    const handleRemove=()=>{
        setGstclicked(false)
        setGstInput("")
        setGstVerified(false)
        props.verifyGstin("")
        CartCall("Company", props.companySearch);
        Clevertap("removeGstCheckout",{click:true});
        notify.danger("GSTIN removed");
    }

    const[checkoutGstData, setcheckoutGstData]=useState()

    useEffect(() => {
       if(props.getGstEntered.gstVerified==true){
           setGstInput(props.getGstEntered.gst)
           setcheckoutGstData(props.getGstEntered.data.data)
           setGstVerified(true)
       }
       else if(buttonClick && props.getGstEntered.isLoading == false && props.getGstEntered.error){
         setButtonClick(false);
         if(!!props.getGstEntered.data && !!props.getGstEntered.data.msg)
         {
        //    notify.danger(props.getGstEntered.data.msg);
         }
       }
    }, [props.getGstEntered])

    useEffect(() => {
        if(gstClicked && props.verifyGst && props.verifyGst.success==1){
            setcheckoutGstData(props.verifyGst.data)
            setGstVerified(true)
            CartCall("GST", props.verifyGst.data)
            notify.success("GSTIN added")
        }
    }, [props.verifyGst])

    return (
      <>
        <div className="com-name-cls">
            {!gstDetails.GstNumber?
            <>
            <div className="abt-cmny-cls">
            {/* {props.getGstEntered.isLoading ? <Loader/> :<> */}
                <div className="cmny-mem-cls">
                    {gstVerified==true && checkoutGstData?
                    <>
                        <div className="cmny-nam">{checkoutGstData.business_name}</div>
                        <div className="addr-cls">{checkoutGstData.address && `${checkoutGstData.address.state} ${checkoutGstData.address.city} ${checkoutGstData.address.pincode}`}</div>
                   </>
                    :
                    <>
                        <div className="cmny-nam">{props.companySearch.business_name}</div>
                        <div className="addr-cls">{'address' in props.companySearch && props.companySearch.address}</div>
                    </>
                    }
                        <div className="tomothy-cls">{companyDetails.Name}</div>
                        <div className="contact-id-div">{companyDetails.Email}</div>
                        <div className="tomothy-cls">{props.TotalEmployees} Members</div>
                        <div className="contact-id-div">Number of team members</div>
                </div>
                <div className="div-line"></div>
                <div >
                    <div className="gstin-sec">Company GSTIN</div>

                    <div className="wrp-gstn-inp">
                        <GstinInput
                            value={gstInput}
                            verified={gstVerified}
                            addClick={handleClick}
                            removeClick={handleRemove}
                            onChange={(e)=>setGstInput(e.target.value)} />
                            {/* Loading... */}
                            {props.getGstEntered.isLoading &&
                            <>
                            <div className="spinner-border ml-2" role="status" /><span className="loadingcomp">Loading...</span>
                            </>
                            }
                    </div>
                </div>
              {/* </>} */}
            </div>
            </>
            :
            <>
                <div className="abt-cmny-cls">
                    <div className="total-mem-num">
                        <div className="mem-count-divv">{props.TotalEmployees} Members</div>
                        <div className="num-clss">Number of team members</div>
                    </div>
                <div className="div-line"></div>
                {props.gst.data &&
               <div className="cmny-mem-div">
                    <div className="cmny-nam">{gstState.business_name}</div>
                    <div className="addr-cls">{gstState.address.state} {gstState.address.city} {gstState.address.pincode}</div>
                    <div className="tomothy-cls">{companyDetails.Name}</div>
                    <div className="contact-id-div">{companyDetails.Email}</div>
                    <div className="gstn-clss">GSTIN: {gstDetails.GstNumber}</div>
                </div>
                }
                </div>
            </>}
        </div>
        </>

    )
}

const mapStateToProps = (state) => {
    return {
    //   gstData:state.gst,
    //   state:state.userstate.gstDetails,
    //   companyData: state.search_company
      verifyGst:state.verifyGst.data,
      getGstEntered: state.verifyGst,
      user:state.userstate,
      gst:state.gst,
      companySearch:state.search_company.selectedCompany
  }}

  const mapDispatchToProps = (dispatch) => ({
      ...bindActionCreators({
          ...gstVerifyActions,
          ...cartActions,
          //ADD IF ITS FROM DIFFRENT ACTION FILE
      }, dispatch)
    });


  export default connect(mapStateToProps, mapDispatchToProps)(CompanyDetails)
