import React, {useState} from 'react';
import Button from '../../../Input-controls/Button/Button';

export default function MapAddrSec(props) {

  const btnMailClickFn = () => {
    
    // console.log("in mail");
    window.location = "mailto:"+props.data.mailId;
  }

  return (
    <>
      <div>
        <img className="imgmobindia" alt="Mobi" src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594658525/New-home-screen/Mask_Group_1_twu6bz.svg" width="100%"/>
      </div>
      <div className="MapAddrSecOut_CntUs_Cls">
        <div className="AddSecOutCntUs">
          <div className="cmpNmCntUsCls">
            {props.data.companyName}
          </div>
          <div className="cmpAddrCntUsCls">
            {props.data.address}
          </div>
          <div className="widthEmptyLineCntUs"></div>
          <div className="lblTxtCntUcTxt">
            {props.data.label}
          </div>
          <div className="lblMailTxtCntUs">
            <span className="malLblCntUs">
              {props.data.emailLbl}
            </span>
            <span className="malIdCntUs" onClick={()=>btnMailClickFn()}>
              {props.data.mailId}
            </span>
            <div className="malIdCntUsBtnOuter">
              <Button value="Send Email" onClick={()=>btnMailClickFn()}/>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}
