import React from 'react'
import './BasePlan.scss';
import Button from '../../../../../Input-controls/Button/Button';


export default function BasePlan(props) {
    return (
        <div className="ItemSecCoverage">
            <div style={{width: '50%'}}>
                <div className="healthcare-contnt">{props.data.find(e=>e.mandatory===true).name}</div>
                <div className="owned-cntnt">Owned Plan</div>
            </div>
            <div className="baseplan">
                <div className="baseplan-div">Base plan</div>
            </div>
        </div>
    )
}
