
import React, {
	Component
}
from 'react';
import './InfoPop.css';
import Button from '../../../Input-controls/Button/Button'

function InfoPopup (props) {

	if(!props.show)
		{
			return null;
		}


		return (
			<>
			<div className="pp_prnt_1"/>
			<div className="pp_cls skeje">
				<img style={{marginBottom:'17px', marginTop:'10px'}} src="https://res.cloudinary.com/onsurity/image/upload/v1608538226/Group_6216_x2ek7f.svg" />
				<h4 className="mar1PerCls" style={{fontSize:'20px', width:'300px', margin:'auto'}}>Oops, we don't have any matching plans!</h4>
				<div class="content" style={{fontSize:'14px', width:'300px', margin:'auto', textAlign:'center'}}>
				We are not able to find the matching plans as per your selection please try again
				</div>
				<div style={{textAlign:'center', marginTop:'26px'}} >
					<Button  onClick={()=>props.Okay()} value="Okay"/>
				</div>
			</div>
			</>

		)
}


export default (InfoPopup)
