import React from 'react'

export default function How_It_Works(props) {
    return (
        <div className="howworks-flx works">
                <div className="feature-image"><img src={props.data.HowWorks.androidImage} className="workdsimg" /></div>
                
                
                {props.lists &&
              <div className=" listsflxx ban-lists ">
                  {props.data.HowWorks.Lists.map(e => (
                      <ul>
                          <li className="colorlists">{e.list}</li>
                      </ul>

                  ))}
              </div>
                }
              

                
                {props.desc && 
                <div className=" listsflx ban-lists descri">
                  <div>{props.data.HowWorks.desc} </div>
                
                 </div> }
        </div>
        
    )
}
