import React, {useState, useEffect} from 'react'
import './CheckoutSkeleton.scss';
import CompanyDetails from './Components/CompanyDetails/CompanyDetails';
import Wellnessmem from './Components/Wellness-Membership/Wellnessmem';
import TeamCount from './Components/TeamCount/TeamCount';
import PlanPrice from './Components/Plan-Price/PlanPrice';
import PaymentWork from './Components/Payment-Work/PaymentWork';
import Summary from './Components/Summary/Summary';
// import Payment from '../Payment/Components/Payment/Payment';
import { urls } from '../../routes/routes';
import Navigation from '../../components/Common/Navigation/Navigation';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import {actions as cartActions} from './../../redux/ducks/cart'
import {actions as priceActions} from './../../redux/ducks/static_pricing';

import {actions as nav_logout} from './../../redux/ducks/navigation_stack';
import {actions as resetCompany} from './../../redux/ducks/search_company';
import {actions as user_state} from './../../redux/ducks/state_change';
import { ResetData, getQueryStringParams } from './../../utils/Utils';


import Loader from '../../utils/Loader/Loader';
import { CheckoutItems } from '../../utils/Env';
import { notify } from '../../Input-controls/Toast/Toast';
import { numberformat } from '../PlanPurchase/util';
import { heightWidthCalc } from '../../utils/Api';
import HowItWorksImage from './Components/HowItWorksImage/HowItWorksImage';
import { Clevertap } from '../../utils/Utils';

function CheckoutSkeleton(props) {

  const maxLimit= {
    maxAmountLimit: 99999999,
    maxCountLimit: 10000,
    minCountLimit:3,
  };
  const [buttonClick,setButtonClick] = useState(false);
    const companyDetails=(
        {
            "comany_name": "McDonald's",
            "address": "5960 Elgin St undefined Toledo, Louisiana 54105 United States",
            "tomothy": "Tomothy Pena|",
            "contact_id": "Tomathy @ MCDonals.com",
            "members_count": "25 Members",
            "emp_text": "Number of Employees"
        }
    )


    /*useEffect(()=>{
      
      if(localStorage.getItem('surity:log') == "0" || localStorage.getItem('surity:log') == null){
        props.history.push("/");
      }
    },[props.authUser,props.cart,props.userData,props.pricing])*/
    useEffect(()=>{
      window.addEventListener('storage', function(event){
        // do what you want on logout-event
        if (event.key == 'surity:log') {
          let loginFlag=localStorage.getItem('surity:log')
          if((loginFlag==0 || loginFlag == null) && window.location.pathname == urls.CHECKOUT){
            props.resetState()
            props.resetCompany();
            props.logout();
            ResetData()
            setTimeout(function(){
              props.history.push("/");
            },500)
          }
        }
      })
    },[])


    let rewardsSection=[
        {
            "label": "Fitness Rewards INR 15,000"
        },
        {
            "label": "Up to 20% off for 5 medicine orders"
        },
        {
             "label": "Up to 65% off for 5 health check-ups"
        },
        {
            "label": "Discounted Medicines"
        },
        {
            "label": "Discounted Health Check-ups"
        },
        {
            "label": "Assistance during hospitalization"
        }
    ]

    const totalEmp=(
        {
            "header_TeamOnly": "Team Member Only",
            "header_TeamFamily": "Team Member & Family",
            "emp_count": "100 members"
        }
    )

    const heading=(
        {
            "headingg": "Age Band",
            "secondheading": "Team Member Only",
            "thirdheading": "Team Member & Family",
            "priceheading": "Price per Team Member",
            "pricemob": "Monthly Price/team"
        }
    )

    const totalMembers =(
        {
            "members": "100 Members",
            "number": "Number of team members",
            "Gstin": "23AADCD4946L1CT"
        }
    )

    const gstinpromo=(
        {
            "plcholder": "Enter company GSTIN number",
            "rgtfloat": "Add"
        }
    )

    const ageAmountt=([
        {
            Age: '18 to 35 years',
            amount: '145'

        },
        {
            Age: '35 to 45 years',
            amount: '145'
        },
        {
            Age: '46 to 55 years',
            amount: '145'
        },
        {
            Age: '56 to 60 years',
            amount: '145'
        },
        {
            Age: '61 to 65 years',
            amount: '145'
        }
    ])

    const handleClick=()=>{
        props.history.push(urls.PAYMENT)
    }

    const handleClickCheckoutMob=()=>{
        props.history.push(urls.SUMMARY_MOB)
    }


    useEffect(() => {
        props.setCart("","",'get')
        // eslint-disable-next-line
      }, [])

    const[cartDta, setCartDta]=useState('')

    const filterCart=(type, data)=>{
        return  data.find(e=>e.type==type)
     }

    const createCheckout=(cartArr)=>{

        const keys=CheckoutItems

        let objCart={ }

        for(let i=0; i<keys.length; i++){
            objCart[keys[i]]=filterCart(keys[i], cartArr.items)
        }

        objCart={...objCart, ...cartArr}
        delete objCart.items
        // 
        setCartDta({...objCart})
      }


      useEffect(() => {
        let dta=localStorage.getItem("order")
        if(dta){
          let da=JSON.parse(dta)
          props.setCart("ES", {
            "purchase_options": {
                // "duration": "duration" in da ? da.duration:"",
                "pay_mode": da.pay_mode
              }
          });
        }

      }, [])

      useEffect(() => {
         if(props.cart.data){
            // 
            createCheckout(props.cart.data.cart)
         }
      }, [props.cart.data])

      const updateTeamCountFn = (e,ind,ageIndex) => {
        // 
        let ltotal = 0;
        let {ES} = cartDta;
        let lval = e.target.value;
        if(ind !== undefined) {
          if(!isNaN(parseInt(lval)))
          {
            ES.details.plans[ind].age_range[ageIndex].total_employees = parseInt(lval);
          }
          else{
            lval = 0;
            ES.details.plans[ind].age_range[ageIndex].total_employees = 0;
          }
          ES.details.plans.map((planObj,planIndex)=>{
            let ltotalCountForPlan = 0;
            ES.details.plans[planIndex].age_range.map((lobj,index)=>{
              
              let lcurrVal = 0;
              if(ageIndex == index && ind == planIndex){
                ltotal = ltotal + parseInt(lval);
                lcurrVal = parseInt(lval);
              }
              else{
                ltotal = ltotal + lobj.total_employees;
                lcurrVal = parseInt(lobj.total_employees);
              }
              ltotalCountForPlan = ltotalCountForPlan + lcurrVal;
            })
            ES.details.plans[planIndex].total_employees = ltotalCountForPlan;
          });
          ES.details.employeeCount.total_employees = ltotal;
        }
        else{
          if(!isNaN(parseInt(lval)))
          {
            ES.details.employeeCount.total_employees = parseInt(lval);
          }
          else{
            lval = 0;
            ES.details.employeeCount.total_employees = 0;
          }
        }
        setCartDta({...cartDta,...ES})
      }

      useEffect(()=>{

        if(buttonClick && props.cart.isLoading == false){

          setButtonClick(false);
          try{
            if(props.cart.data.success == 1)
            {
              notify.success("Team member count added successfully");
            }
            else if(props.cart.data.success == 0 && !!props.cart.data.msg)
            {
              notify.danger(props.cart.data.msg);
            }
            else{
              notify.danger("Error while processing the request. We are looking into it.");
            }
          }
          catch(e){
            notify.danger("Error while processing the request. We are looking into it.");
          }
        }
      },[props.cart])

      const saveBtnClkFn = () => {
        // 
        let lclevertapObj = {};
        let {ES:{details}} = cartDta;
          let lreq = {};
        if(details.employeeCount.total_employees == 0){
            setEditable(true);
            lclevertapObj.data = "Empty";
            notify.danger("Team Member count cannot be empty");
        }else if(details.employeeCount.total_employees < maxLimit.minCountLimit){
            setEditable(true);
            lclevertapObj.data = "Less than 3";
            notify.danger("Team Member count lies between from 3 to 10,000");
        }else if(details.employeeCount.total_employees > maxLimit.maxCountLimit){
            setEditable(true);
            lclevertapObj.data = "Greater than 10k";
            notify.danger("Team Member count lies between from 3 to 10,000");
        }
        else{
          lclevertapObj.data = [];
          setEditable(false);
          setButtonClick(true);
          lreq.plans = details.plans;
          lreq.employeeCount = details.employeeCount;
          props.setCart("ES", {...lreq});
          if(lreq.plans && lreq.plans.length > 0){
            for(let i=0;i<lreq.plans.length;i++){
              if(lreq.plans[i] && lreq.plans[i].age_range){
                lclevertapObj.data.push(lreq.plans[i].age_range);
              }
            }
          }
        }
        Clevertap("EditTeamCountCheckout",{click:true,data:JSON.stringify(lclevertapObj)});
      }

      const[editable, setEditable]=useState(false)


      //for how it works and static pricing

      const[staticPricing, setStaticPricing]=useState()

      useEffect(()=>{
         if(props.pricing.data && props.pricing.data.data){
            setStaticPricing(props.pricing.data.data)
         }
      },[props.pricing.data])

      useEffect(() => {
        props.fetchStaticPricing({
          group_id:props.userData.group_id,
          policy:props.userData.policy_type
        })
      }, [])

    return (
        <>
        {cartDta.ES ?
        <div style={{backgroundColor: '#F5F9FF',width: '100%'}}>
            {/* {console.log("====",cartDta.ES.details.plans)} */}
        {/* {console.log("----",cartDta)} */}
            <Navigation  backPath={urls.ADD_PEOPLE}/>

             <div className="main-flx">
                <div className="frst-flx">
                 <div>
                    <div className="checkout-skel">
                        <CompanyDetails TotalEmployees={cartDta.ES.details.employeeCount.total_employees}/>
                    </div>
                    <Wellnessmem />

                    <TeamCount
                      editable={editable}
                      setEditable={(bool)=>{
                        if(!!cartDta.CN){
                          notify.warning("Coupon code will be removed if you change the count");
                        }
                        setEditable(bool)
                      }}
                      data={totalEmp} dta={cartDta.ES.details.plans} head={heading}
                      TotalEmployees={cartDta.ES.details.employeeCount.total_employees}
                      onChange={(e,ind,ageIndx)=>updateTeamCountFn(e,ind,ageIndx)} saveBtnClk={()=>saveBtnClkFn()}/>
                    {/* <div className="mob-view-compo"><PaymentWork TotalPrice={cartDta.total} staticPricing={staticPricing && staticPricing.payment_text.how_payment_works[0]}/></div> */}

                    <PlanPrice head={heading} dta={ageAmountt} head={heading} staticPricing={staticPricing && staticPricing}/>

                    <div style={{marginTop:"15px"}}>

                      {/*localStorage.getItem("LG")!=1 */}{ false &&
                        <HowItWorksImage/> }

                    </div>
                    {/* <div className="web-view-compo"><PaymentWork TotalPrice={cartDta.total}  staticPricing={staticPricing && staticPricing.payment_text.how_payment_works[0]} /></div> */}

                    <div style={{marginBottom: '0px'}}>
                    <div className="mobview-float-cls">
                         <button className="subscribe-btnn" onClick={()=>handleClickCheckoutMob()}>Subscribe for ₹{numberformat.format(cartDta.total)}/-</button>
                    </div>
                    </div>
                </div>
                </div>
               { heightWidthCalc().width > 800 && (
                <div className="summary-skele">
                    <Summary hideCoupon={true} Pricing={cartDta}  subscribeClick={handleClick} hideSubContent={true}/>
                </div>) }


            </div>
        </div>
        :
        <Loader FullScreen={true}/>}
     </>
    )
}
const mapStateToProps = (state) => {
  
  return {
    cart:state.cart,
    userData:state.userstate.planSelected,
    pricing:state.static_pricing,
    authUser: state.navigationStack.authObj
  }}

  const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...cartActions,
        ...priceActions,
        ...nav_logout,
        ...user_state,
        ...resetCompany
    }, dispatch)
  });


  export default connect(mapStateToProps, mapDispatchToProps)(CheckoutSkeleton)
