import React from 'react'

export default function BillingWork(props) {
    return (
        <div>
            {props.type=="ot_order"?
            <>
            <img className="WebhowWorks" width="100%" src="https://res.cloudinary.com/onsurity/image/upload/v1599843571/OneTimeWeb_n94vmk.svg" />
            <img className="MobilehowWorks" width="100%" src="https://res.cloudinary.com/onsurity/image/upload/v1599843571/OneTimeMob_znwrs6.svg" />
            </>
            :props.type=="si_order" &&
            <>
            <img className="WebhowWorks" width="100%" src="https://res.cloudinary.com/onsurity/image/upload/v1599477452/New-home-screen/AutoWeb_ukhe0r.svg" />
            <img className="MobilehowWorks" width="100%" src="https://res.cloudinary.com/onsurity/image/upload/v1599477452/New-home-screen/AutoMob_kutna2.svg" />
            </> }

        </div>
    )
}
