import React from 'react'

export default function Features_Fitness(props) {
    return (
        <>
            <div className="feature-bene mrgn" >
                <div className="feature-image"><img src={props.data.Feature.featureImage} className="featimgs" /></div>

                <div className="listsflx feature">
                    <div className="feature-header">{props.data.Feature.Heading}</div>
                   
                    <div className="ban-lists">
                       {props.data.Feature.Lists.map(e => (
                           
                            <ul>
                                <li className="colorlists lists">{e.list}</li>
                            </ul>
                     
                           
                    ))
                       }
                        </div>
                    
                </div>
            </div>
        </>
    )
}
