import React, { useEffect, useState } from 'react'
import './Failed.scss';
import Navigation from '../../components/Common/Navigation/Navigation'
import Button from '../../Input-controls/Button/Button';
import {actions as orderAction} from './../../redux/ducks/order';
import { notify } from '../../Input-controls/Toast/Toast';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Loader from '../../utils/Loader/Loader';
import { Clevertap } from '../../utils/Utils';

function Failed(props) {

    const[subscribeClicked, setSubscribeClicked]=useState(false)

    useEffect(() => {
        if(props.payLink.data && subscribeClicked){
          if(props.payLink.data.success==1){
            window.location.href=props.payLink.data.data.redirect_url
          }
          else if(!!props.payLink.data.message){
            notify.danger(props.payLink.data.message);
            setLoader(false)
          }
          else{
            notify.danger(props.payLink.data.msg)
            setLoader(false)
          }
        }
      }, [props.payLink.data])


   const[loader, setLoader]=useState(false)

    const handleClick=()=>{
      let lclevrtapObj = {};
        setSubscribeClicked(true)
        // let data=JSON.parse(localStorage.getItem('orderRequest'))
        let data=props.location.state
        if(data){
            setLoader(true)
            props.putOrder({...data});
            lclevrtapObj.data = {...data};
        }else{
            notify.danger("Invalid request");
            lclevrtapObj.data = "Invalid request";
        }
        Clevertap("TryAgainPaymentFail",{clicked: lclevrtapObj.clicked, data: JSON.stringify(lclevrtapObj)});
      }

    return (
      <>
      {loader && <Loader FullScreen={true}/>}
        <div>
            <Navigation hideBack={true}/>
            <div className="failed-wrp">
                <img alt="paymentFail" src="https://res.cloudinary.com/onsurity/image/upload/v1598001392/New-home-screen/paymntFailImg_uo043h.svg" className="fail-img" />

                <div className="fail-content-cls">
                    <div className="trans-txtt">Transaction Failed</div>
                    <div className="subb-txt">Oh no! Your payment was not successful. Don't worry, you can try again later or try with a new payment method. </div>
                    <div style={{marginTop:'30px'}}>
                        <Button onClick={()=>handleClick()} value="Try Again"/>
                    </div>
                </div>
            </div>
        </div>
        </>
    )
}

const mapStateToProps = (state) => {
    return {
      payLink:state.set_order
  }}

  const mapDispatchToProps = (dispatch) => ({
      ...bindActionCreators({
          ...orderAction
          //ADD IF ITS FROM DIFFRENT ACTION FILE
      }, dispatch)
    });

  export default connect(mapStateToProps, mapDispatchToProps)(Failed)
