import React from 'react'
import './PlanPrice.scss';

export default function PlanPrice(props) {
    return (
            <>
            {props.staticPricing &&
    <>
        <div className="pln-price-cls-web">
            <div className="plan-prc-team">{"Plan Price for Team" + (localStorage.getItem("LG") == "1" ? " (per year)" : " (per month)")}</div>
            <div className="hr-sec"><hr/></div>

                <div className="age-amount-flx">
                        <div className="head-clssdiv">Age Band</div>
                        <div className="head-team-only"> Team Member Only</div>
                        {props.staticPricing.plan_pricing.length>1 &&
                        <div className="head-team-fam">{'Team Member & Family'}</div>}
                </div>
                {props.staticPricing.plan_pricing[0].pricing.map((e,index)=>(
                <div className="age-amount-flx">
                        <div className="age-rng-div">{e.range.split('-').join(' to ')} years</div>
                        <div className="age-per-div">₹ {e.price}</div>
                        {props.staticPricing.plan_pricing.length>1 &&
                          <div className="age-total-div"> {props.staticPricing.plan_pricing[1].pricing[index]?`₹ ${props.staticPricing.plan_pricing[1].pricing[index].price}`:'NA'}</div>
                        }
                </div>
                    ))}


            </div>

                    {/* <div className="pln-price-cls-mob">
                    <div className="plan-prc-team">Plan price for team</div>
                    <div className="hr-sec"><hr/></div>

                        <div className="age-amount-flx">
                                <div className="head-clss">{props.head.headingg}</div>
                                <div className="head-clss">{props.head.pricemob}</div>
                        </div>

                        {props.dta.map(e=>(
                        <div className="age-amount-flx">
                                <div className="age-rng-div">{e.Age}</div>
                                <div className="age-rng-div">{e.amount}</div>
                        </div>
                            ))}
                    </div> */}
                    </>
}
                    </>
    )
}
