import React  from 'react';
import {numberWithCommas} from '../../../utils/Api.js';

export default function AddPplSummry_Mob(props) {

  return(
    <>
      <div className="AddPplSummry_main_cls">
        <div className="frstColAdplSum">
          <div className="adjstSzSumCls tmPriceLbl">
          Price per Team Member
          </div>
          <div className="adjstSzSumCls tmPriceVal">
            {"₹"+ numberWithCommas(props.totalValues.totalAmnt)}
          </div>
        </div>
        <div className="sendColAdplSum">
          <div className="adjstSzSumCls tmCountSumryLbl">
            Team Member Count
          </div>
          <div className="adjstSzSumCls tmCountSumryVal DevPaddingCount">
            {numberWithCommas(props.totalValues.totalCount)}
          </div>
        </div>
      </div>
    </>
  )
}
