import React,{useState,useEffect} from 'react'
import AddPplTitle from './Components/AddPplTitle';
import AddPplTitle_Mob from './Components/AddPplTitle_Mob';
import AddPplCount from './Components/AddPplCount';
import AddPplCount_Mob from './Components/AddPplCount_Mob';
import AddPplSummry from './Components/AddPplSummry';
import AddPplSummry_Mob from './Components/AddPplSummry_Mob';
import AddPplBtn from './Components/AddPplBtn';
import './AddPeople_Skelton.scss';
import Navigation from '../../components/Common/Navigation/Navigation';
import { urls } from '../../routes/routes';
import {g_teamMemberOnly, g_teamMemberFamily} from '../../utils/Constants';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import {actions as planAction} from '../../redux/ducks/state_change';
import {actions as cartActions} from '../../redux/ducks/cart';
import {actions as StaticPriceActions} from '../../redux/ducks/static_pricing';
import { notify } from '../../Input-controls/Toast/Toast';
import Popup from "../../components/Common/ConfirmationPopUp/Popup";
import Loader from '../../utils/Loader/Loader';
import { Clevertap } from '../../utils/Utils';

function AddPeople_Skelton(props) {

  const [popUpState,setPopUpState]= useState({
    show: false,
    errorMsg: {}
  });

  const [buttonClick,setButtonClick] = useState(false);
  const [selectBenfType, setSelectBenfType] = useState("");
  //const [benfTypeArr, setBenfTypeArr] = useState(["TM","TMF"]);
  const [contentObj,setContentObj] = useState({head:[],body:[]});
  const [totalValues, setTotalValues] = useState({totalAmnt:0,totalCount:0});

  const [titleObj,setTitleObj] = useState({});

  const[showOrignalPricing]=useState(false);

  const maxLimit= {
    maxAmountLimit: 99999999,
    maxCountLimit: 10000,
    minCountLimit: 3,
  };

  const titleArr = [
    {
      "id" : g_teamMemberFamily,
      "title" : "Team Members Count",
      "content" : "Please fill below the number of team members as per their age and family status. If you do not have the required details, then we will take our default selection and adjust the difference in the next invoice. "
    },
    {
      "id" : g_teamMemberOnly,
      "title" : "Team Members Count",
      "content" : "Please fill below the number of team members as per their age. If you do not have the required details, then we will take our default selection and adjust the difference in the next invoice. "
    },

  ];

  const [custmCls,setCustmCls] = useState("");


  const[planType, setPlanType]=useState('')

  useEffect(() => {
    let dta=""
    let flag=localStorage.getItem("LG")
    if(flag && flag==1){
      dta="/yr"
    }else{
      dta="/mo"
    }
    setPlanType(dta)

  }, [])

  useEffect(()=>{
    
    let lteamBenefit = "";
    if(props.planSelectData.policy_type.indexOf(g_teamMemberFamily) > -1){
      lteamBenefit = "TMF";
      setSelectBenfType(lteamBenefit);
      setCustmCls("TMF_Outer");
    }
    else if(props.planSelectData.policy_type.indexOf(g_teamMemberOnly) > -1){
      lteamBenefit = "TM";
      setCustmCls("TM_Outer");
      setSelectBenfType(lteamBenefit);
    }
    let lflag = true;
    // eslint-disable-next-line
    titleArr.map((obj,ind) => {
      
      if(localStorage.getItem("LG") == 1){
        obj.title = obj.title.replace("healthcare","lifecare");
      }
      if(props.planSelectData.policy_type.indexOf(obj.id) > -1 && lflag)
      {
        lflag = false;
        obj.id = lteamBenefit;
        setTitleObj(obj);
      }
    });
    let lobj = {};
    if(lteamBenefit == "TMF")
    {
      lobj.head = [
        "Team Member Age Band", "Price / Unmarried or Single Team Member", "Count", "Price / Married or with Children Team Member", "Count"
      ]
    }
    else{
      lobj.head = [
        "Team Member Age Band", "Price / Team Member", "Count"
      ]
    }
    lobj.id = lteamBenefit;
    lobj.body = props.planSelectData.demographicRange;
    setContentObj(lobj);
    // 
    setButtonClick(true)
    props.fetchStaticPricing({
      group_id:props.planSelectData.group_id,
      policy:props.planSelectData.policy_type
    })
    /*contentCountArr.map((obj,ind) => {
      if(obj.id == /*props.planSelectData.plan_id -- g_teamMemberFamily)
      {

      }
    });*/
  },[])

  useEffect(()=>{
    
    if(buttonClick && !!props.staticPricing.data && props.staticPricing.isLoading == false){
      setButtonClick(false);
      let llaunchInputObj = {}
      let {body} = contentObj;
      if(props.staticPricing.data != "Failed")
      {
        body.map((lobj,ind)=>{
          
          for(var i=0;i<props.staticPricing.data.data.plan_pricing.length;i++){
            if(props.staticPricing.data.data.plan_pricing[i].policy_type == g_teamMemberOnly)
            {
              llaunchInputObj.flg = "TM";
              llaunchInputObj.name = "ipt_bdy_name_tm_0";
              // console.log(props.staticPricing.data.data.plan_pricing[i].pricing[ind])
              // lobj.pre_discount_price_per_user = props.staticPricing.data.data.plan_pricing[i].pricing[ind].pre_discount_price_per_user;
              lobj.total_price_per_user = props.staticPricing.data.data.plan_pricing[i].pricing[ind].price;
            }
            else if(props.staticPricing.data.data.plan_pricing[i].policy_type == g_teamMemberFamily)
            {
              llaunchInputObj.flg = "TMF";
              llaunchInputObj.name = "ipt_bdy_name_tmf_0";
              // lobj.pre_discount_price_per_user_TMF = props.staticPricing.data.data.plan_pricing[i].pricing[ind].pre_discount_price_per_user;
              lobj.total_price_per_user_TMF = props.staticPricing.data.data.plan_pricing[i].pricing[ind].price;
            }
          }
        })
      }
      else {
        notify.danger("We are facing some issue, kindly try after some time");
      }
      setContentObj({...contentObj,...body});
      getSummarizeVals();
      if (Object.keys(props.addPplInptVal).length == 0) {
        llaunchInputObj.ind = 0;
        llaunchInputObj.val = parseInt(props.totalEmployees);
        //from mahindras comment starts
        props.planSelectData.demographicRange.map((lobj,ind) => {
          
          if(lobj.selectedAgeRange == ind){
            llaunchInputObj.ind = ind;
            if(llaunchInputObj.flg == "TMF"){
              llaunchInputObj.name = "ipt_bdy_name_tmf_"+ind;
            }
            else if(llaunchInputObj.flg == "TM"){
              llaunchInputObj.name = "ipt_bdy_name_tm_"+ind;
            }
          }
        });
        //from mahindras comment ends
        calcSumValsFn(llaunchInputObj, "addPeople", llaunchInputObj.name, llaunchInputObj.val);
      }
    }
    else if(props.staticPricing.isLoading == false){
      //notify.danger(props.cart.data.msg);
    }

  },[props.staticPricing])

  useEffect(()=>{
    //
    if(!!props.planSelectData && !!props.planSelectData.demographicRange)
    {
      getSummarizeVals();
    }
  },[props.addPplInptVal])

  const getSummarizeVals = () => {
    let {totalAmnt, totalCount} = totalValues;
    totalAmnt = 0; totalCount = 0;
    props.planSelectData.demographicRange.map((lobj,ind) => {
      if(lobj.total_price_per_user_TMF == undefined){
        lobj.total_price_per_user_TMF = 0;
      }
      totalCount = totalCount + lobj.TMCount + lobj.TMFCount;
      totalAmnt = totalAmnt + (lobj.total_price_per_user * lobj.TMCount) + (lobj.total_price_per_user_TMF * lobj.TMFCount);
    })
    setTotalValues({...totalValues , totalAmnt, totalCount});
  }

  const calcSumValsFn = (pobj, key, name, value) => {
    
    let ltotal = 0;
    for(let i=0;i<props.planSelectData.demographicRange.length;i++)
    {
      let ltm = props.planSelectData.demographicRange[i].TMCount;
      let ltmf = props.planSelectData.demographicRange[i].TMFCount;
      if(i == pobj.ind)
      {
        if(pobj.flg == "TM"){
          ltm = pobj.val;
        }
        else if(pobj.flg == "TMF"){
          ltmf = pobj.val;
        }
        ltotal = ltotal + ltm + ltmf;
      }
      else{
        ltotal = ltotal + ltm + ltmf;
      }
    }
    if(ltotal > maxLimit.maxCountLimit){
      value = 0;
      props.updateDemographicRange({dataArr:props.planSelectData.demographicRange, key, name, value});
      notify.danger("Team Member count lies between from 3 to 10,000");
    }
    else{
      props.planSelectData.demographicRange.map((lobj,ind) => {
        if(lobj.index == pobj.ind)
        {
          if(pobj.flg == "TM")
          {
            lobj.TMCount = pobj.val;
          }
          else if(pobj.flg == "TMF"){
            lobj.TMFCount = pobj.val;
          }
        }

      });
      props.updateDemographicRange({dataArr:props.planSelectData.demographicRange, key, name, value});
    }
  };

  useEffect(()=>{

    if(buttonClick && props.cart.isLoading == false){
      setButtonClick(false);
      try{
        if(props.cart.data.success == 1)
        {
          props.history.push(urls.CHECKOUT)
        }
        else if(props.cart.data.success == 0 && !!props.cart.data.msg)
        {
          notify.danger(props.cart.data.msg);
        }
        else{
          notify.danger("Error while processing the request. We are looking into it.");
        }
      }
      catch(e){
        notify.danger("Error while processing the request. We are looking into it.");
      }
    }
  },[props.cart])

  const proccedBtnClkFn = (pflag) => {
    let flag=localStorage.getItem("LG");
    let lclevrtapObj = [];
    let product="Healthsure"
    if(flag==1){
      product="LifeCare"
    }
    // 
    setButtonClick(true);
    let totalAmnt = 0;
    let totalCount = 0;
    let lreq = {};
    lreq.plans = [];
    //lreq.employeeCount.total_employees = parseInt(props.totalEmployees);
    props.staticPricing.data.data.plan_pricing.map((lobj,ind)=>{
      
      let tempObj = {};
      let totalCount = 0;
      tempObj.plan_id = lobj.plan_id;
      tempObj.offerings = "default";
      tempObj.gender = "all";
      tempObj.insured_amount = props.planSelectData.insured_amount;
      tempObj.show_monthly = true;
      tempObj.pricing_frequency= "monthly"
      tempObj.age_range = [];
      tempObj.target_product=product
       props.planSelectData.demographicRange.map((linrObj,index) => {
         
         let ltempInrObj = {};
         ltempInrObj.range = linrObj.range;
         if(lobj.policy_type == g_teamMemberOnly){
           totalCount = totalCount + linrObj.TMCount;
           ltempInrObj.total_employees = linrObj.TMCount;
         }
         else if(lobj.policy_type == g_teamMemberFamily){
           totalCount = totalCount + linrObj.TMFCount;
           ltempInrObj.total_employees = linrObj.TMFCount;
         }
         if(pflag == "addLater"){
           ltempInrObj.total_employees = 0;
         }
         tempObj.age_range.push(ltempInrObj);
         ltempInrObj.plan = lobj.plan_id;
         lclevrtapObj.push(ltempInrObj);
       })
       tempObj.total_employees = totalCount;
       if(pflag == "addLater"){
         tempObj.total_employees = 0;
       }
       lreq.plans.push(tempObj);
    })
    // console.log(JSON.stringify(lreq));
    /*lreq.employeeCount.age_range = [];*/
    if(pflag == "proceed"){
      props.planSelectData.demographicRange.map((lobj,ind) => {
        totalCount = totalCount + lobj.TMCount + lobj.TMFCount;
        totalAmnt = totalAmnt + (lobj.total_price_per_user * lobj.TMCount) + (lobj.total_price_per_user_TMF * lobj.TMFCount);
      });
      lreq.employeeCount = {};
      lreq.employeeCount.total_employees = totalCount;
    }
    else if(pflag == "addLater"){
      lreq.plans.map((lobj,ind)=>{
        if(lreq.plans.length == 2){
          lreq.plans[1].age_range[0].total_employees = parseInt(props.totalEmployees);
          lreq.plans[1].total_employees = parseInt(props.totalEmployees);
        }
        else{
          lreq.plans[0].age_range[0].total_employees = parseInt(props.totalEmployees);
          lreq.plans[0].total_employees = parseInt(props.totalEmployees);
        }
      })
      lreq.employeeCount = {};
      lreq.employeeCount.total_employees = parseInt(props.totalEmployees);
    }

    
    if("proceed" == pflag)
    {
      if(totalCount == 0 || totalAmnt == 0){
        notify.danger("Team Member count cannot be empty");
      }
      else if(totalAmnt > maxLimit.maxAmountLimit || totalCount > maxLimit.maxCountLimit){
        notify.danger("Team Member count lies between from 3 to 10,000");
      }
      else if(totalCount < maxLimit.minCountLimit){
        notify.danger("Team Member count lies between from 3 to 10,000");
      }
      else{
        Clevertap("ProceedBtnClick",{click:true,data:JSON.stringify(lclevrtapObj)});
        /*if(parseInt(props.totalEmployees) != totalAmnt){
          let lobj= {};
          lobj.show = true;
          lobj.errorMsg= {};
          lobj.errorMsg.title = "Confirmation";
          lobj.errorMsg.content = "The Employee count which you have given previously and current one doesn't match. So do you want to procced with this?";
          setPopUpState(lobj)
        }
        else{*/
          props.setCart("ES", {...lreq});
        //}
      }
    }
    else{
      props.setCart("ES", {...lreq});
    }

  }

  const closePopup = () => {
    let lobj= {};
    lobj.show = false;
    lobj.errorMsg= {};
    setPopUpState(lobj);
  }
  return (
    <>
      <Navigation backPath={urls.SELECT_PLAN}/>
      {props.staticPricing.isLoading?<Loader FullScreen={true}/>:<>
      <div style={{backgroundColor: '#F5F9FF',width: '100%'}} className="adPplCls">
        <div className={"adPl_Skltn_Cls "+custmCls}>
          <div className="txtCntr padTitleParntCls webView">
            <AddPplTitle benefitType={selectBenfType} titleObj={titleObj}/>
          </div>
          <div className="listOuterCls webView">
            <AddPplCount planType={planType} showOrignalPricing={showOrignalPricing} benefitType={selectBenfType}
              contentObj={contentObj} calculateSumerize={calcSumValsFn} isLoading={props.cart.isLoading}/>
          </div>
          <div className="sumryOuterCls webView">
            <AddPplSummry benefitType={selectBenfType} totalValues={totalValues}/>
          </div>
          <div className="BtnsOuterCls View webView">
            <AddPplBtn benefitType={selectBenfType} proccedBtnClk={(e)=>proccedBtnClkFn(e)}/>
          </div>
        </div>

        <div className={"adPl_Skltn_Cls mob_"+custmCls}>
          <div className="txtCntr padTitleParntCls mobView">
            <AddPplTitle_Mob benefitType={selectBenfType} titleObjMob={titleObj}/>
          </div>
          <div className="listOuterCls mobView">
            <AddPplCount_Mob planType={planType} showOrignalPricing={showOrignalPricing} benefitType={selectBenfType}
              contentObj={contentObj} calculateSumerize={calcSumValsFn} isLoading={props.cart.isLoading}/>
          </div>
          <div className="mobView alignProperAdPl"/>
          <div className="adPplFxdSec">
            <div className="sumryOuterCls mobView ">
              <AddPplSummry_Mob benefitType={selectBenfType} totalValues={totalValues}/>
            </div>
            <div className="BtnsOuterCls View mobView ">
              <AddPplBtn benefitType={selectBenfType} proccedBtnClk={(e)=>proccedBtnClkFn(e)}/>
            </div>
          </div>
        </div>
      </div>
      <Popup
          show={popUpState.show}
          data={popUpState.errorMsg}
          closePopup={()=>closePopup()}
        />
        </>}
    </>
  )
}

const mapStateToProps = (state) => {

  return {
    planSelectData:state.userstate.planSelected,
    addPplInptVal : state.userstate.addPeople,
    totalEmployees : state.userstate.companyDetails.TotalEmployees,
    cart: state.cart,
    staticPricing: state.static_pricing
}}

const mapDispatchToProps = (dispatch) => ({
  ...bindActionCreators({
      ...planAction,
      ...cartActions,
      ...StaticPriceActions
  }, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(AddPeople_Skelton)
