import React  from 'react'
import './InfoBox.scss'

export default function InfoBox(props) {
   
    return (
        <div style={{display:props.showInfo?'block':'none'}}>
            <div className="info-modal" style={{display:props.showInfo?'block':'none'}}>
                <div className="info-modal-content">
                    <div className="info-main-con mb-3">
                        <div align="right">
                        <img src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594396338/Email-template/Group_582_hnn5xw.png" alt="close" className="info-close" onClick={()=>props.setShowInfo(false)}/>
                        </div>
                       
                        {/* <div className="info-close" onClick={()=>props.setShowInfo(false)}>&times;</div> */}
                        <div className="">
                            {props.children}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
