import React, { useState } from 'react'
import "./CoverageCard.scss"

export default function CoverageCard(props) {

    const HandleCardClick=()=>{
        if(!props.data.mandatory){
            props.handleClick(props.data._id)
        } 
    } 

    let status=props.selected.includes(props.data._id)

    return (
        <> 
        {/* {console.log(props.data.id, props.selected)} */}
        <div className={`${props.data.mandatory==true && "SelectedBasePlan"} CardsCovid ${status && "clicked_coverage"}`} onClick={()=>HandleCardClick()}>
        
        {props.data.mandatory==true &&
        <div class="covrgeCjek customCheckCovergae greenbasesel"> 
            <span class="checkmark_bss_plan">
                <div class="checkmark_stem_plan"></div>
                <div class="checkmark_kick_plan"></div>
            </span>
        </div>}

        {status  &&
        <div class="covrgeCjek customCheckCovergae">
            <span class="checkmark_bss_plan">
                <div class="checkmark_stem_plan"></div>
                <div class="checkmark_kick_plan"></div>
            </span>
        </div>}

          <div className={`${status && "title-img-selctd"}`}>
                <div className={`title-imgflx`}>
                    <div className="titleflx">{props.data.name}</div> 
                    <div className="imggflx"><img src={props.data.icon} width="67px" /></div> 
                </div>

                <div>
                    {
                        props.data.benefits.map(i=>(
                            <div className={`card-content`} >{i}</div>
                        ))
                    }
                </div>
          </div>
        </div>
        </>
    )
}
