import React from 'react'
import './CountryInput.scss';

export default function CountryInput(props) {
    return (
        <>
            <div className={`inputtdivw ${props.className}`}  style={props.style}>
            <div className="outerdiv" >
                <div className="digitflex">
                    <div className="numberc">+91</div>
                </div>
                <div className="inputflex">
                   <input
                         className="inputStyle"
                         name={props.name}
                         type={props.type}
                         value={props.value}
                         placeholder={props.placeholder}
                         onKeyDown={props.keyDownFunction === "true" ? (e)=>props.onKeyDown(e) : null}
                         onChange={(e)=>props.onChange(e)}/>
                </div>
            </div>
            </div>

        </>
    )
}
