import React from 'react'
import './ItemSelectMob.scss';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import {actions as coveragesActions} from './../../../../../redux/ducks/state_change';

function ItemSelectMob(props) {

    
const HandleRemove=(id)=>{
    let data=[...props.coverages]
    data.splice(props.coverages.indexOf(id), 1)
    props.setCoverages(data)
}


    return (
        <div className="MobBacsgs">
            <div className="customise-txt">Customised Plan</div>
            <div className="dsply-mob">
                
            <div style={{overflowX:'auto'}}>
                
                <div className="CustMarCllsew">
                    <div className="basic-pln">{props.data.find(e=>e.mandatory===true).name}</div>
                     
                    {props.coverages && props.coverages.map(e=>{
                        
                        let data=props.data.find(i=>i._id==e)
                        // console.log("+++",data)
                        return (
                            <div className="item-select">
                                <div className="health-prtct">{data.name}</div> 
                                <div style={{border:"1px solid rgba(22, 53, 109, 0.45)"}}></div>
                                <div  style={{whiteSpace: 'nowrap',padding: '0px 10px',textAlign: 'center'}}>
                                    
                                    <img onClick={()=>HandleRemove(data._id)} src="https://res.cloudinary.com/onsurity/image/upload/v1598266010/New-home-screen/Group_1666_bmuk41.svg" width="12px" style={{paddingTop: '10px',cursor: 'pointer'}} />
                                </div>
                            </div>
                        )
                    }
                )}
                </div>
            </div>
                
            </div>
        </div>
    )
}

    const mapStateToProps = (state) => ({
        coverages:state.userstate.coverages
    })
    
    const mapDispatchToProps = (dispatch) => ({
        ...bindActionCreators({
        ...coveragesActions,
        }, dispatch)
    })
    
export default connect(mapStateToProps, mapDispatchToProps)(ItemSelectMob)



