import React, {useState} from 'react'

export default function ContUsSpec(props) {

  return (
    <>
      <div className="ContUsSpecOut_CntUs_Cls">
        {
          props.data.map((lobj,ind)=>(
            <div className="SpecCntUsDivSec">
              <div className="imgSectCntUsDivCls">
                <img className="lazyload" src={lobj.url} alt="contactUs" />
              </div>
              <div className="headSecCntUsSpec">
                {lobj.head}
              </div>
              <div className="subHeadSecCntUsSpec">
                {lobj.subHead}
              </div>
            </div>
          ))
        }
      </div>
    </>
  )
}
