import React, { useState } from 'react'
import './CoverageItems.scss'
import CoverageCard from './Coveragecard/CoverageCard'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import {actions as coveragesActions} from './../../../../redux/ducks/state_change';

function CoverageItems(props) { 
   
     

    const handleClick=(id)=>{
        if(props.coverages.includes(id)){ 
            let data=[...props.coverages]
            data.splice(props.coverages.indexOf(id), 1)
            props.setCoverages(data)
        }else{
            if(props.singleSelect==true){
                props.setCoverages([id])
            }else{
                props.setCoverages([...props.coverages, id])
            }
        }
   }

    return (
        <>
        <div className="cov-wrpdiv">
        <div className="mainhaeading-txt">Give the best benifits for your team.</div>
        <div className="card-subheader">Select plans from the list</div>
        </div>
        {/* {console.log("-----",props.coverages)} */}
        <div className="Clardscrollmaoboor">
            <div className="Card ContentCovid">
                {props.data.map(e=>(
                    <>
                <CoverageCard data={e} selected={props.coverages} handleClick={(id)=>handleClick(id)}/>
                {/* <CoverageCard data={e} selected={props.coverages} handleClick={(id)=>handleClick(id)}/> */}
                </>
                ))}
            </div>
        </div>
        </>
    )
}


const mapStateToProps = (state) => ({
    coverages:state.userstate.coverages
})

const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...coveragesActions,
    }, dispatch)
})

export default connect(mapStateToProps, mapDispatchToProps)(CoverageItems)