import React, { useRef } from 'react'
import './SearchInput.scss';

export default function SearchInput(props) {

        const myRefname= useRef(null);
    
        const handleClick = () => {
            myRefname.current.focus();
         }
        

    return (
        <>
            <div className={`inputtdiv ${props.className}`} onClick={handleClick}  style={props.style}>
            <div className="outerdiv" >
                <div className="digitflex">
                    <div className="number">
                        <img src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594105691/New-home-screen/Group_4_xhkxm8.svg" className="lazyload" />
                    </div>
                </div>
                <div className="inputflex">
                   <input
                         className="inputStyle"
                         name={props.name}
                         type={props.type}
                         value={props.value}
                         placeholder={props.placeholder}
                         
                         ref={myRefname}
                         onKeyDown={props.keyDownFunction === "true" ? (e)=>props.onKeyDown(e) : null}
                         onChange={(e)=>props.onChange(e)}/>
                </div>
            </div>
            </div>

        </>
    )
}
