export const types={
    FETCH_GSTIN:'GST/FETCH_GSTIN',
    FETCH_GSTIN_SUCCESS:'GST/FETCH_GSTIN_SUCCESS',
    FETCH_GSTIN_FAIL:'GST/FETCH_GSTIN_FAIL'
}

export const initialState = {
    gstin:'',
    data:null,
    isLoading: false,
    error: null
  }

  export default (state = initialState, action) => {
    switch (action.type) {
      case types.FETCH_GSTIN:
            return { ...state, gstin:action.gst, isLoading:true, data: null}

      case types.FETCH_GSTIN_SUCCESS:
            return { ...state, isLoading:false, error:false, data:action.data}

      case types.FETCH_GSTIN_FAIL:
            return { ...state, isLoading:false, error:true, data:action.data}

      default:
        return state
    }
  }

  export const actions={
      fetchGstin:(gst)=>({type:types.FETCH_GSTIN, gst})
  }
