import React, {useState, useEffect} from 'react'
import './ContactUsSkeleton.scss';
// import Payment from '../Payment/Components/Payment/Payment';
import { urls } from '../../routes/routes';
import Navigation from '../../components/Common/Navigation/Navigation';
import {data} from './Contact_data.js';

import HeadSection from './Components/HeadSection';
import ContUsSpec from './Components/ContUsSpec';
import MapAddrSec from './Components/MapAddrSec';
import FooterComponent from '../../components/Common/Footer/footer';
import Header from '../../components/Home/HeaderSection/Header';
import { navigation } from '../../utils/Env';
import {actions as stateActions} from '../../redux/ducks/state_change'
import { withRouter } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Helmet } from 'react-helmet';

function ContactUsSkeleton(props) {

    useEffect(() => {
        props.setNavSelected(navigation.CONTACT)
      }, [])

    return (
        <>

        <div style={{backgroundColor: '#FFF',width: '100%'}}>
            <Header hideGetApp={localStorage.getItem("LG")==1 && true}/> 
             <div className="parentOut_Dtls_Cls">
                <HeadSection data={data.heading}/>
                <ContUsSpec data={data.contactUsSpec}/>
                <MapAddrSec data={data.AddressSection}/>
                <FooterComponent />
            </div>
        </div>
     </>
    )
}

const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...stateActions,
        //ADD IF ITS FROM DIFFRENT ACTION FILE
    }, dispatch)
  });
  
  
  export default withRouter(connect(null, mapDispatchToProps)(ContactUsSkeleton))
  