import React from 'react';
import {GHIFooterData} from './GHI_Data';
import './GHISeo.css';

function GHISeo(props) {
  return (
    <>
      {
        <div className="GhiSeoPrntCls">
          {
            GHIFooterData.map((lobj,index)=>(
              <div className={"SectionGhiParentCls sectionGhiPrnt_"+index}>
                <h1 className="SeoFtrHeadingGhiCls">
                  {lobj.MainHeading}
                </h1>
                <div className={"subContPrntGhiCls subCntPrntGhiCls_"+index}>
                  {
                    lobj.subCntArr.map((currObj,ind)=>(<>
                      <div className={"ContentSectnGhiCls contSectnGhiCls_"+index+"_"+ind}>
                      {
                        currObj
                      }
                      </div>
                      </>
                    ))
                  }
                </div>
              </div>
            ))
          }
        </div>
      }
    </>
  )
}

export default GHISeo
