import React, { useEffect, useState } from 'react'
import './Success.scss';
import Navigation from '../../components/Common/Navigation/Navigation'
import Wellnessmem from '../CheckOut/Components/Wellness-Membership/Wellnessmem';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import {actions as verifyActions} from './../../redux/ducks/verify_order'
import {actions as stateActions} from './../../redux/ducks/state_change'
import Loader from '../../utils/Loader/Loader';
import { notify } from '../../Input-controls/Toast/Toast';
import { getQueryStringParams, Clevertap } from '../../utils/Utils';
import { urls } from '../../routes/routes';
import { withRouter } from 'react-router-dom';
import { DashboardUrl } from '../../utils/MicroServiceURL';

function Success(props) {

    useEffect(() => {
        let lgdata=localStorage.getItem("LG")
        if(lgdata==1){
            localStorage.setItem("LG",0);
        }
      }, [])

      const[paramData, setParamData]=useState() 

    useEffect(() => {
        if(props.location.search){
            let data=getQueryStringParams(window.location.search)
            if('order_id' in data && 'thash' in data){
                setParamData(data)
                props.verifyOrderFn({onsurity_order_id:data.order_id , pay_hash:data.thash})
            }else{
                props.history.push(urls.HOME);
                // notify.danger("Invalid request")
            }
        }else{
            props.history.push(urls.HOME);

            // notify.danger("Invalid request")
        }
    }, [])



    const[userData, setUserData]=useState({})

    useEffect(() => {
        if(props.state.data){
          let lclevrtapObj = {};
            if(props.state.data.success==1){
                let lobj = {};
                lobj.success = true;
                Clevertap("Payment_Successful",lobj);
                props.setDashbordAccess(true)
                setUserData(props.state.data.data);
            }else if('code' in props.state.data && props.state.data.code == 103){
                let lobj = {};
                lobj.failed = true;
                lobj.code = 103;
                Clevertap("Payment_Failure",lobj);
                props.history.push(urls.HOME)
            }
            else if( 'code' in props.state.data &&  props.state.data.code == 101){
                let lobj = {};
                lobj.failed = true;
                lobj.code = 101;
                Clevertap("Payment_Failure",lobj);
                props.history.push({
                    pathname:urls.PAYMENT_FAIL,
                    state:{
                        pay_mode: paramData.pay_mode,
                        pay_option: paramData.pay_option
                    }
                });
            }else{
                if(props.state.data.msg){
                  notify.danger(props.state.data.msg);
                  let lobj = {};
                  lobj.failed = true;
                  lobj.data = props.state.data.msg;
                  Clevertap("Payment_Failure",lobj);
                }
                props.history.push(urls.HOME)
            }
        }
    }, [props.state.data])

    const getTotalCountFn = () => {
    //   let totalCount = 0;
    //   if(!!userData.subscriptions && userData.subscriptions.length > 0){
    //     userData.subscriptions.map((lobj,ind)=>{
    //       totalCount = totalCount + lobj.plan.total_employees;
    //     }) 
    //   }
    console.log(userData,'2345654321')

    if('employer' in userData){
        return userData.employer.total_employees;
    }
    return 'NA'
        
    }
    const getGstIn = () => {
      let lgst = "-";
      if(!!userData.employer && !!userData.employer.gstin){
        lgst = userData.employer.gstin;
      }
      return lgst;
    }
    const getBusnessName = () => {
      let lname = "";
      if(!!userData.employer && !!userData.employer.business_name){
        lname = userData.employer.business_name;
      }
      return lname;
    }
    const getAdminName = () => {
        let Adminname = "";
        if(!!userData.employer && !!userData.employer.name){
            Adminname = userData.employer.name;
        }
        return Adminname;
      }
    const getSubscrptnFn = () => {
      let subscriptionArr = [];
      if(!!userData.subscriptions && userData.subscriptions.length > 0){
        userData.subscriptions.map((lobj,ind)=>{
          subscriptionArr.push(lobj._id);
        })
      }
      return subscriptionArr.join(', ');
    }


    const HandleInvite=()=>{
        let lobj = {};
		    lobj.clicked = true;
        Clevertap("Invite_Employees",lobj);
        window.location.href=`${DashboardUrl}?token=${localStorage.getItem('token')}&success=1`
    }
    return (
        <>
        {/* {console.log(userData)} */}
        {props.state.isLoading?<Loader FullScreen={true}/>:
        <div className="paySuccCls">
            <Navigation hideBack={true}/>
            <div className="success-dsplyflx">
                <div className="succ-img-flx">
                    <img src="https://res.cloudinary.com/onsurity/image/upload/v1598002596/New-home-screen/paymtSuccImg_cxoedd.svg" alt="success" className="success-imgg" />
                </div>
                <div className="succ-congr-flx">
                    <div className="success-wrpp-div">
                    <div className="congrts-txt-div">Hurray! Payment Successful</div>
                    <div className="succ-msg-mobview">Payment Successful</div>
                    {localStorage.getItem("LG") == "0" ?
                      <div className="subhead-txt">Thank you for trusting us !</div>
                        : <div className="subhead-txt">Thank you for trusting us with your health. You will receive a payment receipt on your registered email </div>
                    }
                    <div className="subhead-mobview">We taken auto payment approval, from next payment we detect from your card</div>
                    </div>

                </div>
            </div>
        <div className="pay-cycle-content">{userData && userData.message}</div>

            <div className="success-addr-flx">
                <div className="addr-frst-flx">
                    <div className="addr-plandetail">
                        <div className="onsu-name-cls">{getBusnessName()}</div>

                        <div className="user-details-sec">{getTotalCountFn()} Members</div>
                        {getGstIn()!=="-" &&
                        <div className="user-details-sec">GSTIN : {getGstIn()}</div>}

                        <div className="user-details-sec">{getAdminName()}</div>
                        <div className="user-details-sec">Subscription id(s): {getSubscrptnFn()}</div>
                        <div className="user-details-sec">Payment Status: Success</div>
                        <div className="succ-hr"></div>
                        <div style={{paddingTop: '0px'}}>
                            <Wellnessmem/>
                        </div>
                    </div>

                </div>

                <div className="invite-flx">
                    <div className="success-secnd-flx">
                        <div className="header-dash">Get your Employer Dashboard</div>

                        <div className="getstartedd">
                    <div className="checkcontent-succ">
                        <div className="checkk-succ">
                            <label class="contain-succ">
                                             <input type="checkbox" checked="checked" />
                                             <span class="succ-checkmark"></span>
                                         </label>
                        </div>
                        <div className="contentt contentTxt">
                        A dedicated dashboard to manage your team and their {/*healthcare*/} membership plan access.
                        </div>
                    </div>
                    <div className="checkcontent-succ">
                        <div className="checkk-succ">
                            <label class="contain-succ">
                                             <input type="checkbox" checked="checked" />
                                             <span class="succ-checkmark"></span>
                                         </label>
                        </div>
                        <div className="contentt contentTxt">
                        View quick and easy statistics on your team plan. Billing details, employee status, plan information and more.
                        </div>
                    </div>
                    <div className="checkcontent-succ">
                        <div className="checkk-succ">
                            <label class="contain-succ">
                                             <input type="checkbox" checked="checked" />
                                             <span class="succ-checkmark"></span>
                                         </label>
                        </div>
                        <div className="contentt contentTxt">
                        Invite your team through the dashboard and activate their membership instantly.

                        </div>
                    </div>
                    <div align="center">
                        <button className="getbtn" onClick={()=>HandleInvite()}>Invite Employees</button>
                    </div>


            </div>
                    </div>

                </div>

            </div>
        </div>}
        </>
    )
}

const mapStateToProps = (state) => ({
    state:state.verify_order
  })

  const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...verifyActions,
        ...stateActions
    }, dispatch)
  });


  export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Success))
