import React from 'react'
import Header from '../../components/Home/HeaderSection/Header'
import Banner_Benefits from './Components/Banner_Benefits'
import Benefits_of_ordereing from './Components/Benefits_of_ordereing'
import How_It_Works from './Components/How_It_Works'
import Org_Benefits from './Components/Org_Benefits'
import FooterComponent from '../../components/Common/Footer/footer';
import Card_Title from './Components/Card_Title'
import { HealthCheckup_Data } from './Data/HealthCheckup_Data'

export default function HealthCheckup_Details() {
    return (
        <div>
              <Header />
            <Card_Title data={HealthCheckup_Data} />
            <Banner_Benefits data={HealthCheckup_Data}/>
            <Benefits_of_ordereing data={HealthCheckup_Data} />
            <How_It_Works data={HealthCheckup_Data} lists={true}/>
            <Org_Benefits />
            <div className="footer-comp">
            <FooterComponent />
            </div>
        </div>
    )
}
