import React from 'react'
import './Styles.scss';

export default function Covered(props) {
    return (
        <div className="covered-not-div">
            <div className="coveredmain-flx">

                <div className="covered-flx">
                    <div className="covered-header">{props.data.Covered.heading}</div>
                    <div className="coveredlists">
                        {props.data.Covered.Lists.map(e=>(
                            <ul>
                               <li className="colorlists">{e.list}</li>
                          </ul>
                        ))}     
                    </div>
                </div>

                <div className="covered-flxx">
                    <div className="covered-header">{props.data.Notcovered.heading}</div>
                    <div className="coveredlists">
                        {props.data.Notcovered.Lists.map(e=>(
                            <ul>
                               <li className="colorlists">{e.list}</li>
                          </ul>
                        ))}     
                    </div>
                </div>
            </div>
            
        </div>
    )
}
