import React, {useState} from 'react'

export default function HeadSection(props) {

  return (
    <>
      <div className="headOut_AbtUs_Cls">
        <div className="mainHeadClsAbtUs">
          {props.data.mainHead}
        </div>
        <div className="subHeadClsAbtUs">
          {props.data.subHead}
        </div>
      </div>
    </>
  )
}
