import React from 'react'

export default function Card_Title(props) {
    return (
        <div align="center" style={{marginTop: '40px'}}>
            <div className="selected-dic"><span style={{color:'rgba(9, 28, 63, 0.5)',fontFamily: 'Roboto'}}>Home/ </span> {props.data.Title.selected}</div>
            <div className="title-card">{props.data.Title.title}</div>
        </div>
    )
}
