import React from 'react'
import './Pending.scss';
import Navigation from '../../components/Common/Navigation/Navigation'
import Button from '../../Input-controls/Button/Button';
import { urls } from '../../routes/routes';

export default function Failed(props) {
    return (
        <div>
            <Navigation hideBack={true}/>
            <div className="failed-wrp">
                <img alt="pending" src="https://res.cloudinary.com/onsurity/image/upload/v1598001574/New-home-screen/paymntPndImg_xalnzm.svg" className="pending-img" />

                <div className="fail-content-cls">
                    <div className="pending-txtt">Payment Processing</div>
                    <div className="subb-txt">We will notify you when the payment is complete. In case of a failed payment, you will receive a payment link on your registered email ID.</div>
                    <div style={{marginTop:'30px'}}>
                        <Button onClick={()=>{
                            props.history.push(urls.HOME);
                        }}
                        value="Go Home"/>
                    </div>
                </div>
            </div>
        </div>
    )
}
