import React from 'react'
import './planInfo.scss';

export default function PlanInfo(props) {
    return (
        <div>
            {console.log(props.provider,'345324532')}
            <div className="global-font-cls mainHeading_max" style={{textDecoration: 'underline'}} > Details of the {props.data.name} Plan</div>
                {props.data.ui.features_purchase_web.map(e=>(
                    <div className="contentTxt">
                        <div className="pln-title">{e.title}</div>
                        <div className="pln-description">{e.content}</div>
                        {/* {e.features.map(e=>(
                            <>
                            <div className="pln-description">{e.title}</div>
                            <ui className="ejfheuf">
                            {
                                e.points.map(r=>(
                                    <li>{r}</li>
                                ))
                            }
                            </ui>
                            </>
                        ))}  */}
                    </div>
            
                ))}
                {console.log(props.provider)}
                {!props.HideProvider && props.provider &&
                 <div><img className="lazyload provider-icon" src={props.provider && props.provider[0].provider_logo} alt={props.provider && props.provider[0].provider}/></div>}
                
        </div>
    )
}
