import React, {useState} from 'react'

export default function InvestSection(props) {

  return (
    <>
      <div className="investOut_AbtUs_Cls abtwrp">
        <div className="investMainHeadClsAbtUs">
          {props.data.mainHead}
        </div>
        <div className="investSubHeadClsAbtUs">
          {props.data.subHead}
        </div>
        <div className="imgClntOutAbtUsCls">
          {props.data.logos.map((lobj,ind)=>(
            <img key={"imgAbtUsInvstSec"+ind} className={"imgAbtUsInvstSec imgAbtUsInvstCurrSec_"+ind} src={lobj.image} alt={lobj.Alt} />
          ))}
        </div>
      </div>
    </>
  )
}
