import React, {useState} from 'react'

export default function AbtUsSection(props) {

  return (
    <>
      <div className="AbtUsSec_AbtUs_Cls">
        <div className="SecndColAbtUsSec cmmonPadinLftAbtUsParent">
          <div className="AbtUsMainClsTxt">
            {props.data.mainHead}
          </div>
          <div className="AbtUsSubHadClsTxt">
            {props.data.subHead}
          </div>
        </div>
        <div className="firstColAbtUsSec cmmonPadinRightAbtUsParent">
          <img className="abtUsSecImgCls" src={props.data.imageSec} alt="About Onsurity – An employee healthcare platform for Startups and MSMEs " />
        </div>
      </div>
    </>
  )
}
