import React, {useState, useEffect} from 'react'
import './AboutUsSkeleton.scss';
// import Payment from '../Payment/Components/Payment/Payment';
import { urls } from '../../routes/routes';
import Navigation from '../../components/Common/Navigation/Navigation';
import {data} from './AboutUs_data.js';

import HeadSection from './Components/HeadSection';
import AbtUsSection from './Components/AbtUsSection';
import VisnMisnSection from './Components/VisnMisnSection';
import VisnMisnSection_Mob from './Components/VisnMisnSection_Mob';
import FounderInvestSection from './Components/FounderInvestSection';
import InvestSection from './Components/InvestSection';
import FooterComponent from '../../components/Common/Footer/footer';
import Header from '../../components/Home/HeaderSection/Header';
import { navigation } from '../../utils/Env';
import {actions as stateActions} from '../../redux/ducks//state_change'
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import {Helmet} from "react-helmet";

function AboutUsSkeleton(props) {

  useEffect(() => {
    props.setNavSelected(navigation.ABOUT)
  }, []) 

  const[checkHead,setCheckHead]=useState(true);

  useEffect(()=>{
    const {pathname}=window.location;
    if(pathname==urls.ABOUT_US_MOBILE){
          setCheckHead(false)
    }
  },[])

    return (
        <>
        <div style={{backgroundColor: '#FFF',width: '100%'}}>
          {checkHead &&<Header hideGetApp={localStorage.getItem("LG")==1 && true}/>}
             <div className="parentOut_AbtUs_Cls">
                <HeadSection data={data.heading}/>
                <AbtUsSection data={data.firstSection}/>
                <div className="webview">
                  <VisnMisnSection vission={data.vision} mission={data.mission} image={data.missonVissionImg}/>
                </div>
                <div className="mobview">
                  <VisnMisnSection_Mob vission={data.vision} mission={data.mission} imageSection={data.mobileMissonVissionSections}/>
                </div>
                <FounderInvestSection founderData={data.founderDetails} investData={data.invester}/>
                <InvestSection data={data.invester}/>
                <FooterComponent />
            </div>
        </div>
     </>
    )
}


const mapDispatchToProps = (dispatch) => ({
  ...bindActionCreators({
      ...stateActions,
      //ADD IF ITS FROM DIFFRENT ACTION FILE
  }, dispatch)
});


export default withRouter(connect(null, mapDispatchToProps)(AboutUsSkeleton))