import React from 'react'
import './InputSection.scss';
import { useState } from 'react';
//import { toast } from 'react-toastify';
//import { Modal, Button, Input } from 'semantic-ui-react';
import Popup from '../../../../components/Common/ConfirmationPopUp/Popup';
import { ClevertapUserProfile } from '../../../../utils/Utils';

export default function InputSection() {

    const [state, setstate] = useState({
        name:"",
        validname:"",
        email:"",
        validemail:"",
        phone:"",
        validphone:"",
        company:"",
        validcompany:""
    })

    const HandleChange=(e)=>{
        const{name, value}=e.target;
        setstate({...state, [name]:value, [`valid${[name]}`]:true})
    }


    const[showPopAuth, setShowPopAuth]=useState(false)

    const toggleAuth=()=>{
        setShowPopAuth(!showPopAuth)
    }



    const HandleOkay=()=>{
        setShowPopAuth(false)
          setTimeout(() => {
        window.location.href="https://onsurity.com/"
         }, 1000);
    }
    const getGenericNum = () => {
      let randomNum = parseInt(Math.random()*1000);
      let currentTm = Date.now();
      return randomNum + currentTm;
    }

    const modelAuth=(
        {/*<Modal open={showPopAuth} size={'tiny'} onClose={()=>toggleAuth()}  >
         <Modal.Header>Success</Modal.Header>
                <Modal.Content>
                   <div className="mt-3 mb-3">
                        <div className="text-center">
                            <div>Thank you for submitting the details. We will get back to you soon!</div>
                            <img alt="" src={"https://tracking.geoadmedia.com/aff_l?offer_id=1531&adv_sub="+getGenericNum()} width="1" height="1" />
                            <button className="submit-btn mt-5" onClick={()=>HandleOkay()}>Okay</button>
                        </div>

                   </div>
                </Modal.Content>
        </Modal>*/}

     )

    const HandleSubmit=()=>{
        const{name, email, phone,company}=state;
        var lerr = false;

        if((!/^[a-zA-Z ]{2,100}$/.test(name))){
            setstate({...state, validname:false})
            lerr=true
        }else if(phone.trim()== "" || !/^\d{10}$/.test(phone)){
            setstate({...state, validphone:false})
                    lerr=true
        }else if(email.trim() == "" || !/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(email)){

            setstate({...state, validemail:false})
                    lerr=true
        }
        else if(company.trim() == ""){
          setstate({...state, validcompany:false})
                  lerr=true
        }
        else {
                if(lerr==false){
                    if(window.clevertap){ 
                        window.clevertap.event.push("UserRequest", {
                            "name": name,
                            "Email": email,
                            "Phone": phone,
                            "Company_Name": company
                        });
                        window.clevertap.onUserLogin.push({
                            "Site": {
                                "Name": name,
                                "Identity": phone,
                                "Email": email.toLowerCase(),
                                "Phone": "+91"+phone, 
                                "Company_Name": company 
                            }
                           });

                            setShowPopAuth(true)
                            setstate({...state, name:'', email:'', phone:'', company:''})


                    }
                }
        }
    }

    return (
        <>
        {/*modelAuth*/}
        <Popup show={showPopAuth} showImg={true}
          data={{title:"success", content:"Thank you for submitting the details. We will get back to you soon!"}}
          Close={()=>setShowPopAuth(false)}/>

        <div className="com-section">
            <div className="img-section">
                 <div className="addtoRight">
                    <img src="https://res.cloudinary.com/onsurity/image/upload/v1592627616/New-home-screen/OBJECTS_sounsl.png" className="grp-img" />
                    <div className="header-div">TeamSure, your one-stop for employee healthcare</div>
                    <div className="subhead-divv">A comprehensive, monthly healthcare membership with <span style={{color:'#1987FB'}}>Group Health Insurance</span></div>
                 </div>
            </div>

            <div className="input-section">
                <div className="qoute-div">
                    <div className="quote-txt">Get a personalized quote</div>

                    <div className="wrp-clss">
                         <div className="label-name">Your Name</div>
                    <div className="outer-divv">
                        <input type="text" name="name" value={state.name} onChange={(e)=>HandleChange(e)} placeholder="Name" className="name-input" />
                        {state.validname===false &&<div className="erri">Please enter valid name</div> }
                     </div>
                    </div>

                    <div className="wrp-clss">
                          <div className="label-name">Mobile Number</div>
                    <div className="outer-divv">
                        <input type="number" name="phone" value={state.phone} onChange={(e)=>HandleChange(e)} placeholder="Mobile number" className="name-input" />
                        {state.validphone===false &&<div className="erri">Please enter valid phone</div> }

                     </div>
                    </div>

                    <div className="wrp-clss">
                    <div className="label-name">Email ID</div>
                    <div className="outer-divv">
                        <input type="text" name="email" value={state.email} onChange={(e)=>HandleChange(e)} placeholder="Email address" className="name-input" />
                        {state.validemail===false &&<div className="erri">Please enter valid email</div> }

                     </div>
                    </div>

                    <div className="wrp-clss">
                         <div className="label-name">Company Name</div>
                    <div className="outer-divv">
                        <input type="text" name="company" value={state.company} onChange={(e)=>HandleChange(e)}
                          placeholder="Company Name" className="name-input" />
                        {state.validcompany===false &&<div className="erri">Please enter valid company name </div> }
                     </div>
                    </div>

                    <div style={{textAlign: 'center',marginTop: '30px',marginBottom: '20px'}}>
                       <button className="submit-btn" onClick={()=>HandleSubmit()}>Submit</button>
                    </div>
                </div>
            </div>
        </div>
        </>
    )
}
