import React from 'react'
import './Request.scss';

export default function Request(props) {
  const openForm = () => {
    if (window.clevertap) {
      window.clevertap.event.push("RequestDemo", {
        "clicked": "clicked request demo"
      });
    }
    //window.location.assign('https://forms.office.com/Pages/ResponsePage.aspx?id=nNUjj-DjbUu6h3mc5yJrIutCRhJ6XQpFnquRhIrlh3dUOUhPSDYxMDZTTjBSR0ROV0kxVktKVUtQRy4u');
    window.open('https://forms.office.com/Pages/ResponsePage.aspx?id=nNUjj-DjbUu6h3mc5yJrIutCRhJ6XQpFnquRhIrlh3dUOUhPSDYxMDZTTjBSR0ROV0kxVktKVUtQRy4u', '_blank');
  }
  const openSite = () => {
    if (window.clevertap) {
      window.clevertap.event.push("VisitWebsite", {
        "clicked": "clicked on visit website"
      });
    }
    //window.location.assign('https://onsurity.com/');
    window.open('https://onsurity.com/', '_blank');
  }
    return (
        <div className="demodiv" style={{backgroundColor:'#F5F9FF'}}>
            <div className="demoflexx my-4">
                <div className="infodiv">
                    <div className="mainHeading_max">Access healthcare for your employees at just Rs. 145</div>
                </div>
                <div className="btnflexx">
                        {/*<div class="mobtext">vitae lobortis eget tellus. Netus malesuada nulla risus sapien vitae vestibulum.</div>*/}
                        <button className="startedbtn" onClick={openSite}>Get Started</button>

                </div>
            </div>

        </div>
    )
}
