import React from 'react'
import './BenifitsHowItWorks.scss'

export default function BenifitsHowItWorks(props) {
    return (
        <div style={{backgroundColor:props.background}} className="MainSec">
            <div className="SectionHead" >{props.Heading}</div>

            <div className="my-4 text-center">
                <div className={`text-center ${props.pattern==1?'flexContent':'flexContent2'}`}>
                    {props.data.map(e=>(
                        <>
                        {props.pattern==1?
                        <div className="mx-auto">
                            <div className="CardBlock mx-1">
                                <img src={e.img} className="my-5" height="40px"/>
                                <div className="mx-4 text-center" style={{color:'rgba(9, 28, 63, 0.8)'}}>{e.description}</div>
                            </div>
                        </div>
                        :
                        <div className="mx-auto">
                            <div className="mx-1 Card2Block">
                                <img src={e.img} className="mt-0" height="110px"/>
                                <div className="mb-4" style={{fontWeight:'500'}}>{e.description}</div>
                            </div>
                        </div>
                        }
                        </>
                    ))}
                </div>
            </div>
        </div>
    )
}
