import React from 'react'
import './Input.scss';

export default function Input(props) {
    return (
        <div className={`${props.className}`}>
            <div className="LableFloat d-flex mx-auto" style={{ width: props.width}}>
                {'label' in props && <div className=" company-label">{props.label}
                </div>}

                {'help' in props && <div className="gstin-label">{props.help}<img onClick={props.HelpClicked} src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1592211087/New-home-screen/QuestionMarkIcon_yiqvtp.svg" className="qutn-mark-crcl lazyload" alt="questionmark" /></div>}
            </div>

            <input 
               className={`inputtypee ${props.err && 'errorInput'}`}
               name={props.name}
               style={props.style}
               style={{width:props.width, textAlign:props.align}}
               type={props.type}
               value={props.value}
               placeholder={props.placeholder}
               autoComplete={props.autoComplete ? props.autoComplete : "on"}
               onKeyDown={props.keyDownFunction === "true" ? (e)=>props.onKeyDown(e) : null}
               disabled={props.disabled ? props.disabled : ""}
               onChange={(e)=>props.onChange(e)}/>
            {props.err &&
                <div className="error_input_text" style={{textAlign:`${props.errAlign && props.errAlign}`, width:props.width}}>{props.err}</div>
            }
        </div>
    )
}
