import React from 'react'
import './CheckBox.scss'

export default function CheckBox(props) {

    console.log("+++++++++================",props.checked)
    return (
            <div className="checkk-cls">
             <div>
                <label class="contain">
                     <input type="checkbox" checked={props.checked && "checked"} onClick={props.onClick}/>
                     <div class="checkmark-cmn-cls"></div>
                     {/* <span className="chck-updates">Get updates on WhatsApp</span> */}
                </label>
            </div>
                  
                  {/* <div className="chckbx-content">By verifying OTP, you agree to our Terms & Conditions and Privacy Policy.</div> */}
             </div>
    )
}