import React from 'react'
import './Total.scss';
import { urls } from '../../../../../routes/routes';
import { withRouter } from 'react-router-dom';
import { numberformat } from '../../../../PlanPurchase/util';

 function Total(props) {
    return (
        <div className="pay-total-mob">
            <div onClick={()=>{props.history.push(urls.SUMMARY_MOB)}} className="view-details-div">View Details</div>
            
            <div className="total-txtdiv">Grand Total</div>
            <div className="total-rupeediv">₹ {numberformat.format(props.price)}/-</div>
        </div>
    ) 
}

export default withRouter(Total)