import React from 'react'
import './Navigation.scss'
import { withRouter } from 'react-router-dom'
import { urls } from '../../../routes/routes';
import { navigation } from '../../../utils/Env';
import {actions as user_state} from '../../../redux/ducks/state_change'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

function Navigation(props) {
    return (
        <>
        <div style={{marginTop:'50px'}}>
        <div className="NavInnerHeader">
            <div style={{width:'30%', marginLeft:'23px'}} className="my-auto ">
                <div className="BackBlock" onClick={()=> props.history.push(props.backPath?props.backPath:"/")}>
                {!props.hideBack &&
                <>
                        <img height="28px" src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1592369462/New-home-screen/Group_723_c1pny0.png" alt="back" />

                        <div className="ml-1" style={{paddingTop:'5px', color:'#091C3F'}}>Back</div>
                    </>}
                </div>
            </div>
            <div style={{width:'40%'}} className="my-auto text-center">
                <img height="25px" alt="img" style={{cursor: 'pointer'}} src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1595591334/New-home-screen/Group_1328_bbme78.svg" alt="navigation"
                   onClick={()=>{
                    props.setNavSelected(navigation.HOME);
                    props.history.push("/");
                }} />
            </div>
            <div style={{width:'30%' , marginRight:'25px'}} className="my-auto text-right">
                <span style={{ cursor:'pointer'}} onClick={()=>{
                    props.setNavSelected(navigation.FAQ)
                    window.open( urls.FAQ, "_blank");
                }
                } >FAQ</span>
            </div>
            {/* {console.log(props)} */}
        </div>
        <div className="LoaderHeader" style={{ width:props.loaded?props.loaded:'0%', height:!props.loaded || props.loaded=="0%"?'0px':'3px'}}>

        </div>
        </div>
        </>
    )
}


const mapStateToProps = (state) => ({

  })

const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...user_state,
        //ADD IF ITS FROM DIFFRENT ACTION FILE
    }, dispatch)
  });


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Navigation))
