import React, { useState, useEffect } from 'react'
import './Coverage.scss'
import Navigation from '../../components/Common/Navigation/Navigation'
import { urls } from '../../routes/routes'
import CoverageItems from './Components/CoverageItems/CoverageItems'
import ItemsSelected from './Components/ItemsSelected/ItemsSelected'
import ItemSelectMob from './Components/ItemsSelected/ItemSelected-Mobile/ItemSelectMob'
import CoverageProceed from './Components/ItemsSelected/CoverageProceed/CoverageProceed'
import { actions as coveragesActions } from '../../redux/ducks/coverages'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { actions as actionsUser } from '../../redux/ducks/state_change'

function Coverage(props) {

    const[singleSelect, setSingleSelect]=useState(false)

    useEffect(() => {
        props.getCoverages()
    }, [])

    useEffect(() => {
        if(props.coverages && props.coverages.data && props.coverages.data.success==1){
            const {coverages}=props.coverages.data.data;
            
            if(coverages){
                console.log(coverages)
                setCoverages(coverages)
                
                let defaultCoverages=[]
                coverages.map(e=>{
                    if(e.mandatory==true){ 
                        defaultCoverages.push(e._id)
                    }
                })
                props.setDefaultCoverages([...defaultCoverages])

            }else{
                alert("error")
            }
        }
    }, [props.coverages])

    let[coverages, setCoverages]=useState([])
     
    return (
        coverages.length>0 &&
        <div>
            <Navigation loaded={"90%"} backPath={urls.COMPANY_DETAILS}/>
            <div className="CovidSkliton">
                <div className="CoverageItem1">
                    <div > 
                        <CoverageItems data={coverages} singleSelect={singleSelect}/>
                    </div>
                </div>
                <div className="CoverageItem2">
                    <div>
                        <ItemsSelected data={coverages}/>
                    </div>
                </div>

                <div className="CoverageItem-Mob">
                    <div>
                    <ItemSelectMob data={coverages}/>
                    </div>
                    <div className="CovrageMon">
                        <CoverageProceed/>
                    </div>
                </div>
               
                </div>
            </div>

    )
}

const mapStateToProps = (state) => ({
    coverages:state.coverages,
    coveragesArray:state.userstate.coverages
  })
  
  const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...coveragesActions,
        ...actionsUser
    }, dispatch)
  });
  
  
  export default connect(mapStateToProps, mapDispatchToProps)(Coverage)