import React, {useEffect, useState} from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { urls } from '../../../routes/routes'
import './BannerBottom.css'

function BannerBottom(props) {

    const [show, setshow] = useState(false)

    useEffect(() => {
        if(sessionStorage.getItem('bottomBanner')==undefined){
            setTimeout(()=>{
                setshow(true)
            },5000)
        }
    }, [])


    const HandleClick=()=>{
        if(props.auth.authStatus===true){
            props.history.push(urls.GSTIN_DETAILS)
        }else{
            props.history.push(urls.VERIFY_PHONE)
        }
    }

    const HandleClose=()=>{
        setshow(false)
        sessionStorage.setItem('bottomBanner','1')
    }

    return (
        <div className={`BottomSheet ${show && "BottomSheetShow"}`}>
            <div className="wrapbanner">
                <div className="BannerText">
                    Confused about choosing an affordable employee healthcare plan? 
                </div>
                <div style={{marginTop:'auto', marginBottom:'auto'}}>
                    <button className="btnGetstatredBanner" onClick={()=>HandleClick()}>Talk to Experts</button>
                </div>

                <div className="closeBanneridc">
                    <img onClick={()=>HandleClose()} style={{cursor:'pointer'}} src="https://res.cloudinary.com/onsurity/image/upload/v1602587131/employer%20dashboard/avatars/Group_6541_cosuxm.svg" className="closeBanner" />
                </div>
            </div>
        </div>
    )
}

const mapStateToProps = (state) => ({
    auth:state.navigationStack.authObj
  })

export default withRouter(connect(mapStateToProps, null)(BannerBottom))