import React, { useState, useEffect } from 'react'
import './Payment.scss'
import Radio from '../../../../Input-controls/Radio/Radio'
import { Clevertap } from '../../../../utils/Utils'
// import Input from '../../../../Input-controls/Input/Input'
import {actions as cartActions} from './../../../../redux/ducks/cart'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import PaymentWork from '../../../CheckOut/Components/Payment-Work/PaymentWork'
import BillingWork from '../BillingWork/BillingWork'

function Payment(props) {


    const[selectedPayment, setSelectedPayment]=useState()

    const[selectedManualPayment, setSelectedManualPayment]=useState()

    useEffect(() => {
        setSelectedManualPayment(props.SelectedManualPayment)
    }, [props.SelectedManualPayment])

    // const onTabOutFn = (e) => {
    //   if (e.key == 'Tab' && e.shiftKey) {

    //   }
    //   else if(e.key == "Tab")
    //   {
    //     e.preventDefault();
    //     document.getElementsByClassName('code-cls')[0].focus();
    //   }
    // }


    const DefaultSelect=()=>{
        if(props.paymentOption){
            let arr=props.paymentOption
            let recommendedObj=arr.find(e=>e.recommended==1)
            props.currentSelectedOpt(recommendedObj && recommendedObj.option_value);
            setSelectedPayment(recommendedObj && recommendedObj.option_value);
            if(recommendedObj){
              Clevertap("Payment_Selection",{clickedValue:recommendedObj.option_value});
            }
        }
    }


    useEffect(() => {
        DefaultSelect()
        // eslint-disable-next-line
    }, [props.paymentOption])

    const selectCurrOption = (pval) => {
        // console.log(selectedPayment)
      props.currentSelectedOpt(pval);
      setSelectedPayment(pval);
    }

    let selected= props.paymentModes.find(e=>e.value==props.selectedMethod);

    const HandleDuration=(duration, value)=>{
        if(selectedManualPayment != duration){
            setSelectedManualPayment(duration)
            // props.setCart("ES", {duration:duration});
            props.setCart("ES", {
                "purchase_options": {
                    "duration": duration,
                    "pay_mode": value
                }
            });
        }

    }

   const staticPricing={
    "payment_text": {
        "how_payment_works": [{
            "main": "The initial subscription amount is calculated for 365 days depending on the plan type, number of team members and their estimated age provided to us at the time of subscription to the plan.",
            "subs": [],
        }, {
            "main": "In case there is any new joiner or member exiting the company during the annual cycle or in the case of incorrect details provided (incorrect number of actual employees, different age-distribution of employee etc.), then: ",
            "subs": ["We compute the charge on a daily basis which is calculated on the actual number of the team members, their actual age, their plan type and the number of days for which they were activated on our platform. We will then charge the adjustment payment till the end of 365 days from the plan subscription date when the initial subscription payment is about to get exhausted. This adjustment payment could also be in negative if lesser employees were activated than the estimated number of employees provided to us or age-mix distribution was different than provided (more on younger side).", "For example, if at the time of initial subscription, there are 10 employees and all are in age-band of 18-35, then we shall be charging an initial subscription price of INR 5400 plus applicable GST (INR 540X10 employees). We consider this subscription amount as the deposit amount and if there are any joiners post, we will be charging the additional amount from the initial subscription amount basis the daily utilization. When the amount starts to get exhausted, we will be collecting the additional amount."]
        }],
        "how_payment_works_heading": "How our billing works?"
    }
   }


    return (
        <div className="mb-4 payment_component">
          <div className="m-3">
              {props.banners.length>0 && props.banners[0].visible &&
            <div className="img-clss">
                <img width="100%" src={props.banners[0].link} alt="group" />
            </div>}
            <div className="payment-typetxt my-4">
                <div className="pay_type_1 my-auto">
                    Payment Type
                </div>
                <div className="pay_type_2">
                    <div className="pay_type_btn" style={{width:props.paymentModes.length==1 && "80px"}}>
                        {props.paymentModes.length>1?
                            props.paymentModes.map(e=>(
                            <div className={`pay_type_btn_two ${props.selectedMethod==e.value && "payment_selected_div"}`} onClick={()=>{

                                Clevertap("Payment_Method",{clickedValue:e.value});
                                props.setSelectedMethod(e.value)
                                DefaultSelect()
                                    props.setCart("ES", {
                                        "purchase_options": {
                                            "duration": e.purchase_options.length==0 ? 0 : e.purchase_options[0].duration,
                                            "pay_mode": e.value
                                        }
                                    });
                                    if(e.purchase_options.length==0){
                                        setSelectedManualPayment("")
                                    }else{
                                     setSelectedManualPayment(e.purchase_options[0].duration)
                                    //  console.log("====",selected.purchase_options)
                                    }
                              }
                            }>{e.name}</div>
                        ))
                    :
                    <div className={`pay_type_btn_two_one`} >{props.paymentModes[0] && props.paymentModes[0].name}</div>
                    }
                        {/* <div className={`pay_type_btn_two ${props.selectedMethod=='auto' && "payment_selected_div"}`} onClick={()=>props.setSelectedMethod('auto')}>Auto</div>
                        <div className={`pay_type_btn_two ${props.selectedMethod=='manual' && "payment_selected_div"}`}  onClick={()=>props.setSelectedMethod('manual')}>Manual</div> */}
                    </div>
                </div>
            </div>
            {/* {console.log("5555555555555555555555",selected)} */}
            {selected &&
            <>
            <div className="d-flex mb-1">
                <div className="monthly_auto_payment">
                    {selected.title}
                </div>
                <div className="text-right secure_server">
                    {/* Secure Server */}
                </div>
            </div>
            <div className="suBheadingPayment mb-4">
                {selected.description}
            </div>
            </>}

            {selected && "purchase_options" in selected && selected.purchase_options.length>0 &&
            <div className="auto-payment-sec mb-4">
                {/* {console.log(selectedManualPayment, selected.purchase_options)} */}
                {/* initial card */}

                {selected.purchase_options.map(e=>(
                    <>
                    {console.log("!!!!!!!!!!!!!!!!!!!",selectedManualPayment)}
                    <div className={`month_payment_dis ${selectedManualPayment==e.duration && "month_payment_selected"}`} onClick={()=>HandleDuration(e.duration, selected.value)}>

                        {selectedManualPayment==e.duration &&
                        <div class="SideCheck">
                            <span class="checkmark_bss">
                                <div class="checkmark_stem"></div>
                                <div class="checkmark_kick"></div>
                            </span>
                        </div>}
                        <div style={{paddingTop:'40px'}}>
                            {/* <div className="percent_discount">{e.discount}</div> */}
                            <div>
                                <span style={{fontWeight:'600'}}>{e.duration}</span> months
                            </div>
                        </div>
                    </div>
                    </>
                ))}
            </div>}
            {/* {console.log("567re654567567",props.selectedMethod)} */}
            <div className="payment_main_div">
                {props.paymentOption.map(e=>(
                    <>
                    {e.modes.includes(props.selectedMethod) &&
                    <div className={`payment_inner_div px-2 py-4 `}>
                    {/*<div className={`payment_inner_div px-2 py-4 ${selectedPayment==e.id && "expand_payment_selected"}`}>*/}
                        <div className="inner_pay_card">
                            <div className="text-center my-auto saassww">
                                <Radio
                                    for={"Payment"}
                                    onClick={(val)=>{Clevertap("Payment_Option",{paymentOption:val}); selectCurrOption(val)}}
                                    value={e.option_value}
                                    selected={selectedPayment}
                                    />
                            </div>
                            <div className="" style={{width:'70%'}}>
                                <div className="paymentHeading">{e.option_name}</div>
                                <div className="paymentSubHeaing mt-1">{e.desc[props.selectedMethod]}</div>
                            </div>
                            <div className="my-auto">
                                {'cardsAccepted' in e &&
                                <div className="d-flex float-right pr-3">
                                    {/* {console.log("(((((((",e.cardsAccepted[props.selectedMethod])} */}
                                    {e.cardsAccepted && e.cardsAccepted[props.selectedMethod] && e.cardsAccepted[props.selectedMethod].map(i=>(
                                        <div>
                                            <img className="mx-1" height="25px" src={i} alt="payment" />
                                        </div>
                                    ))}
                                </div>}
                            </div>
                        </div>
                        {/* <div className="hidden_payment_section">
                            <div>
                                <hr/>
                            </div>
                            <div className="payment-card-type">
                                <div className="card-num-type mr-2">
                                    <Input
                                        type="number"
                                        label="Card Number"
                                        // value={otpValue}
                                        // width='100px'
                                        // onChange={(e)=>chnageOTPFn(e)}
                                        placeholder="0000 0000 0000 0000"/>
                                </div>
                                <div   className=" card-monthyer-type mx-4 ">
                                    <Input
                                        type="text"
                                        label="Expiry Date"
                                        // value={otpValue}
                                        // width='100px'
                                        // onChange={(e)=>chnageOTPFn(e)}
                                        placeholder="MM/YY"/>
                                </div>
                                <div style={{width:'70px'}}>
                                    <Input
                                        type="number"
                                        label="CVV"
                                        help=""
                                        // value={otpValue}
                                        // width='100px'
                                        // onChange={(e)=>chnageOTPFn(e)}
                                        placeholder="0000"/>
                                </div>
                            </div>
                            <div style={{width:'170px'}} className="mr-2 mt-2">
                                <Input
                                    type="number"
                                    label="Name of card"
                                    keyDownFunction="true"
                                    onKeyDown={(e)=>onTabOutFn(e)}
                                    // value={otpValue}
                                    // width='100px'
                                    // onChange={(e)=>chnageOTPFn(e)}
                                    placeholder="Name of card"/>
                            </div>
                        </div> */}
                    </div>} 
                    </>
                ))}
            </div>
            {props.selectedMethod=="si_order" &&
            <div className="OthrPaymntsPdf">
                {/* <div>View all supported banks</div> */}
                <div className="LinkMoreDetails" onClick={()=>window.open("https://healthsure.s3.ap-south-1.amazonaws.com/PayU+List+of+Banks+for+Auto+Debit.pdf", '_blank')}>
                View all supported banks
                </div>
            </div>}
             <div className="wedfefef">
                    {/* <PaymentWork TotalPrice={200}  staticPricing={props.billingText.find((item) =>item.mode == props.selectedMethod)} /> */}
                    <BillingWork type={props.selectedMethod}/>
             </div>
          </div>
        </div>
    )
}

const mapStateToProps = (state) => ({
    // cart:state.cart,
    // userData:state.userstate.planSelected,
    // pricing:state.static_pricing
  })

  const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...cartActions,
    }, dispatch)
  });


  export default connect(mapStateToProps, mapDispatchToProps)(Payment)
