import React,{useState,useEffect} from 'react';
import './Slide_Purchase.css'

function Slide4(props) {

	const [clsNam,setClsNam] = useState("");
   const [imag,setImg] = useState([]);
   const [classCheck, setClassCheck] = useState("");
	 const [idIntrval, setIdIntrval] = useState("");
	  var ind = 0;
	 useEffect(()=>{
		 if(props.data.length > 0)
		 {
			setImg(props.data);
		 }
		 if(imag.length > 0)
		 {
			if(props.blur)
			 {
				 setClsNam(" mySlides custWid80 w3-animate-fading ");
				 setIdIntrval("carousel");
				carousel();
			 }
			 if(!!props.slideDirection && props.slideDirection !== "")
			 {
				 setClsNam(" mySlides custWid80 w3-animate-"+props.slideDirection);
				 setIdIntrval("carousel");
				carousel();
			 }
		 }
		 if(props.fixedContent === false)
		 {
			setClassCheck(" dispFlx notFxdCls adjstSizeCls_sld");
		 }
		 else {
			 setClassCheck(" dispFlxCont fxdCls");
		 }
		 // eslint-disable-next-line
	 },[...props.data]);

	 useEffect(()=>{
		 if(imag.length > 0)
		 {
			if(props.blur)
			 {
				 setClsNam(" mySlides custWid80 w3-animate-fading ");
				 setIdIntrval("carousel");
				carousel();
			 }
			 if(!!props.slideDirection && props.slideDirection !== "")
			 {
				 setClsNam(" mySlides custWid80 w3-animate-"+props.slideDirection);
				 setIdIntrval("carousel");
				carousel();
			 }
		 }
		 // eslint-disable-next-line
	},[imag.length === props.data.length]);

	useEffect(()=>{

		 const id = setInterval(carousel, props.timeDurationSlide);
		return () => {

	    clearInterval(id);
	  }
	},[idIntrval])

	const handleCursolPosition = (pindex) => {
		// console.log("clicked in position "+pindex);
		ind = pindex;
		carousel("fromClicked");
	};

	 const carousel = (pfalg) => {
		 let i;
		 let img = [...imag];
		 //let x = document.getElementsByClassName("mySlides");
		for (i = 0; i < img.length; i++) {
			img[i].currentCls = " hideImg hideDots";
		  }
		  //let ind = {...ind};
		  ind++;
		  if (ind > img.length) {ind = 1}
		  img[ind-1].currentCls = " showImg showDots";
		  setImg(img);
		  //setInd(ind);
		  if(pfalg !== "fromClicked") {
				//setTimeout(carousel, props.timeDurationSlide);
		  }
		  //{/*<div className="w3-content w3-section ">*/}

	 }
	  return (

			<div className={[classCheck, "w3-display-container", props.customClass, "slidd","pur_sld_algn_txt"].join(' ')}>

				<div className="wid40Per_old carslPrpty alignCentr put_sld_padbtm">
				{
					props.fixedContent === false ?
					imag.map((imageg,index) => (<div key={props.keyData+"wid40_"+index} className={imageg.currentCls + clsNam}>
						<div className="mainHead_pur_max SlideHead_old" dangerouslySetInnerHTML={{ __html: imageg.head}}></div>
						<p className=" content_sld_pur slideContent_old" dangerouslySetInnerHTML={{ __html: imageg.sub}}></p>
						</div>
				))
				: (<div key={props.keyData+"wid40_"} className="sld_mar_lft">
					<p className=" mainHeading_max SlideHead_old">{props.content.Head}</p>
					<p className=" contentTxt slideContent_old sld_hid_cls">{props.content.subcontent}</p></div>
					)
				}
				</div>


				<div className="slderInter w3-center marBtmPrntDts w3-container w3-section w3-large w3-text-white w3-display-bottommiddle custWidth100 curosalDots">
				{
					props.carosalDots === true ?
					imag.map((imageg,index) => (
						<span key={props.keyData+"spanSlider_"+index}className={" w3-badge demo w3-border w3-transparent1 bg_wht_pur_dt w3-hover-white curslDotCls "+imageg.currentCls} onClick={() => handleCursolPosition(index)}></span>))
					: null
					}
					</div>

			</div>

	  )

}

export default Slide4;
