import React from 'react'
import './Styles.scss';

export default function Benefits_of_ordereing(props) {
    return (
        <div className="benefits-flx discount">
            <div className="feature-image ">
                

                <div className="benimage"><img src={props.data.Ordering.benImage} className="benimage-cls" /></div>
                <div className="bene-ordreing-header">{props.data.Ordering.heading}</div>
            </div>

            <div className="listsflx">
                <img src={props.data.Ordering.orderImage} className="orderimg" />
            </div>
        </div>
    )
}
