import React, { useState } from 'react'
import './EmpCount.scss';
import MembersMoreInfo from '../../../Informations/membersMoreInfo';
import InfoBox from '../../../../components/Common/InfoBox/InfoBox';

export default function EmpCount(props) {
    const[showInfo, setShowInfo]=useState(false)
    return (
        <>
         <InfoBox showInfo={showInfo} setShowInfo={(bool)=>setShowInfo(bool)}>
            <MembersMoreInfo/>
        </InfoBox>
        <div className="emp-count-cls">
            <div className="wrp-divv">
               <div className="ppl-num-div mt-2">{props.members} Members</div>
               <div alt="questionmark" className="emp-labl-div">Number of team members<img onClick={()=>setShowInfo(true)} src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1592211087/New-home-screen/QuestionMarkIcon_yiqvtp.svg" className="qutn-mark-crcl lazyload" /></div>
            </div>
        </div>
        </>
    )
}
