const mapStateToProps = (state) => ({

})

const mapDispatchToProps = (dispatch) => ({
  ...bindActionCreators({
	  ...user_state,
	  //ADD IF ITS FROM DIFFRENT ACTION FILE
  }, dispatch)
});


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(FooterComponent))
