import React from 'react'
import { LazyImage } from '../../../../utils/LazyImage'

export default function HowItWorks(props) {
    return (
        <div>
            <div className="mx-4">
                <div className="mb-3 mx-2 text-center">
                    <LazyImage alt={props.data.Alt} height="150px" src={props.data.Image} className="lazyload"/>
                </div>
                <div className="OrgCardSubHeading text-center px-4 hw_wrk_upSty_cls">
                    {props.data.Description}
                </div>
            </div>
        </div>
    )
}
