import React,{useState, useEffect} from 'react';
import Input from '../../../Input-controls/Input/Input';
import InfoBox from '../../../components/Common/InfoBox/InfoBox';
import { actions as stateChangeActions} from '../../../redux/ducks/state_change';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import TeamMemberInfo from '../../Informations/TeamMemberInfo';
import TeamMemberFamilyInfo from '../../Informations/TeamMemberFamilyInfo';

function AddPplCount_Mob(props) {

  const[showInfo, setShowInfo]=useState(false);
  const[selectedHelp, setSelectedHelp]=useState("");

  // useEffect(()=> {
  //   contentArr.map((obj,ind) => {
  //     if(obj.id == props.benefitType)
  //     {
  //       setContentObj(obj);
  //     }
  //   });
  // },[props.benefitType])

  useEffect(()=>{
    let lObj = props.addPplInptVal;
  },[props.addPplInptVal])

  const chnageTMCnt = (e,pflag,pindex) => {
    //
    let lval = e.target.value;
    if(!isNaN(parseInt(lval)))
    {
      if(parseInt(lval) < 0)
      {
        lval = 0;
      }
      else {
        lval = parseInt(lval);
      }
    }
    else{
      lval = 0;
    }
    //props.setState("addPeople",e.target.name,lval);
    let lobj = {
      ind: pindex,
      flg: pflag,
      val: lval
    };
    props.calculateSumerize(lobj,"addPeople",e.target.name,lval);
  }


  const addInfoTMO = (pVal) => {
    //
    setShowInfo(true);
    setSelectedHelp(pVal);
  }

  return(
    <>
      <div className="adPlOuterCls">

        {
          props.contentObj.body.map((obj,ind)=>(
          <div  className="OuterRowCls">
            <div className="showInLineHead txtCntr">
              {
                props.contentObj.head.length > 0 ? (
                  props.contentObj.head.map((headName,ind)=>(
                    <div key={"adPpl_head_"+ind} className={"headClsIndvtl headPrntDiv"+ind}>
                      <div className={" headClsIndvtl_inner qMIconDivCls"+ind}>
                        <span className={"qMIconSpanCls"+ind}>{headName}</span>
                        {ind == 2 ?
                          (<span className="adPplQM_Icon" onClick={(e)=>addInfoTMO("TMO")}></span>) : null
                        }
                        {ind == 4 ?
                          (<span className="adPplQM_Icon" onClick={(e)=>addInfoTMO("TMF")}></span>) : null
                        }
                      </div>
                    </div>
                  ))
              ) : null
              }
              </div>

            <div className="showInLineBody" key={"outerAdPl_"+ind}>
              <div key={"adPpl_bdy_age"+ind} className="bdyAgeCls bodyClsIndvtl padTp">
                <div className="bodyClsIndvtl_inner my-auto ageTxt">
                  {obj.range} Years
                </div>
              </div>
              <div key={"tm_actual_rs"+ind} className="tmAmtbdyCls bodyClsIndvtl padTp">
                <div className="bodyClsIndvtl_inner my-auto">
                {props.showOrignalPricing &&
                  <span className="actlPriceCls my-auto">₹{obj.pre_discount_price_per_user}</span>}
                  <span className="discountedPriceCls">₹{obj.total_price_per_user}{props.planType}</span>
                </div>
              </div>
              <div key={"tm_discount_rs"+ind} className="bdyCls bodyClsIndvtl fltRght TMOIptOutDiv">
                <div className="bodyClsIndvtl_inner">
                  <Input className="TMOIpt" value={props.addPplInptVal["ipt_bdy_name_tm_"+ind]}
                    name={"ipt_bdy_name_tm_"+ind}  placeholder="00" type="Number"
                    onChange={(e)=>chnageTMCnt(e,"TM",obj.index)}/>
                </div>
              </div>
              {
                "TMF" == props.contentObj.id ? ( <>
                  <div key={"tmf_actual_rs"+ind} className="bodyClsIndvtl tmAmtbdyCls padTp marTpAdpl">
                    <div className="bodyClsIndvtl_inner">
                    {props.showOrignalPricing &&
                      <span className="actlPriceCls">₹{obj.pre_discount_price_per_user_TMF}</span>}
                      <span className="discountedPriceCls">₹{obj.total_price_per_user_TMF}{props.planType}</span>
                    </div>
                  </div>
                  <div key={"tmf_discount_rs"+ind} className="bodyClsIndvtl bdyCls">
                    <div className="bodyClsIndvtl_inner">
                      <Input className="TMOIpt" value={props.addPplInptVal["ipt_bdy_name_tmf_"+ind]}
                        name={"ipt_bdy_name_tmf_"+ind}  placeholder="00" type="Number"
                        onChange={(e)=>chnageTMCnt(e,"TMF",obj.index)}/>
                    </div>
                  </div>
                </>) : null
              }
            </div>
            </div>
          ))
        }
      </div>
      <InfoBox showInfo={showInfo} setShowInfo={(bool)=>setShowInfo(bool)}>
         {selectedHelp=="TMO"?<TeamMemberInfo/>
             :
          selectedHelp=="TMF" && <TeamMemberFamilyInfo/>}
     </InfoBox>
    </>
  )
}

const mapStateToProps = (state) => ({
  addPplInptVal : state.userstate.addPeople
})
const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...stateChangeActions,
        //ADD IF ITS FROM DIFFRENT ACTION FILE
    }, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(AddPplCount_Mob)
