import React from 'react'
import './AgePrice.scss';
import { numberformat } from '../../util';

export default function AgePrice(props) {

    const Capitalize=(string)=>{
        return string[0].toUpperCase()+string.substring(1)
    }

    const WebRange=()=>{
        return ( 
            <div className="age-price-cls WebRange">
                <div className="monthly-prc-cls">{Capitalize(props.rangeSelected.pricing_frequency)} price per Team Member for {props.pricingSelected} Plan</div>
                <div className=" d-flex mt-1">

                <div className="ageband-priceflx">
                  <div className="prps-pricval">Price</div>
                  <div className="prps-pricvall">₹ {numberformat.format(props.rangeSelected.price)}/-</div>
                </div>
                 
                 <div className="age-selct mt-3">Age</div>
                  <div className="d-flex mt-1" style={{display: 'inline-block',flexWrap: 'wrap'}}>
                  {props.data.map((e)=> (
                    <div className={`years-cls ${props.rangeSelected.range===e.range && "selected-range"}`} onClick={()=>props.DemoClicked(e)}>
                       <div className="age-sytl">{e.range} years</div>
                   </div>
                  ))}
                  </div>
                 </div>
            </div>
        )
    }

    const MobileRange=()=>{
        return (
            <div className="MobileRange mb-4">
                <div className="monthly-prc-cls pt-3">{Capitalize(props.rangeSelected.pricing_frequency)} price per Team Member for {props.pricingSelected} Plan</div>
                <div className="price_range_mob">Price ₹ {numberformat.format(props.rangeSelected.price)}/-</div>

                <div className="ranges_mobile mt-md-3">
                    {props.data.map((e)=> (
                        <div className={`years-cls ${props.rangeSelected.range===e.range && "selected-range"}`} onClick={()=>props.DemoClicked(e)}>
                            <div className="age-sytl">{e.range} years</div>
                        </div>
                    ))}
                </div>
            </div>
        )
    }

    return (
        <>
            {/* WEB VIEW */}
            {WebRange()}

            {/* MOBILE VIEW */}
            {MobileRange()}
        </>
    )
}
