import React from 'react'

export default function Terms_conditions(props) {
    return (
        <div>
        <div className="coveredmain-flx termscondi">

            <div className="condition-flx">
            <div style={{width: 'fit-content',margin: 'auto'}}>
            <div className="covered-header">{props.data.Covered.heading}</div>
                <div className="coveredlists">
                    {props.data.Covered.Lists.map(e=>(
                        <ul>
                           <li className="colorlists">{e.list}</li>

                      </ul>
                    ))}     
                </div>
            </div>
               
            </div>

            <div className="covered-flxx">
                <div className="covered-header">{props.data.Notcovered.heading}</div>
                <div className="coveredlists termslist">
                        <ul>
                           <li className="colorlists">Lives between the ages 18 to 59 are only eligible.</li>
                           <li className="colorlists">Medical Questionnaire needs to be filled for life aged above 45 or if where the membership plan has Sum Insured under the membership exceeding INR 5,00,000 </li>
                           <li className="colorlists">One-year suicide waiting period is applicable</li>
                           <li className="colorlists">The Group Term Life under the membership plan will cease at the earliest of    </li>
                        </ul>
                        </div>  
                            <div className="cond-alphaol">
                                  <div className="ollists-terms">a)  Member attaining maximum age of 60 years</div>    
                                  <div className="ollists-terms">b)  Deactivation of the membership </div> 
                                   <div className="ollists-terms">c)  Death during the active membership </div>
                            </div>
                                 
                           
                        
                      
                      
              
            </div>
        </div>
        
    </div>
    )
}
