import React, { useState, useEffect } from 'react';


import './MarketingPage.css'
import BenifitsHowItWorks from './Components/BenifitsHowItWorks/BenifitsHowItWorks';
import Accessible from './Components/Accessible/Accessible';
import Request from './Components/Request/Request';
//import FooterComponent from './Components/Footer/footer';
import HeadSection from './Components/HeadSections/HeadSection';
import InputSection from './Components/InputSection/InputSection';

import Header from '../../components/Home/HeaderSection/Header';
import FooterComponent from '../../components/Common/Footer/footer';
import '../About/AboutUsSkeleton.scss';
import { Clevertap, getQueryStringParams } from '../../utils/Utils';

export default function MarketingPage(props) {

    useEffect(() => {

        if (window.clevertap) {
            let obj=getQueryStringParams(props.location.search)
            if(obj && "utm_source" in obj){
                Clevertap("ACQEmail",{
                    ...obj,
                    "path":window.location.href
                   })
            }else{
              Clevertap("ACQEmail",{
                  "path":window.location.href,
                  "utm_source":'organic',
                  "utm_medium":'organic',
                  "utm_campaign":'ONS_Unknown'
              })
              
            }
            // window.clevertap.event.push("ACQEmail", {
            //   "viewed": window.location.search? window.location.search:true
            // }); 
          }
    }, [])

    const [benifits, setBenifits] = useState([
        {
            img:'https://res.cloudinary.com/onsurity/image/upload/v1591000465/OnSurity/Group_688_1_dhlgro.png',
            description:'HealthSure Membership for your team with Group Health Insurance and COVID-19 cover'
        },
        {
            img:'https://res.cloudinary.com/onsurity/image/upload/v1591000560/OnSurity/Group_692_dke44k.png',
            description:'Easy, Monthly, Usage- based payments that dont eat into your working capital'
        },
        {
            img:'https://res.cloudinary.com/onsurity/image/upload/v1591000564/OnSurity/Group_696_mna1ym.png',
            description:'Fully digitized subscription process with a dedicated dashboard for employers and HRs'
        },
        {
            img:'https://res.cloudinary.com/onsurity/image/upload/v1591000569/OnSurity/Group_699_byea50.png',
            description:'Employee health benefits like fitness rewards, discounted medicines and health check-ups'
        }
    ])

    const [howItWorks, setHowItWorks] = useState([
        {
            img:'https://res.cloudinary.com/onsurity/image/upload/v1591003947/OnSurity/image_71_dz8qes.png',
            description:'1. Enter your company GSTIN'
        },
        {
            img:'https://res.cloudinary.com/onsurity/image/upload/v1591003950/OnSurity/image_72_pfasvh.png',
            description:'2. Select a membership type for your team'
        },
        {
            img:'https://res.cloudinary.com/onsurity/image/upload/v1591073621/OnSurity/inr_symb_jeny4u.svg',
            description:'3. Make payment online'
        },
        {
            img:'https://res.cloudinary.com/onsurity/image/upload/v1591003956/OnSurity/image_74_d5zsqp.png',
            description:'4. Invite your team through our dashboard'
        },
    ])


    return (
        <div className="markrtPgCls">
            {/*<HeadSection/>*/}
            <Header/>
            <InputSection/>
            {/* <Request/> */}
            <Accessible/>
            <BenifitsHowItWorks data={benifits} pattern={1} background="#F5F9FF" Heading="TeamSure features and benefits"/>
            <BenifitsHowItWorks data={howItWorks} pattern={2} background="#fff" Heading="How It Works"/>

            {/*<FooterComponent/>*/}
             <div className="parentOut_AbtUs_Cls">
               <Request/>
               <FooterComponent/>
            </div>
        </div>
    )
}
