import React, {useState} from 'react'

export default function HeadSection(props) {

  return (
    <>
      <div className="headOut_CntUs_Cls">
        <h1 className="mainHeadClsCntUs">
          {props.data.mainHead}
        </h1>
        <h2 className="subHeadClsCntUs">
          {props.data.subHead}
        </h2>
      </div>
    </>
  )
}
