

//CALLBACK WHICH NEED TO RETURN ONCE PAYMENT IS SUCCESSFULL
export const CallBackUrl=()=>{
    var url = window.location.href;
    var arr = url.split("/");
    var result = arr[0] + "//" + arr[2]+"/teamsure/verify-payment";
    return(result)
}

