import React from 'react'
import Banner_Benefits from './Components/Banner_Benefits'
import { FitnessRewards_Data, howworks_Dta } from './Data/FitnessRewards_Data'
import Header from '../../components/Home/HeaderSection/Header'
import Features_Fitness from './Components/Features_Fitness'
import How_It_Works from './Components/How_It_Works'
import Redeem_Fitness from './Components/Redeem_Fitness'
import Org_Benefits from './Components/Org_Benefits'
import FooterComponent from '../../components/Common/Footer/footer';
import Card_Title from './Components/Card_Title'

export default function FitnessRewards_Details() {
   
    return (
        <div>
            <Header />
            <Card_Title data={FitnessRewards_Data} />
            <Banner_Benefits data={FitnessRewards_Data} showList={true}/>
            <Features_Fitness data={FitnessRewards_Data} />
            <How_It_Works data={FitnessRewards_Data} lists={true}/>
            <hr/>
            <How_It_Works data={howworks_Dta} lists={true}/>
            <Redeem_Fitness data={FitnessRewards_Data}  />
            <Org_Benefits />
            <div className="footer-comp">
            <FooterComponent />
            </div>
            

        </div>
    )
}
