import React from 'react'
import './TeamCount.scss';
import Input from '../../../../Input-controls/Input/Input';
import { connect } from 'react-redux';

function TeamCount(props) {


   const{companyDetails}=props.user;
   const saveBtnClikFn = () => {
     props.saveBtnClk();
   }
    return (
        //FOR WEB VIEW
        <>
        <div className="team-cls chkOutTmCnt">
            <div className="sec-wrp">
                {props.editable?
                <div className="save-btn" style={{cursor:'pointer'}} onClick={()=>saveBtnClikFn()}>SAVE</div>
                :
                <div className="edit-btn" style={{cursor:'pointer'}} onClick={()=>props.setEditable(true )}>EDIT</div>
                }
               <div className="team-header">{props.dta.length == 2 ? props.data.header_TeamFamily : props.data.header_TeamOnly}</div>
               <div className="emp-count-clss">{props.TotalEmployees} members</div>
            </div>
            <div className="hr-sec"><hr/></div>

            {!!props.dta && props.dta.length > 0 ?  <>
            <div className="age-amount-flxx">
                        <div className="head-clss">{props.head.headingg}</div>
                        <div className="head-clss">{props.head.secondheading}</div>
                        {props.dta.length == 2 ? <div className="head-clss">{props.head.thirdheading}</div> : null}
                </div>

                  <div className="">
                  {props.dta[0].age_range.map((lobj,index)=>(
                  <div className="d-flex">
                    <div className="age-amount-flx-edit head-clss">

                        <div className="my-2">{lobj.range.split('-').join(' to ')} years</div>

                    </div>
                    <div className="age-amount-flx-edit head-clss">
                          <Input align="center" className={props.editable ? "edit-btn-input" : "btnDisbLoksIpt edit-btn-input"}
                             value={lobj.total_employees} onChange={(e)=>{props.onChange(e,0,index)}}
                             disabled={props.editable ? "" : "disabled"}/>
                    </div>
                    {props.dta.length == 2 ?
                          <div className="head-clss">
                              <Input align="center" className={props.editable ? "edit-btn-input" : "btnDisbLoksIpt edit-btn-input"}
                               value={props.dta[1].age_range[index].total_employees} onChange={(e)=>{props.onChange(e,1,index)}}
                               disabled={props.editable ? "" : "disabled"}/>
                          </div>
                        : null}

                  </div>))}
                  </div>


                   </>
              : <>
                  <div className="age-team-flx">
                        <div className="age-header-div">Total Team Member Count </div>
                        <div className="">
                            <Input align="center" className={props.editable ? "edit-btn-input"
                              : "btnDisbLoksIpt edit-btn-input"}
                             value={props.TotalEmployees} onChange={(e)=>{props.onChange(e)}}
                             disabled={props.editable ? "" : "disabled"}/>
                        </div>
                  </div>
                </> }


           </div>


           {/* //FOR MOBILE VIEW */}
           {/* <div className="mobview-cls">
           {editable?
                <div className="save-btn" style={{cursor:'pointer'}} onClick={()=>setEditable(false)}>SAVE</div>
                :
                <div className="edit-btn" style={{cursor:'pointer'}} onClick={()=>setEditable(true )}>EDIT</div>
                }
               <div className="inner-team-clss">
               <div className="team-header">{props.data.header}</div>
               <div className="emp-count-clss">{companyDetails.TotalEmployees} members</div>
               </div>

           </div> */}
           </>
    )}

    const mapStateToProps = (state) => ({
        user:state.userstate,
      })



      export default connect(mapStateToProps, null)(TeamCount)
