import React, { useRef , useEffect, useState} from 'react'
import './OrgBenefits.scss'
import OrganizationBenefits from './pattern/OrganizationBenefits'
import CustomersSays from './pattern/CustomersSays'
import HowItWorks from './pattern/HowItWorks'
import { SideScroll } from '../../../layouts/PlanPurchase/util'
import { heightWidthCalc } from '../../../utils/Api'
import Trusts from './pattern/Trusts'
import { LazyImage } from '../../../utils/LazyImage'

export default function OrgBenefits(props) {

    // const[disableLeft, setDis(true);
    const ref = useRef(null);
    const [nextIcon,setNextIcon] = useState("https://res.cloudinary.com/onsurity/image/upload/v1596561524/New-home-screen/Group_522_faiuks.svg");

    const HandleBack=()=>{
        SideScroll(ref.current,'left', 1, props.data.Steps, 3)
    }

    const HandleNext=()=>{
      
        SideScroll(ref.current,'right', 1, props.data.Steps, 3)
    }

    useEffect(()=>{
      window.addEventListener('scroll', handleScrollFn, true);

      return () => {
        window.removeEventListener('scroll', handleScrollFn)
      }

    },[props.data.CardData])

   const[sliderIntervel, setSliderIntervel]=useState();
   const[startSlider, setStartSlider]=useState(true) 

    /*useEffect(() => {
      if(startSlider===true &&  heightWidthCalc().width>800){
          let intervel= setInterval(function() {
            SideScroll(ref.current,'right', 100, 0, 1)
          }, 100);
          setSliderIntervel(intervel)
      }
     return ()=>{
        clearInterval(sliderIntervel)
      }
    }, [startSlider])*/


    const handleScrollFn = () => {
      try{
        let divWidth = ref.current.querySelector('.lastItem').clientWidth;
        let inrWidth = window.innerWidth;
        let boundingObj = ref.current.querySelector('.lastItem').getBoundingClientRect();
        let totalWidthDiv = boundingObj.left + divWidth;
        let currentDev = heightWidthCalc();
        /*console.log("Div_Widht :: "+divWidth);
        console.log("Bnd_Widht :: "+boundingObj.left);*/
        // console.log("Inr_Widht :: "+inrWidth);
        // console.log("Ttl_Widht :: "+totalWidthDiv);
        // console.log("Output_dt :: "+(inrWidth - totalWidthDiv));
        if(inrWidth - totalWidthDiv >= 200 && currentDev.width >= 1600){
          setNextIcon("https://res.cloudinary.com/onsurity/image/upload/v1596561524/New-home-screen/Group_522_faiuks.svg");
          // setNextIcon("https://res.cloudinary.com/onsurity/image/upload/v1596553020/New-home-screen/Group_524_qqqlte.svg");
        }
        else if(inrWidth - totalWidthDiv >= 5 && currentDev.width < 1600){
          setNextIcon("https://res.cloudinary.com/onsurity/image/upload/v1596561524/New-home-screen/Group_522_faiuks.svg");
          // setNextIcon("https://res.cloudinary.com/onsurity/image/upload/v1596553020/New-home-screen/Group_524_qqqlte.svg");
        }
        else{

          // setNextIcon("https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594208549/New-home-screen/Org%20benefits/nextImg_owqexp.svg");
          setNextIcon("https://res.cloudinary.com/onsurity/image/upload/v1596891681/New-home-screen/Org%20benefits/rghtArrwEnb_wpwfzj.svg");
        }
      }
      catch(e){
        setNextIcon("https://res.cloudinary.com/onsurity/image/upload/v1596561524/New-home-screen/Group_522_faiuks.svg");
      }
    }

    const HandleHover=()=>{
      // debugger
      clearInterval(sliderIntervel)
      setStartSlider(false)
    }

    const HandleLeave=()=>{
      setStartSlider(true)
    }

        // let currentPos=0;
        // if(direction == 'left'){
        //     currentPos-=step;
        // } else {
        //     currentPos+=step;
        // }
        // console.log(element.scrollLeft)
        // if(element.scrollLeft==0){
        //     setDisableLeft(true)
        // }else{
        //     setDisableLeft(false)
        // }


    return (
        <div style={{backgroundColor:props.data.backColor}} className={`CustShadow ${props.data.Pattern==2 && 'py-4'}`}>
          <div className={props.data.Pattern==2 ? "homeScrollOrgComponents": props.data.Pattern==3 ? " hwItWrkPrntCls ": "patrn1Cls"}>
            <div className={`${props.data.Pattern==3 ?'HowWorksHeading ':
              props.data.Pattern==1 ?' mobOrhPtn1CssCls MobileOrgHeading org_ben_headCls' :
              'MobileOrgHeading org_ben_headCls'} mt-3 mainHeading_max`}>{props.data.Heading}</div>
            <div className="py-md-4 padTpOrgBenf">
              {props.data.Pattern != 3 ? 
                <div className="newDesngTitleHdr">
                  <div className="titleOrgBenfCls">
                    <div className={`mainHeading_max ${(props.data.Pattern==4 || props.data.Pattern==2) && "HeadingBllk"}`}>
                        {props.data.Heading}
                    </div>
                    <div className="OrgCardSubHeading contentTxt">
                        {props.data.SubHeading}
                    </div>
                  </div>
                  <div className="leftRightImgIcnCls">
                    <span className="pointerclass" onClick={()=>HandleBack()}>
                        {/* <LazyImage style={{height:'27px'}} src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594116011/New-home-screen/back_cs2sc0.svg" alt="backbtn" className="lazyload"/> */}
                        <LazyImage style={{height:'32px'}} src="https://res.cloudinary.com/onsurity/image/upload/v1596891681/New-home-screen/Org%20benefits/letfArrw_frkvkh.svg" alt="backbtn" />
                    </span>
                    <span className="pointerclass" onClick={()=>HandleNext()}>
                        <LazyImage className="pl-3" style={{height:'32px'}} src={nextIcon} alt="nextbtn" />
                    </span>
                  </div>
              </div>
              : null }
                <div className="d-flex scrlimg">
                    {/* STATIC CARD */}
                   {props.data.Pattern!==3 ?
                    <div className="FixedCardLayout my-auto mobViewOrgBenf">
                        <div className="FixedCard">
                            <h1 className="mainHeading_max">
                                {props.data.Heading}
                            </h1>
                            <h2 className="OrgCardSubHeading contentTxt">
                                {props.data.SubHeading}
                            </h2>
                            <div className="d-flex mt-5">
                                {/* className={disableLeft?"OrgBackArrowDisable":"OrgBackArrow"} */}
                                <div className="pointerclass" onClick={()=>HandleBack()}>
                                    {/* <LazyImage style={{height:'27px'}} src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594116011/New-home-screen/back_cs2sc0.svg" alt="backbtn" className="lazyload"/> */}
                                    <LazyImage style={{height:'27px'}} src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1596553012/New-home-screen/Group_525_afrhof.svg" alt="backbtn" />
                                </div>
                                <div className="pointerclass" onClick={()=>HandleNext()}>
                                    <LazyImage className="pl-3" style={{height:'27px'}} src={nextIcon} alt="nextbtn" />
                                </div>
                            </div>
                        </div>
                    </div>
                    :
                    <div className="HowitCss"></div>
                    }
                    {/* SCROLABLE CARDS */}
                        {/* <marquee behavior="scroll" direction="left" scrollamount="2"> */}
                        <div className="mainSliderOrg" id="ScrollOrg" onMouseEnter={()=>HandleHover()} onMouseLeave={()=>HandleLeave()} ref={ref}>
                        {props.data.CardData.map((e,ind)=>(
                          props.data.Pattern==1 ?
                                <OrganizationBenefits showCursor={props.showCursor} index={ind} mainData={props.data}  data={e} classNm={props.data.CardData.length-1 == ind ? "lastItem" : ""}/>
                          :props.data.Pattern==2?
                                <CustomersSays index={ind}  data={e} classNm={props.data.CardData.length-1 == ind ? "lastItem" : ""}/>
                          :props.data.Pattern==3?
                                <HowItWorks data={e} classNm={props.data.CardData.length-1 == ind ? "lastItem" : ""}/>
                          :props.data.Pattern==4?
                                <Trusts index={ind} data={e} classNm={props.data.CardData.length-1 == ind ? "lastItem" : ""}/>
                                :
                          <div>-----------------------</div>
                                
                            ))
                        }
                          <div className="lastDivOrgBenf"></div>
                        </div>
                        {/* </marquee> */}
                    </div>
            </div>
            </div>
        </div>
    )
}
