import React from 'react'
import './GSTIN.scss'
import { withRouter } from 'react-router-dom'
import { urls } from '../../../routes/routes'
import { connect } from 'react-redux'

function GSTIN(props) {
    return (
        <div>
            <div className="graydiv" style={{backgroundColor:props.data.color}}>

            <div className="flexdiv">
                    <div className="textdiv">
                        <div className="mainHeading_max">
                            {props.data.heading}
                        </div> 
                           
                       </div>
                    </div>
               

                <div align="center">
                    <div className="btndiv">
                        <div> <input type="text" placeholder="Enter Company Name" className="companyinput" /></div>
                        <div className="OR">or</div>
                        <div className="gst"><input type="text" placeholder="Enter your GSTIN" className="inputtypees" /></div> 
                         <div><button className="gstinbtn" onClick={()=>{
                             props.auth.authStatus===true?
                             props.history.push(urls.GSTIN_DETAILS)
                             :
                             props.history.push(urls.VERIFY_PHONE)}
                             }>Get Started</button></div>
                    </div>
                    
                </div>
                
            </div>
             
            </div>
    )
}

const mapStateToProps = (state) => ({
    auth:state.navigationStack.authObj
  })
  
export default withRouter(connect(mapStateToProps, null)(GSTIN))
