import React, {useEffect, useState} from 'react'
import Button from '../../../../../Input-controls/Button/Button'
import { connect } from 'react-redux';
import './CoverageProceed.scss';
import { urls } from '../../../../../routes/routes';
import { withRouter } from 'react-router-dom';
import { actions as actionsCoverge } from '../../../../../redux/ducks/state_change';
import { bindActionCreators } from 'redux';
import { actions as planAction } from '../../../../../redux/ducks/plan';
import InfoPopup from '../../../../../components/Common/InfoPopup/InfoPop'

function CoverageProceed(props) {

    useEffect(() => {
        console.log(props.plans, '11111111111112223322')
        if(props.plans && props.plans.data && props.plans.success==1 && proceedClicked){
          const {plans}=props.plans.data;
          if(plans.length>0){
            props.history.push(urls.SELECT_PLAN)
          }else{
            setShowPopUp(true)
          }


        }
      }, [props.plans])

      const[proceedClicked, setProceedClicked]=useState(false)

    const Proceed =(type)=>{
        console.log(props.coverages, '')
        setProceedClicked(true)
        if(type=='skip'){
            props.setCoverages([])
            props.fetchPlans(props.gstData.GstNumber, [...props.defaultCoverages])
        }else{
            props.fetchPlans(props.gstData.GstNumber, [...props.coverages, ...props.defaultCoverages])
        }
        // props.history.push(urls.SELECT_PLAN)
        
    }

    const[showPopUp, setShowPopUp]=useState(false)

    return (
        <>
        <InfoPopup show={showPopUp} Okay={()=>setShowPopUp(false)}/>
        <div style={{textAlign:"center",marginTop: '40px',marginBottom: '20px'}}>
            <Button onClick={()=>Proceed('proceed')} value="Proceed" className="coverage-prcdbtn" disabled={props.coverages.length===0}/>
            <div className="coverage-skip-btn" onClick={()=>Proceed('skip')}>Skip</div>
        </div>
        </>
    )
}

const mapStateToProps = (state) => ({
    plans:state.plans.data,
    coverages:state.userstate.coverages,
    gstData:state.userstate.gstDetails,

    defaultCoverages:state.userstate.defaultCoverages,
    coverages:state.userstate.coverages
})

const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...actionsCoverge,
        ...planAction
    }, dispatch)
})


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CoverageProceed))