import React, {useState, useEffect} from 'react'
import './SummarySkeleton.scss';
import Summary from '../CheckOut/Components/Summary/Summary';
// import Payment from '../Payment/Components/Payment/Payment';
import { urls } from '../../routes/routes';
import Navigation from '../../components/Common/Navigation/Navigation';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import {actions as cartActions} from './../../redux/ducks/cart'
import Loader from '../../utils/Loader/Loader';
import { CheckoutItems } from '../../utils/Env';


function SummarySkeleton(props) {

    useEffect(() => {
        props.setCart("","",'get')
        // eslint-disable-next-line
      }, [])
      
    const[cartDta, setCartDta]=useState('')

    const filterCart=(type, data)=>{
        return  data.find(e=>e.type==type) 
     }

    const createCheckout=(cartArr)=>{

        const keys=CheckoutItems;

        let objCart={ }
        
        for(let i=0; i<keys.length; i++){
            objCart[keys[i]]=filterCart(keys[i], cartArr.items)
        }

        objCart={...objCart, ...cartArr}
        delete objCart.items
       
        setCartDta({...objCart})
      }


    const handleClick=()=>{
        props.history.push(urls.PAYMENT)
    }

    useEffect(() => {
        if(props.cart.data){
           //  
           createCheckout(props.cart.data.cart)
        }
     }, [props.cart.data])

    return (
        <>
        {cartDta.ES ?
        <div style={{backgroundColor: '#F5F9FF',width: '100%'}}>
            <Navigation  backPath={urls.CHECKOUT}/>

             <div className="main-flx summaryMob">
                   <div className="summary-skele">
                      <Summary Pricing={cartDta} subscribeClick={handleClick}/>
                   </div>
            </div>
        </div>
           :
           <Loader/>}
     </>
    )
}

const mapStateToProps = (state) => ({
    cart:state.cart
  })
  
  const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...cartActions,
    }, dispatch)
  });
  
  
  export default connect(mapStateToProps, mapDispatchToProps)(SummarySkeleton)
