import React from 'react'
import './TeamMember.scss';
import Radio from '../../../../Input-controls/Radio/Radio';

export default function TeamMember(props) {

    return (
        <div onClick={()=>props.currentSelection(props.data)} className={`mem-only-cls ${props.data.policy_type===props.selected.policy_type && 'curntSelectionPlanRadio'}`}>
            <div className="radio-btnn">
              <Radio disabled={true} value={props.data.policy_type} selected={props.selected.policy_type}/>
            </div>
               <div className="wrp-contentdiv">
                   <div className="prps-dta-head">{props.data.title}</div>
                   <div className="prps-dta-sub">{props.data.description}</div>
            </div>
        </div>
    )
}
