import React from 'react'
import './SelectedPlan.scss';

export default function SelectedPlan(props) {
    let data=props.data.find(e=>e._id==props.selected)
    return (
        <div> 
              <div className="ItemSecCoverage">
                <div style={{width: '50%'}}> 
                <div className="healthcare-contnt">{data.name}</div>
                    <div className="owned-cntnt">Added Product</div>
                </div>
                <div className="cancelbtn">
                    <div><img src="https://res.cloudinary.com/onsurity/image/upload/v1598007751/New-home-screen/Group_3424_krdfrb.svg" width="22px" onClick={()=>props.HandleRemove(data._id)}/></div>
                </div>
            </div>
        </div>
    )
}
