import React from 'react'
import { LazyImage } from '../../../../utils/LazyImage'

export default function CustomersSays(props) {
    return (
        <div >
            <div className={`${props.index!=0 ? "mx-4":"mr-4"} ${props.classNm}`}>
                <div className='CustDynamicCards px-4 pb-4 py-4 custs_fnt_sty_Cls'>
                    <div className="small_scroll_cust_says my-1 pr-2">
                        <div className="mb-2">
                            <LazyImage height="10px" className="lazyload" src="https://res.cloudinary.com/onsurity/image/upload/v1597052596/New-home-screen/startQuote_ppsjje.svg" alt="ceclve" />
                        </div>
                        {props.data.Quote}
                        <div className="mb-2 txtAlgnCntCustSys">
                            <LazyImage height="10px" className="lazyload" src="https://res.cloudinary.com/onsurity/image/upload/v1597052596/New-home-screen/EndQuote_fxrwpj.svg" alt="ceclve" />
                        </div>
                    </div>
                    {/* <div className="mb-2">
                            <LazyImage height="10px" className="lazyload" src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594285981/New-home-screen/qute_r15mhp.svg" alt="ceclve" />
                        </div> */}
                </div>
                <div className="text-center mt-4">
                    <div className="d-flex" >
                        <div className="pr-3">
                        <LazyImage height="40px" className="lazyload" src={props.data.Image} alt={props.data.Alt} />

                        </div>
                        <div className="text-left">
                            <div className="CustHead cust_up_hd_cls">{props.data.Name}</div>
                            <div className="custsay-compname">{props.data.Comp}</div>
                            <div className="CustSubHead pt-1 cust_up_subhd_cls">{props.data.Company.toUpperCase()}</div>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    )
}
