import React from 'react'

export default function VisnMisnSection_Mob(props) {

  return (
    <>
      <div className="VisnMisnSectionOut_AbtUs_Cls">
        <div className="firstVisnMisnClsAbtUs cmmonPadinLftAbtUsParent">
          {props.imageSection.map((lobj,ind)=>(
            <div className="outerDivImgMobAbtUs">
              <img className="ImgMobAbtUsMisVis" src={lobj.image} alt="image" />
              <div className="imgSecMobTxtAbtUs">
                {lobj.label}
              </div>
            </div>
          ))}
        </div>
        <div className="sndVisnMisnClsAbtUs cmmonPadinRightAbtUsParent">
          <div className="visnPrntAbtUsCls">
            <div className="visnHeadAbtUs">
              {props.vission.mainHead}
            </div>
            <div className="visnSubHeadAbtUs">
              {props.vission.subHead}
            </div>
          </div>

          <div className="misnPrntAbtUsCls">
            <div className="misnHeadAbtUs">
              {props.mission.mainHead}
            </div>
            <div className="misnSubHeadAbtUs">
              {props.mission.subHead}
            </div>
          </div>
        </div>
      </div>
    </>
  )
}
